import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import html from './template/notice.html?raw';

class OONotice extends OOComponent{
    static prop = {
        title: '',
        text: '',
        icon: '',
        titleStyle: '',
        textStyle: '',
        showClose: true,
        type: 'info',   //error, warn, success
        skin: 'default', //banner
        // style: '',
        contentAlign: '',
        contentValign: '',
        width: '',
        height: ''
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _elements={
        container: null,
        content: null,
        icon: null,
        title: null,
        text : null,
        close: null
    };
    #skinStyle = null;
    _setPropMap={
        title: ()=>{
            this._elements.title.textContent = this._props.title;
        },
        text: ()=>{
            this._elements.text.textContent = this._props.text;
        },
        icon: ()=>{
            dom.toggleClass(this._elements.icon, `ooicon-${this._props.icon}`, this._props.icon);
        },
        titleStyle: ()=>{
            this._elements.title.setAttribute('style', this._props.titleStyle);
        },
        textStyle: ()=>{
            this._elements.text.setAttribute('style', this._props.textStyle);
        },
        type: () =>{
            this.#changeType();
        },
        skin: ()=>{
            this.#changeSkin().then();
        },
        showClose: ()=>{
            dom[(this._props.showClose) ? 'removeClass' : 'addClass'](this._elements.close, 'hide');
        },
        width: ()=>{
            if (this._props.width) dom.setStyle(this._elements.container, 'width', this._props.width)
        },
        height: ()=>{
            if (this._props.width) dom.setStyle(this._elements.container, 'height', this._props.height)
        },
        contentAlign: ()=>{
            if (this._props.contentAlign){
                dom.addClass(this._elements.content, 'align_'+this._props.contentAlign);
            }else{
                dom.removeClass(this._elements.content, 'align_left align_right align_center');
            }
        },
        contentValign: ()=>{
            if (this._props.contentValign){
                dom.addClass(this._elements.content, 'align_'+this._props.contentValign);
            }else{
                dom.removeClass(this._elements.content, 'valign_left valign_right valign_center');
            }
        },
        style: ()=>{
            dom.toggleAttr(this._elements.container, 'style', this._props.style);
        },
        $default: (key)=>{
            dom.toggleAttr(this._elements.container, key, this._props[key]);
        }
    };
    constructor() {
        super();
        this._initialize('oo-notice', OONotice, html);
        // this.props = cloneObject(OONotice.prop);
        // const template = use(html, null, 'oo-notice', true);
        // this.useTemplate(template);
    }
    _setEvent(){
        this._elements.close.addEventListener('click', ()=>{
            this.style.opacity = 0;
            window.setTimeout(()=>{
                this.remove();
            }, 200);
        });
    }
    async #changeSkin() {
        if (this.#skinStyle) {
            this.#skinStyle.remove();
            this.#skinStyle = null;
        }
        this.#skinStyle = document.createElement('style');
        this.#skinStyle.textContent = (!this._props.skin || this._props.skin==='default') ? (await import(/* @vite-ignore */ `./template/notice.default.scope.css?raw`)).default : (await import(`./template/notice.${this._props.skin}.scope.css?raw`)).default;

        this.shadowRoot.prepend(this.#skinStyle);

        this.dispatchEvent(new CustomEvent('skinLoaded'));
    }
    #changeType() {
        const className = 'container '+this._props.type || 'info';
        this._elements.container.setAttribute('class', className);
        if (!this._props.icon){
            const iconMap = {
                'info': 'ooicon-info',
                'error': 'ooicon-error',
                'warn': 'ooicon-help',
                'success': 'ooicon-check'
            }
            const className = 'icon '+ iconMap[this._props.type] || 'ooicon-info';
            this._elements.icon.setAttribute('class', className);
        }
    }
}

if (!customElements.get('oo-notice')) customElements.define('oo-notice', OONotice);
