export default "* {\r\n    box-sizing: border-box;\r\n}\r\n.content{\r\n    --shadow: var(--oo-shadow-border);\r\n}\r\n.group {\r\n    display: table;\r\n    align-items: center;\r\n    min-height: 1.5em;\r\n    width: 100%;\r\n}\r\n\r\n.label {\r\n    white-space: nowrap;\r\n    transition: color 0.5s;\r\n    display: flex;\r\n    align-items: center;\r\n    color: var(--oo-color-text2);\r\n    height: 100%;\r\n    min-height: 2.25em;\r\n}\r\n\r\n.labelText {\r\n    margin-right: 0.5em;\r\n    padding: 0.5em 0.35em;\r\n    display: flex;\r\n    align-items: center;\r\n    height: 100%;\r\n    min-height: 2.25em;\r\n}\r\n.flex-end {\r\n    justify-content: flex-end;\r\n}\r\n\r\n.label-row {\r\n    display: table-cell;\r\n    vertical-align: middle;\r\n    width: 1px;\r\n}\r\n\r\n.group.readmode .label-row {\r\n    display: table-cell;\r\n    vertical-align: middle;\r\n}\r\n\r\n.items {\r\n    display: block;\r\n}\r\n.group.readmode .items {\r\n    display: flex;\r\n}\r\n\r\n.hide-items {\r\n    position: absolute;\r\n    opacity: 0;\r\n    width: 1px;\r\n    height: 1px;\r\n    overflow: hidden;\r\n}\r\n.hide {\r\n    display: none !important;\r\n}\r\ndiv.invalidHint {\r\n    /*position: absolute;*/\r\n    color: var(--oo-color-error);\r\n    height: 0;\r\n    width: 0;\r\n    overflow: hidden;\r\n    word-break: break-all;\r\n    transition: margin-top 0.2s, height 0.2s;\r\n}\r\ndiv.invalidHint.show {\r\n    margin: 0.3em 0 0 0;\r\n    height: unset;\r\n    width: unset;\r\n    line-height: 1.2em;\r\n}\r\ndiv.invalidHint.show::before {\r\n    line-height: 1.4em;\r\n    margin-right: 0.2em;\r\n}\r\n.itemsContent {\r\n    display: table-cell;\r\n    border-radius: var(--oo-default-radius);\r\n    position: relative;\r\n    vertical-align: middle;\r\n    transition: padding 0.2s;\r\n    width: 100%;\r\n}\r\n.group:has(div.invalidHint.show) .itemsContent {\r\n    padding: 0 0.5em;\r\n    background: var(--oo-color-error-bg);\r\n    box-shadow: var(--oo-shadow-border-error);\r\n}\r\n\r\ninput {\r\n    position: absolute;\r\n    width: 1px;\r\n    height: 1px;\r\n    overflow: hidden;\r\n    left: 50%;\r\n    bottom: 1px;\r\n    appearance: none;\r\n    outline: none;\r\n    border: 0;\r\n    opacity: 0;\r\n    font-size: 1px;\r\n}\r\n\r\n.requiredFlag {\r\n    color: var(--oo-color-highlight);\r\n    padding-left: 0.3em;\r\n    font-size: 0.75em;\r\n    display: table-cell;\r\n    vertical-align: middle;\r\n}\r\n.emptyPlaceholder{\r\n    padding-left: 0.3em;\r\n}\r\n\r\n/* 移动端，标签在上，输入框下划线 skin-mode='mobile' */\r\n.content.mobile{\r\n    --oo-shadow-border: inset 0 -1px 0 0px var(--oo-color-gray-d);\r\n    --oo-shadow-border-focus: inset 0 -1px 0 0px var(--oo-color-main);\r\n    --oo-shadow-border-error: inset 0 -1px 0 0px var(--oo-color-error);\r\n    --oo-default-radius: 0;\r\n    --oo-area-radius: 0;\r\n}\r\n\r\n.content.mobile.group {\r\n    display: block !important;\r\n}\r\n.content.mobile.group > div {\r\n    display: block !important;\r\n}\r\n\r\n.content.mobile .label-row {\r\n    display: block !important;\r\n    width: unset !important;\r\n}\r\n\r\n.content.mobile .label-row > .label {\r\n    display: block !important;\r\n}\r\n\r\n.content.mobile .label-row > .label > .labelText {\r\n    width: unset !important;\r\n    min-width: unset !important;\r\n    max-width: unset !important;\r\n    margin-right: 0 !important;\r\n    padding: 0.5em 0.35em !important;\r\n    display: block !important;\r\n    height: unset !important;\r\n    min-height: unset !important;\r\n}\r\n\r\n.content.mobile .itemsContent{\r\n    width: 100% !important;\r\n    padding: 0.5em 0.35em !important;\r\n    box-shadow: var(--shadow);\r\n    display: block !important;\r\n}\r\n.content.mobile .items{\r\n    font-size: 1.125em;\r\n    color: var(--oo-color-text);\r\n}\r\n.content.mobile.readmode .itemsContent {\r\n    padding: 0.5em 0.05em !important;\r\n    /* border-bottom: 1px solid #dddddd !important; */\r\n}\r\n\r\n.content.mobile .requiredFlag {\r\n    box-shadow: var(--shadow);\r\n}\r\n.content.mobile.required .itemsContent{\r\n    display: table-cell !important;\r\n}\r\n\r\n/* ---------------------------------------------- */"