import {dom, hyphenate, erase} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from './template/nav.scope.css?raw';

const html = `
<nav class="content">
    <slot name="before-outer"></slot>
    <div class="box">
        <slot name="search"></slot>
        <div class="search">
            <oo-input class="searchInput" right-icon="search" placeholder="输入关键字搜索">
                    <div slot="after-inner-before" class="ooicon-close clearSearch hide"></div>
            </oo-input>
        </div>
        <slot name="before-inner-before"></slot>
        <div class="navContent">
            <slot name="before-inner-after"></slot>
            <div class="searchItems hide"></div>
            <div class="items"></div>
            <slot name="after-inner-before"></slot>
        </div>
        <slot name="after-inner-after"></slot>
    </div>
    <slot name="after-outer"></slot>
</nav>`;
export default class OONav extends OOComponent{
    static prop = {
        skin: '',
        cssLink: '',
        key: 'name',
        searchable: true,
        allExpanded: false,
        dragEnable: false,
        indent: 1.3, //每级item缩进单位em
        iconPrefix: 'ooicon-', //icon前缀
        menu: []
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    #items = [];
    #searchItems = [];
    #itemKeyMap = new Map();
    #itemDataMap = new WeakMap();
    #childrenDataMap = new WeakMap();
    #selectedItem = null;
    #selectedSearchItem = null;
    _setPropMap={
        $default: ()=>{},
        skin: ()=>{
            this._useSkin(this._props.skin);
        },
        cssLink: ()=>{
            this.useCssLink(this._props.cssLink);
        },
        searchable:()=>{
            dom.checkClass( this._elements.search, 'hide', !this._props.searchable );
        },
        dragEnable:()=>{
            dom.checkClass( this._elements.items, 'items-draggable', !!this._props.dragEnable );
        },
        menu:()=>{
            !!this.loaded && this.#init();
            const data = {
                children: this._props.menu
            };
            if( this._props.key ){
                data[this._props.key] = 'oo-nav-root';
            }
            this.data = this.#createProxy(data);
            const item = new Item(this, null, this._elements.items, this.data, 0).append();
            this.#items.push(item);
            this.loaded = true;
        }
    };
    _elements={
        box: null,
        navContent: null,
        items: null,
        search: null,
        searchInput: null,
        clearSearch: null,
        searchItems: null,
    }
    _render() {
        // this._setPropMap.searchable();
        this._setPropMap.dragEnable();
        this._setEvent();
    }
    constructor() {
        super();
        this._initialize('oo-nav', OONav, html, css);
    }
    _connected() {}
    _disconnected(){
    }
    #init(){
        this.#items = [];
        this.#searchItems = [];
        this.#itemKeyMap = new Map();
        this.#itemDataMap = new WeakMap();
        this.#childrenDataMap = new WeakMap();
        this.#selectedItem = null;
        this.#selectedSearchItem = null;
        dom.empty(this._elements.items);
    }
    _setEvent() {
        super._setEvent();

        const {searchInput, clearSearch} = this._elements;

        let isComposing = false; // 标记是否处于输入法输入中

        //输入法开始输入事件
        searchInput.addEventListener('compositionstart', () => { isComposing = true; });

        //输入法结束输入事件
        searchInput.addEventListener('compositionend', (e) => {
            isComposing = false;
            this.search( e.currentTarget.value );
        });

        //常规的input事件
        searchInput.addEventListener('input', (e) => {
            !isComposing && this.search( e.currentTarget.value );
        });

        clearSearch.addEventListener('click', (e)=>{
            searchInput.setAttribute('value', '');
            this.search('');
        })
    }
    setMenu(menu){
        this._props.menu = menu;
        this._setPropMap.menu();
    }
    active(key){
        this.getItem(key)?.active();
    }
    select(key){
        this.getItem(key)?.select();
    }
    collapse(key){
        this.getItem(key)?.collapse();
    }
    expand(key){
        this.getItem(key)?.expand();
    }
    getItem(key){
        return this.#itemKeyMap.get(key);
    }
    getSelectedItem(){
        return this.#selectedItem;
    }
    _checkItemUnique(data){
        const key = data[this._props.key];
        return !(key && this.#itemKeyMap.get(key));
    }
    _checkChildrenMap(item){
        const data = item.data.___
        if( data.children && !this.#childrenDataMap.has(data.children) ){
            this.#childrenDataMap.set(data.children, item);
        }
    }
    _addItem(item){
        const data = item.data.___;

        this.#itemDataMap.set(data, item);
        data.children && this.#childrenDataMap.set(data.children, item);

        const key = data[this._props.key];
        key && this.#itemKeyMap.set(key, item);
    }
    _removeItem(item){
        const data = item.data.___;

        (this.#selectedItem === item) && (this.#selectedItem = null);

        this.#itemDataMap.delete(data);
        data.children && this.#childrenDataMap.delete(data.children, item);

        const key = data[this._props.key];
        key && this.#itemKeyMap.delete(key);
    }
    _selectItem(item){
        if (this.#selectedItem !== item ){
            //this.#selectedItem?.unselect();
            !!this.#selectedItem && (this.#selectedItem.data.selected = false);
            this.#selectedItem = item;
        }
    }
    _selectSearchItem(item){
        if (this.#selectedSearchItem !== item ){
            this.#selectedSearchItem?.unselect();
            this.#selectedSearchItem = item;
        }
    }
    _fireEvent( eventType, data , manual ){
        this.dispatchEvent(new CustomEvent(eventType, {
            detail: { data, manual: !!manual }
        }));
    }
    search(value){
        dom.checkClass(this._elements.clearSearch, 'hide', !value);
        dom.checkClass(this._elements.items, 'hide', !!value);
        dom.checkClass(this._elements.searchItems, 'hide', !value);
        this.#emptySearchItems();
        if( !!value ){
            this.#findSearchitem( this.#items, value );
        }else{
            this.#selectedItem?.checkExpand();
            this.#selectedSearchItem = null;
        }
        this.dispatchEvent(new CustomEvent('search', {
            detail: { value }
        }))
    }
    #findSearchitem(items, value){
        items.forEach(item=>{
            if( item._searchable() && item.data.text.includes(value) ){
                const searchItem = new SearchItem(this, item, item.data.___)
                this.#searchItems.push( searchItem );
            }
            this.#findSearchitem( item.items, value );
        })
    }
    #emptySearchItems(){
        while(this.#searchItems.length){
            const searchItem = this.#searchItems.pop();
            searchItem.remove();
        }
        this.#selectedSearchItem = null;
    }
    #createProxy(value) {
        return new Proxy(value, {
            get: (target, property) => {
                if (property==='___'){
                    return target;
                }else{
                    const value = target[property];
                    return (typeof value === 'object' || Array.isArray(value)) && value !== null ?
                        this.#createProxy(value) :
                        value;
                }
            },
            set: (target, property, value) => {
                let res, item;
                if( Array.isArray(target)){
                    if( value.___ )value = value.___;
                    res = Reflect.set(target, property, value);
                    item = this.#childrenDataMap.get(target)
                    item?._updateChildren(property, value);
                }else{
                    const oldValue = target[property];
                    res = Reflect.set(target, property, value);
                    item = this.#itemDataMap.get(target);
                    item?._updateProperty(property, value, oldValue);
                }
                return res;
            }
        });
    }
}


// data {
//     icon, text, key, count, expanded, selected, selectable(yes, no, auto), disabled, title, group, expandable, draggable, children
// }
class Item {
    constructor(nav, parentItem, parentEl, data, level) {
        this.nav = nav;
        this.data = data;
        this.parentItem = parentItem;
        this.parentEl = parentEl;
        this.level = level;
        this._render();
    }
    items = [];
    childrenMap = new WeakMap();
    _render(){
        this.nodeEl = dom('div.content');
        if( this.level > 0 ){
            this.#createElements();
            this.#setEvents();
            this.#checkStatus();
        }else{
            this.nav._addItem(this);
            this.childrenEl = dom('div.children.show');
            this.nodeEl.append(this.childrenEl);
        }
        this._createItems();
    }
    append(where = 'beforeend', element = this.parentEl){
        element.insertAdjacentElement(where, this.nodeEl);
        return this;
    }
    #createElements(){
        const data = this.data.___;

        const name = data[this.nav._props.key];

        this.itemEl = dom('div.item');

        !!name && this.itemEl.append( dom('slot', { name: `${name}-inner-before` }) );

        this.iconEl = dom(`div.icon ${this.nav._props.iconPrefix}${data.icon||''}`);
        this.itemEl.append( this.iconEl );
        if (data.img){
            this.iconEl.addClass('img');
            this.iconEl.style.backgroundImage = `url(${data.img})`;
        }

        this.textEl = dom('div.text', { text: data.text || '' });
        this.itemEl.append(this.textEl);

        !!data.title && this.itemEl.setAttribute('title', data.title);

        !!name && this.itemEl.append( dom('slot', { name: `${name}-inner` }) );

        this.countEl = dom('div.count');
        (typeof data.count !== 'undefined') && (this.countEl.textContent = data.count);
        this.itemEl.append(this.countEl);

        this.expandEl = dom('div.arrowArea');
        this.arrowEl = dom('div.arrow ooicon-drop_down');
        this.expandEl.append(this.arrowEl);
        this.itemEl.append(this.expandEl);

        this.dragEl = dom('div.dragArea');
        this.dragIconEl = dom('div.draggable ooicon-sorting');
        this.dragEl.append(this.dragIconEl);
        this.itemEl.append(this.dragEl);

        !!name && this.itemEl.append( dom('slot', { name: `${name}-inner-after` }) );

        this.nodeEl.append(this.itemEl);

        this.childrenEl = dom('div.children');
        this.nodeEl.append(this.childrenEl);
    }
    #setEvents(){
        const { itemEl, expandEl, dragEl, data } = this;

        itemEl.addEventListener('click', (e)=>{
            if(this._selectable()){
                this.select();
                data.___.selected = true;
                this.active();
            }else if(this._expandable()){
                //!data.expanded ? this.expand() : this.collapse();
                data.expanded = !data.___.expanded;
                this.active();
            }else{
                this.active();
            }
        });

        //可选择并且可展开
        expandEl.addEventListener('click', (e)=>{
            if( this._expandable() && this._selectable() ){
                //!data.expanded ? this.expand() : this.collapse();
                data.expanded = !data.___.expanded;
                this.active();
                e.stopPropagation();
            }
        })

        //可拖拽
        dragEl.addEventListener('mousedown', (e)=>{
            this._draggable() && itemEl.setAttribute('draggable', 'true');
            e.stopPropagation();
        });
        dragEl.addEventListener('click', (e)=>{
            e.stopPropagation();
        })
        itemEl.addEventListener('dragstart', (e)=>{
            if( this._draggable() ){
                this.nav.draggingItem = this;
                itemEl.addClass('dragging');
                this.nav._fireEvent('dragstart', this.data, true);
            }
            e.stopPropagation();
        })
        itemEl.addEventListener('dragend', (e)=>{
            if( this._draggable() ){
                itemEl.setAttribute('draggable', 'false');
                itemEl.removeClass('dragging');
                this.nav.draggingItem = null;
                this.nav._fireEvent('dragend', this.data, true);
            }
            e.stopPropagation();
        })
        //作为拖拽目标的事件
        itemEl.addEventListener('dragover', (e) => {
            if( this._droppable() ){
                //阻止默认行为以允许放置
                e.preventDefault();
                this._handleDragover(e);
            }
            e.stopPropagation();
        });
        itemEl.addEventListener('dragleave', (e)=>{
            if( this._droppable() ){
                this._handleDragleave(e);
            }
        });
        itemEl.addEventListener('drop', (e)=>{
            if( this._droppable() ){
                //阻止默认行为以允许放置
                e.preventDefault();
                this._handleDrop(e);
            }
            e.stopPropagation();
        })
    }
    #checkStatus(){
        const { itemEl, iconEl, childrenEl, arrowEl, expandEl, countEl, dragEl, nav, level } = this;
        const data = this.data.___;

        dom.checkClass(iconEl, 'hide', (!data.icon && !data.img));
        dom.checkClass(countEl, 'hide', typeof data.count === 'undefined');

        dom.checkClass(itemEl, 'disabled', !!data.disabled);

        level===1 && dom.addClass(itemEl, 'level1');
        dom.setStyle(itemEl, 'padding-left', (nav._props.indent * level) +'em');

        dom.checkClass(arrowEl, 'hide', !this._expandable());
        dom.checkClass(expandEl, 'hide', !this._expandable());

        dom.checkClass(dragEl, 'hide', !this._draggable());

        dom.checkClass(childrenEl, 'show', !!data.group);
        !!arrowEl && dom.checkClass(arrowEl, 'down', !!data.expanded);
        nav._props.allExpanded && !data.expanded && !data.group && this.expand(false);

        !!data.group && dom.addClass(itemEl, 'group');
        !!data.group && dom.addClass(childrenEl, 'group');
        !!data.selected && this.select(false);

        !!data.expanded && this.checkExpand(false, true);
    }
    _updateChildren(){
        for (let i = 0; i < this.items.length; i++) {
            const item = this.items[i];
            if (!this.data.___.children.includes(item.data.___)) {
                item.remove();
                i--; // 调整索引以补偿已删除的元素
            }
        }

        const invalidChileren = [];
        let lastItem;
        this.data.children.forEach((child, index) => {
            let item = this.childrenMap.get(child.___);
            if( item ){
                //移动位置
                if( this.items.indexOf(item) !== index ){
                    this.items.splice(this.items.indexOf(item), 1);
                    this.items.splice(index, 0, item);

                    !!lastItem ?
                        item.append('afterend', lastItem.nodeEl) :
                        item.append('afterbegin', this.childrenEl);
                }
            }else{
                !!lastItem ?
                    (item = this._createItem(child, 'afterend', lastItem.nodeEl)) :
                    (item = this._createItem(child, 'afterbegin', this.childrenEl))
            }
            item ? (lastItem = item) : invalidChileren.push(child);
        })
        invalidChileren.forEach((child)=>{
            erase(this.data.___.children, child.___);
        })
        if( this.level > 0 ){
            dom.checkClass(this.arrowEl, 'hide', !this._expandable());
            dom.checkClass(this.expandEl, 'hide', !this._expandable());
        }

        invalidChileren.length > 0 && this.throwNotUniqueError(invalidChileren);
    }
    _createItems(){
        const invalidChileren = [];
        this.data.children?.forEach(child=>{
            !this._createItem(child) && invalidChileren.push(child);
        });
        invalidChileren.forEach((child)=>{
            erase(this.data.___.children, child.___);
        })
        invalidChileren.length > 0 && this.throwNotUniqueError(invalidChileren);
    }
    throwNotUniqueError(invalidChileren){
        const keys = invalidChileren.map((child)=>{
            return child.___[this.nav._props.key]
        })
        throw new Error(`Item key ${this.nav._props.key}:${keys.join(", ")} is not unique!`);
    }
    _createItem(child, position, el){
        if( this.nav._checkItemUnique(child.___) ){
            const item = new Item(this.nav, this, this.childrenEl, child, this.level+1).append(position, el);
            this.items.push(item);
            this.childrenMap.set(child.___, item);
            this.nav._addItem(item);
            return item;
        }else{
            return null;
        }
    }
    _removeItem(item){
        this.childrenEl.removeChild(item.nodeEl);
        erase(this.items, item);
        this.childrenMap.delete(item.data.___);
        this.nav._removeItem(item);
    }
    remove(){
        this.emptyItems();
        this.parentItem?._removeItem(this);
    }
    emptyItems(){
        while(this.items.length>0){
            this.items.pop().remove();
        }
    }
    _updateProperty( property, value, oldValue){
        //icon, text, key, count, expanded, expandable, selected, selectable, disabled, title, group
        if( value === oldValue )return;
        const data = this.data.___;
        switch (property) {
            case 'icon':
                !!oldValue && dom.removeClass(this.iconEl, `${this.nav._props.iconPrefix}${oldValue}`);
                !!value && dom.addClass(this.iconEl, `${this.nav._props.iconPrefix}${value}`);
                dom.checkClass(this.iconEl, 'hide', !value);
                break;
            case 'text':
                this.textEl.textContent = value || '';
                break;
            case 'title':
                this.itemEl.setAttribute('title', value || '')
                break;
            case 'count':
                this.countEl.textContent = data.count || '';
                dom.checkClass(this.countEl, 'hide', typeof data.count === 'undefined');
                break;
            case 'expanded':
                !!value ? this.expand() : this.collapse();
                break;
            case 'selected':
                !!value ? this.select() : this.unselect();
                break;
            case 'expandable':
                // data.expandable = !!value;
                dom.checkClass(this.arrowEl, 'hide', !this._expandable());
                dom.checkClass(this.expandEl, 'hide', !this._expandable());
                break;
            case 'disabled':
                dom.checkClass(this.itemEl, 'disabled', !!value);
                break;
            case 'draggable':
                dom.checkClass(this.dragEl, 'hide', !this._draggable());
                break;
            case 'group':
                dom.checkClass(this.arrowEl, 'hide', !this._expandable());
                dom.checkClass(this.expandEl, 'hide', !this._expandable());
                dom.checkClass(this.itemEl, 'group', !!value);
                dom.checkClass(this.childrenEl, 'group', !!value);
                if( !!value && !data.expanded )this.expand(false);
                break;
            case this.nav._props.key:
                this.nav._removeItem(this);
                this.nav._addItem(this);
                break;
            case 'children':
                this.nav._checkChildrenMap(this);
                this._updateChildren(property, value);
                break;

        }
    }
    _selectable( manual = true ){
        const data = this.data.___;
        switch (data.selectable){
            case 'no': return false;
            case 'yes':
                return manual ? ( !data.disabled && !data.group ) : !data.group;
            default:
                const flag = !data.group && !data.children?.length;
                return manual ? ( !data.disabled && flag ) : flag;
        }
    }
    _searchable(){
        const data = this.data.___;
        return !data.disabled && !data.group && ( !data.children?.length || data.selectable==='yes' );
    }
    _expandable( manual = false ) {
        const data = this.data.___;
        const flag = (!data.group && !!data.children?.length) || !!data.expandable;
        return manual ? ( !data.disabled && flag ) : flag;
    }
    _draggable(){
        const data = this.data.___;
        return !!data.draggable && !data.disabled && !!this.nav._props.dragEnable;
    }
    _droppable(){
        const data = this.data.___;
        const dragItem = this.nav.draggingItem;
        //如果拖拽到自己的后代，不允许拖动
        if( dragItem?._isDescendants(this) )return false;
        return !!data.droppable && !data.disabled && !!this.nav._props.dragEnable;
    }
    _isDescendants (item){
        while ( item ) {
            if (item === this) {
                return true;
            }
            item = item.parentItem;
        }
        return false;
    }
    #dropPosition = 'top';
    _handleDragover(e){
        const size = dom.getSize(this.itemEl);
        switch (this.data.droppable){
            case 'in':
                this.#dropPosition = 'middle';
                break;
            case true:
                this.#dropPosition = e.offsetY < size.y/3 ? 'top' : ( e.offsetY <= size.y/3*2 ? 'middle' : 'bottom' );
                break;
            default:
                this.#dropPosition = e.offsetY < size.y/2 ? 'top' : 'bottom';
                break;
        }
        dom.checkClass(this.itemEl, 'dragover-top', this.#dropPosition === 'top');
        dom.checkClass(this.itemEl, 'dragover-middle', this.#dropPosition === 'middle');
        dom.checkClass(this.itemEl, 'dragover-bottom', this.#dropPosition === 'bottom');
    }
    _handleDragleave(e){
        dom.removeClass(this.itemEl, 'dragover-top');
        dom.removeClass(this.itemEl, 'dragover-middle');
        dom.removeClass(this.itemEl, 'dragover-bottom');
    }
    _handleDrop(e){
        this._handleDragleave(e);
        const dragItem = this.nav.draggingItem;
        const data = dragItem.data.___;
        if( dragItem ){
            if( dragItem !== this ){
                //移除拖拽元素
                let fromIndex;
                const fromData = dragItem.parentItem.data;
                if( fromData ){
                    fromIndex = fromData.children.___.indexOf(data);
                    fromData.children.splice(fromIndex, 1);
                }

                //添加拖拽元素
                let toIndex, toData;
                if( this.#dropPosition === 'middle' ){
                    toData = this.data;
                    if( !toData.children ){
                        toData.children = [data];
                        toIndex = 0;
                    }else{
                        toData.children.push(data);
                        toIndex = toData.children.___.length - 1;
                    }
                    !this.data.expanded && (this.data.expanded = true);
                }else{
                    toData = this.parentItem.data;
                    if( toData ){
                        if( !toData.children ){
                            toData.children = [data];
                            toIndex = 0;
                        }else{
                            toIndex = toData.children.___.indexOf(this.data.___);
                            this.#dropPosition === 'bottom' && toIndex++;
                            toData.children.splice(toIndex, 0, data);
                        }
                    }
                }


                this.nav.draggingItem = null;

                this.nav._fireEvent('drop', {
                    from: fromData,
                    to: toData,
                    data,
                    fromIndex,
                    toIndex
                }, true);
            }
        }
    }
    select( manual = true, isCheckExpand = true ) {
        if( this._selectable(manual) ){
            dom.addClass(this.itemEl, 'selected');
            this.nav._selectItem(this);
            isCheckExpand && this.parentItem?.checkExpand( manual );
            this.nav._fireEvent('select', this.data, manual);
        }
    }
    active( manual = true ) {
        !this.data.___.disabled && this.nav._fireEvent('active', this.data, manual);
    }
    unselect(){
        dom.removeClass(this.itemEl, 'selected');
    }
    checkExpand( manual = true, force = false ){
        (!this.data.___.expanded || force) && this._expandable( manual ) && this.expand( manual );
        this.parentItem?.checkExpand( manual, force );
    }
    expand( manual = true ){
        if( this._expandable( manual ) ){
            this.arrowEl && dom.addClass( this.arrowEl, 'down');
            this.data.___.expanded = true;
            if( manual ){
                this.#showChildren();
            }else{
                dom.addClass( this.childrenEl, 'show' );
                this.nav._fireEvent('expand', this.data, false);
            }
        }
    }
    collapse( manual = true ){
        if( this._expandable( manual ) ){
            this.arrowEl && dom.removeClass( this.arrowEl, 'down');
            this.data.___.expanded = false;
            if( manual ){
                this.#hideChildren();
            }else{
                dom.removeClass( this.childrenEl, 'show' );
                this.nav._fireEvent('collapse', this.data, false);
            }
        }
    }
    #showChildren(){
        const el = this.childrenEl;

        dom.addClass(el, 'show');
        const y = dom.getSize(el).y;
        dom.setStyle(el, 'height', '0');

        setTimeout(()=>{
            dom.setStyle(el, 'height', y+'px');
            setTimeout(()=>{
                dom.setStyle(el, 'height', 'unset');
                this.nav._fireEvent('expand', this.data, true);
            }, 200);
        }, 1)
    }
    #hideChildren(){
        const el = this.childrenEl;
        const y = dom.getSize(el).y;
        dom.setStyle(el, 'height', y+'px');
        setTimeout(()=>{
            dom.setStyle(el, 'height', '0');
            setTimeout(()=>{
                dom.setStyle(el, 'height', 'unset');
                dom.removeClass(el, 'show');
                this.nav._fireEvent('collapse', this.data, true);
            }, 200);
        }, 1);
    }
}

class SearchItem {
    constructor(nav, item, data) {
        this.nav = nav;
        this.item = item;
        this.data = data;
        this.parentEl = nav._elements.searchItems;
        this._render()
    }
    _render(){
        this.#createElements();
        this.#addEvents();
        this.#checkStatus();
        this.parentEl.append(this.nodeEl);
    }
    #createElements(){
        const data = this.data;

        this.nodeEl = dom('div.content');

        this.itemEl = dom('div.item level1');

        !!data.icon && this.itemEl.append(dom(`div.icon ${this.nav._props.iconPrefix}${data.icon}`));

        this.itemEl.append(dom('div.text', { text: data.text || '' }));

        !!data.title && this.itemEl.setAttribute('title', data.title);

        this.nodeEl.append(this.itemEl);
    }
    #addEvents(){
        this.itemEl.addEventListener('click', (e)=>{
            this.select();
        });
    }
    #checkStatus(){
        const {itemEl, data} = this;
        dom.checkClass(itemEl, 'disabled', !!data.disabled);
        dom.setStyle(itemEl, 'padding-left', this.nav._props.indent +'em');
        !!data.selected && this.select(false, false);
    }
    select( manual = true, isCheckItem = true ) {
        if (this.item._selectable()) {
            dom.addClass(this.itemEl, 'selected');
            this.nav._selectSearchItem(this);
            if( isCheckItem ){
                this.item.data.___.selected = true;
                this.item.select(true, false);
                this.item.active(manual);
            }
        }else{
            this.item.active(manual);
        }
        // this.nav._fireEvent('select', this.data, manual);
    }
    unselect(){
        dom.removeClass(this.itemEl, 'selected');
        // this.#selectedSearchItem && dom.removeClass(this.#selectedSearchItem, 'selected');
    }
    remove(){
        this.parentEl.removeChild(this.nodeEl);
    }
}
