import {typeOf, hyphenate, cleanText, camelCase} from "./common.js";

const setAttribute = function(e, k, v){
    const _k = k.toLowerCase();
    if (_k === 'text'){
        e.textContent = v;
    }else if (_k === 'html'){
        e.innerHTML = v;
    }else if (_k === 'styles'){
        dom.setStyles(e, v);
    }else{
        e.setAttribute(k, v);
    }
}
const getAttribute = function(e, k){
    const _k = k.toLowerCase();
    if (_k === 'text'){
        return e.innerText || e.textContent || '';
    }else if (_k === 'html'){
        return e.innerHTML || '';
    }else if (_k === 'value'){
        switch (e.tagName.toString().toLowerCase()){
            case 'select':
                return e.options[e.selectedIndex].value;
            default:
                return e.value;
        }
    }
    return e.getAttribute(k);
}

const _classes = function(className){
    const classNames = cleanText(className || '').split(' '), uniques = {};
    return classNames.filter(function(className){
        if (className !== '' && !uniques[className]) return uniques[className] = className;
    });
};
const _isBody = function(element){
    return !element.tagName || (/^(?:body|html)$/i).test(element.tagName);
};
const _isHtml = function(element){
    return (/html/i).test(element.tagName);
};
const _styleNumber = function(element, style){
    return parseInt(dom.getStyle(element, style)) || 0;
}
const _topBorder = function(element){
    return _styleNumber(element, 'border-top-width');
}
const _leftBorder = function(element){
    return _styleNumber(element, 'border-left-width');
}
const _computePosition = function(obj, element){
    return {
        left: obj.x - _styleNumber(element, 'margin-left'),
        top: obj.y - _styleNumber(element, 'margin-top')
    };
}

/**
 * DOM操作的工具方法
 * @module dom
 * @example
 * import {dom} from '@o2oa/util';
 *
 * //创建div元素
 * const div = dom('div');
 * //设置div的title属性
 * dom.set(div, 'title', 'my title')
 * */

 /**
 * @summary 创建一个Element对象
 * @function
 * @instance
 * @param tag {String} - 要创建的Element的tagName。使用.来设置元素的class；使用#来设置元素的id
 * @param [options] {Object} - 创建的Element的属性
 * @return {Element} 新建的元素（Element）。此元素还没有被添加到Dom树中
 * @example
 * //创建一个div元素，如下html：
 * //<div class="myclass" id="myid" title="this is div title">text in div</div>
 * const div = dom('div.myclass#myid', {text: "text in div", title: "this is div title"});
 * */
const dom = (tag, options)=>{
    let tagName;
    const selector = tag.replace(/^\S*?(?=\.|#|$)/, (match)=>{
        tagName = match;
        return '';
    });

    let id = '', classes;
    if (selector){
        const props = selector.split('.').filter((p)=>{
            return !!p;
        });
        classes = props.map((p)=>{
            const ps = p.split('#');
            const cls = ps.shift();
            id = ps.pop() || id;
            return cls;
        });
    }
    const node = document.createElement(tagName);
    if (id) node.setAttribute('id', id);
    if (classes && classes.length) node.setAttribute('class', classes.join(' '));
    if (options) dom.set(node, options);
    return node;
}
Object.assign(dom, {
    /**
     * @summary 此方法返回文档中与指定选择器或选择器组匹配的第一个 Element对象。如果找不到匹配项，则返回null。（同document.querySelector或Element.querySelector方法）
     * @instance
     * @param selector {String | Element} - 是一个 CSS 选择器字符串
     * @param [el] {Element} - 可选，在此元素的后代元素中查找
     * @return {Element} 满足指定选择器组的第一个元素
     * */
    el: (selector, el)=>{
        return (typeOf(selector)==='string' && selector) ? (el || document).querySelector(selector) : selector;
    },

    /**
     * @summary 此方法返回与指定的选择器组匹配的文档中的元素列表 (使用深度优先的先序遍历文档的节点)。返回的对象是 NodeList 。（同document.querySelectorAll或Element.querySelectorAll方法）
     * @instance
     * @param selector {String} - 是一个 CSS 选择器字符串
     * @param [el] {Element} - 可选，在此元素的后代元素中查找
     * @return {NodeList} 一个静态 NodeList，包含一个与至少一个指定选择器匹配的元素的Element对象，或者在没有匹配的情况下为空NodeList
     * */
    els: (selector, el)=>{
        return (el || document).querySelectorAll(selector);
    },

    /**
     * @summary 为一个Element添加class属性值
     * @instance
     * @param el {Element|String} - 要为哪个Element添加class属性（目标Element）。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取要设置的Element对象。
     * @param className {String} - 要添加的class属性值
     * @return {Element} 返回添加class属性值后的目标Element
     * */
    addClass: (el, className)=>{
        const e = dom.el(el);
        _classes(className).forEach((name)=>{
            e.classList.add(name);
        });
        return e;
    },

    /**
     * @summary 为一个Element删除一个class属性值
     * @instance
     * @param el {Element|String} - 要为哪个Element删除class属性值(目标Element)。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取要设置的Element对象。
     * @param className {String} - 要删除的class属性值，如果目标Element的class属性中没有这个值，则什么都不做。
     * @return {Element} 返回删除class属性值后的目标Element
     * */
    removeClass: (el, className)=>{
        const e = dom.el(el);
        _classes(className).forEach((name)=>{
            e.classList.remove(name);
        });
        return e;
    },

    /**
     * @summary 判断一个Element的class属性中是否有指定的值
     * @instance
     * @param el {Element|String} - 要判断哪个Element（目标Element）。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取要判断的Element对象。
     * @param className {String} - class属性值，判断这个值是否存在。
     * @return {Boolean} 返回true或false，表示指定的class属性值在目标Element上是否存在
     * */
    hasClass: (el, className)=>{
        return dom.el(el).classList.contains(className);
    },

    /**
     * @summary 切换一个Element的class属性值。如果有指定属性值，则删除它；如果没有指定属性值，则添加它。
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @param className {String} - class属性值。
     * @param [force] {Boolean} - 是否强制添加class属性值，默认为false。当此参数为true时，效果等同于dom.addClass。 @see dom:addClass
     * @return {Element} 返回处理后的目标Element
     * @example
     * import {dom} from '@o2oa/util';
     *
     * //点击按钮事件，切换'#light'元素的开关状态（class值是否有'on'）。
     * button.addEventListener('click', ()=>{
     *     dom.toggleClass('#light', 'on');
     * });
     * */
    toggleClass: (el, className, force=false)=>{
        const e = dom.el(el);
        _classes(className).forEach((name)=>{
            let f = force;
            if (f == null || f===false) f = !dom.hasClass(e, name);
            (f) ? dom.addClass(e, name) : dom.removeClass(e, name);
        });
        return e;
    },

    checkClass: (el, className, check)=>{
        const e = dom.el(el);
        _classes(className).forEach((name)=>{
            (check) ? dom.addClass(e, name) : dom.removeClass(e, name);
        });
        return e;
    },
    mapProps: (el, props)=>{
        const e = dom.el(el);
        for (const p in props){
            const hp = hyphenate(p);
            if ((typeof props[p])==='boolean'){
                props[p] = e.hasAttribute(p) || e.hasAttribute(hp) || false;
            }else{
                const v = e.getAttribute(p) || e.getAttribute(hp) || '';
                if (v !== props[p]){
                    props[p] = v;
                }
            }
        }
        return e;
    },
    toggleAttr: (el, attr, value)=>{
        const e = dom.el(el);
        if (value) {
            e.setAttribute(attr, value);
        }else{
            e.removeAttribute(attr);
        }
        return e;
    },

    /**
     * @summary 给目标Element设置属性
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @param key {String|Object} - 如果传入字符串，表示要设置的属性名，传入'text'可设置元素的textContent，传入'html'可设置元素的innerHTML。也可以传入一个json对象，可以设置多个属性名和值，当传入对象时，忽略第三个参数。
     * @param [value] {String} - 要设置的属性值。
     * @return {Element} 返回处理后的目标Element
     * @example
     * import {dom} from '@o2oa/util';
     *
     * //为'#div'元素设置title属性
     * dom.set('#div', 'title', 'my div title');
     *
     * //为'#input'元素设置type、name和max属性
     * dom.set('#input', {
     *     type: 'number',
     *     name: 'count',
     *     max: '100'
     * });
     * */
    set: (el, key, value)=>{
        const e = dom.el(el);
        if (typeof key === 'object'){
            Object.keys(key).forEach((k)=>{
                setAttribute(e, k, key[k]);
            });
        }else{
            setAttribute(e, key, value);
        }
        return e;
    },

    /**
     * @summary 获取目标Element的属性值
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @param key {String} - 要获取的属性值。传入'text'可获取元素的textContent，传入'html'可获取元素的innerHTML
     * @return {String} 返回属性值
     * */
    get: (el, key)=>{
        const e = dom.el(el);
        return getAttribute(e, key);
    },
    setProperty: (el, name, value)=>{
        const e = dom.el(el);
        e.setAttribute(name, value);
        return e;
    },

    /**
     * @summary 获取目标Element滚动的距离
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @return {Object} 返回一个对象表示目标Element滚动的距离。返回对象中的 x 表示元素的内容水平滚动的像素数；y 表示元素的内容垂直滚动的像素数。
     * */
    getScroll: (el)=>{
        const e = dom.el(el);
        return {x: e.scrollLeft, y: e.scrollTop};
    },
    getScrolls: (el)=>{
        const e = dom.el(el);
        let element = e.parentNode, position = {x: 0, y: 0};
        while (element && !_isBody(element)){
            position.x += element.scrollLeft;
            position.y += element.scrollTop;
            element = element.parentNode;
        }
        return position;
    },

    /**
     * @summary 获取包含目标Element的最近的上级定位元素
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @return {Element} 返回包含目标Element的最近的上级定位元素。如果无法获取，则返回document.body
     * */
    getOffsetParent: (el)=>{
        let node = el.offsetParent;
        while (node && ['table', 'td', 'th'].includes(node.tagName.toString().toLowerCase())){
            node = node.offsetParent;
        }
        return node || document.body;
    },

    /**
     * @summary 获取目标Element的位置
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @param [relative] {Element} - 可选。如果传入此参数，则相对此元素计算目标元素的位置；否则相对于document。
     * @return {Element} 返回一个对象表示目标Element的位置。返回对象中的 x 表示元素的左边距离relative元素（或document）的左边的像素；y 表示元素的顶部距离relative元素（或document）的顶部的像素
     * */
    getPosition: (el, relative)=>{
        const e = dom.el(el);

        if (relative===window) relative = document.documentElement;
        while (relative && dom.getStyle(relative, 'display')==='contents'){
            relative = relative.parentElement;
        }
        while (el && dom.getStyle(el, 'display')==='contents'){
            el = el.parentElement;
        }
        const bound = el.getBoundingClientRect(),
            html = document.documentElement,
            htmlScroll = dom.getScroll(html),
            elemScrolls = dom.getScrolls(e),
            isFixed = (dom.getStyle(e, 'position') === 'fixed');

        const offset = {
            x: bound.left + elemScrolls.x + ((isFixed) ? 0 : htmlScroll.x) - html.clientLeft,
            y: bound.top + elemScrolls.y + ((isFixed) ? 0 : htmlScroll.y) - html.clientTop
        };
        const scroll = dom.getScrolls(e);

        const position = {
            x: offset.x - scroll.x,
            y: offset.y - scroll.y
        };

        if (relative){
            const relativePosition = dom.getPosition(relative);
            return {x: position.x - relativePosition.x - _leftBorder(relative), y: position.y - relativePosition.y - _topBorder(relative)};
        }
        return position;
    },

    /**
     * @summary 设置目标Element的位置。（left和top）
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @param positions {Object} - 要设置的位置对象。如 {x: 100, y: 150}。单位是px
     * @return {Element} 返回目标元素Element
     * */
    setPosition: (el, positions)=>{
        const e = dom.el(el);
        return dom.setStyles(e, _computePosition(positions. this));
    },

    /**
     * @summary 获取目标元素的大小
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @return {Object} 返回表示目标元素大小的json对象。x：元素的宽度；y：元素的高度。单位是px
     * */
    getSize: (el)=>{
        const e = dom.el(el);

        if (_isHtml(e)){
            return {x: e.clientWidth, y: e.clientHeight};
        }else{
            const rect = e.getBoundingClientRect();
            return {x: rect.width, y: rect.height};
        }
    },
    /**
     * @summary 获取目标元素的大小，包括由于 overflow 溢出而在屏幕上不可见的内容。
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @return {Object} 返回表示目标元素大小的json对象。x：元素的宽度；y：元素的高度。单位是px
     * */
    getScrollSize: (el)=>{
        const e = dom.el(el);
        if ((/^(?:body|html)$/i).test(e.tagName)){
            const doc = document.documentElement, body = document.body;
            return {x: Math.max(doc.scrollWidth, body.scrollWidth), y: Math.max(doc.scrollHeight, body.scrollHeight)};
        }
        return {x: e.scrollWidth, y: e.scrollHeight};
    },

    /**
     * @summary 获取目标元素计算后的样式值。
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @param key {String} - 要获取的css样式名称。
     * @return {String} 返回目标元素的指定样式值
     * */
    getStyle: function(el, key){
        const e = dom.el(el);
        const property = camelCase(key);
        let result = e.style[property];
        if (!result){
            const style = window.getComputedStyle(e);
            result = style[property];
        }
        return result;
    },
    getStyles: (el)=>{
        const arr = [...arguments];
        const e = dom.el(arr.shift());
        const r = {};
        arr.forEach((arg)=>{
            r[arg] = dom.getStyle(e, arg);
        });
        return r;
    },

    /**
     * @summary 给目标Element设置css样式
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @param key {String|Object} - 如果传入字符串，表示要设置的样式名。也可以传入一个json对象，可以设置多个样式和值，当传入对象时，忽略第三个参数。
     * @param [value] {String} - 要设置的样式值。
     * @return {Element} 返回处理后的目标Element
     * @example
     * import {dom} from '@o2oa/util';
     *
     * //为'#div'元素设置width样式
     * dom.setStyle('#div', 'width', '200px');
     *
     * //为'#input'元素设置 width、border 和 cursor 样式
     * dom.setStyle('#input', {
     *     width: '200px',
     *     border: '1px solid #333333',
     *     cursor: 'pointer'
     * });
     * */
    setStyle: (el, key, value)=>{
        const e = dom.el(el);
        if (typeof key === 'object'){
            Object.keys(key).forEach((k)=>{
                e.style[camelCase(k)] = key[k];
            });
        }else{
            e.style[camelCase(key)] = value;
        }
        return e;
    },
    setStyles: (el, styles)=>{
        return dom.setStyle(el, styles);
    },

    /**
     * @summary 判断目标元素是否是一个定位元素。
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @return {Boolean} 如果目标元素是一个定位元素（display样式值为：'absolute', 'fixed', 'relative', 'sticky'），则返回 true；否则返回 false。
     * */
    isPositioned: (el)=>{
        const e = dom.el(el);
        const position = dom.getStyle(e, 'position');
        return position && ['absolute', 'fixed', 'relative', 'sticky'].includes(position);
    },

    /**
     * @summary 设置目标元素可见。
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @return {Element} 目标元素。
     * */
    show: (el)=>{
        const e = dom.el(el);
        const ds = dom.getStyle(e, 'display');
        if (ds==='none'){
            dom.setStyle(e, 'display', (e.dataset['storeDisplay'] || 'block'));
        }
        return e;
    },

    /**
     * @summary 设置目标元素隐藏。
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @return {Element} 目标元素。
     * */
    hide: (el)=>{
        const e = dom.el(el);
        const ds = dom.getStyle(e, 'display');
        if (ds!=='none'){
            e.dataset['storeDisplay'] = ds;
            dom.setStyle(e, 'display', 'none');
        }
        return e;
    },

    /**
     * @summary 获取包含目标元素的可滚动元素。
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @return {Element} 目标元素上级最近的可滚动元素，如果没有，则返回document.documentElement。
     * */
    getParentSrcollNode: (el)=>{
        const e = dom.el(el);
        let node = e.parentElement;
        let ov = dom.getStyle(node, "overflow");
        let ovy = dom.getStyle(node, "overflow-y");

        while (node && (dom.getScrollSize(node).y <= dom.getSize(node).y || (!['auto', 'scroll'].includes(ov) && !['auto', 'scroll'].includes(ovy) ))) {
            node = node.parentElement;
            if (node){
                ov = dom.getStyle(node, "overflow");
                ovy = dom.getStyle(node, "overflow-y");
            }
        }
        return node || document.documentElement;
    },

    /**
     * @summary 获取目标元素符合匹配条件的最近上级元素。
     * @instance
     * @param el {Element|String} - 目标Element。可以是一个 CSS 选择器字符串，或者是一个Element对象。如果是CSS选择器字符串，则用dom.el获取Element对象。
     * @param [match] {String} - 可选。一个 CSS 选择器字符串，如果设置，获取的上级元素必须比配此选择器，如果忽略此参数，就直接获取目标元素的父元素。
     * @return {Element} 查询到的匹配match的上级元素。如果没有匹配的上级元素，则返回 null;
     * */
    getParent: function(el, match){
        if (match){
            let e = el.parentElement;
            while (e && !e.matches(match)){
                e = e.parentElement;
            }
            return e;
        }else{
            return el.parentElement
        }
    },
    isBody: (el)=>{
        return _isBody(dom.el(el))
    },
    isHtml: (el)=>{
        return _isHtml(dom.el(el))
    },
    empty: (el)=>{
        const e = dom.el(el);
        while(e.childNodes.length) e.removeChild(e.childNodes[0]);
        return e;
    },
    peel: (el, keepPlace)=>{
        const {node, position} = (keepPlace && typeOf(keepPlace)==='element') ? {node: keepPlace, position: 'beforeend'} : {node: el, position: 'beforebegin'};

        let n = el.firstElementChild;
        while (n){
            node.insertAdjacentElement(position, n);
            n = el.firstElementChild;
        }
        el.remove();
    },
    getPositionParent(el){
        let node = dom.el(el).parentElement;
        while (node){
            const tag = node.tagName.toString().toLowerCase();
            // if (['application', 'oo-dialog'].includes(tag)){
            //     return node.parentElement;
            // }

            if (tag==='application'){
                // return node.parentElement;
                return node;
            }
            if (tag==='oo-dialog'){
                return node._content;
            }
            node = node.parentElement;
        }
        return document.body;
    },
    //将el绝对定位，并与other的位置和大小完全重叠
    overlap(el, other, pNode = document.body, z){
        const size = this.getSize(dom.el(other));
        const position = this.getPosition(dom.el(other), pNode);
        const node = dom.el(el);
        this.setStyles(node, {
            'position': 'absolute',
            'width': `${size.x}px`,
            'height': `${size.y}px`,
            'top': `${position.y}px`,
            'left': `${position.x}px`,
        });
        if (!node.isConnected) pNode.append(node);
    },

    appendTop(pNode, node){
        const firstChild = pNode.firstChild;
        if (firstChild){
            return pNode.insertBefore(node, firstChild);
        }else{
            return pNode.appendChild(node)
        }
    }

});

export {
    dom
};
