import {dom, hyphenate} from "@o2oa/util";
import '../web-component/menu/menu.js';
import '../web-component/menu/menuItem.js';

class Menu{
    options = {
        css: '',
        cssLink: '',
        skin: ''
    };
    constructor(target, options) {
        this.options = Object.assign(this.options, options);
        this.target = target;
        this.container = this.options.container || dom.getOffsetParent(target);
        this.area = this.options.area;

        const event = this.options.event || 'click';
        this.target.addEventListener(event, ()=>{
            this.show();
        });
        this.target.addEventListener('mousedown', (e)=>{
            // e.stopPropagation();
        });
    }
    show(){
        if (!this.menu) this.#createMenu();
        this.#positionMenu()
        this.menu.show();
    }
    hide(){
        this.menu.hide();
    }
    #createMenu(){
        this.menu = dom('oo-menu', this.options?.attr);
        this.container.append(this.menu);
        if (this.options.items && this.options.items.length){
            this.#createMenuItems();
        }
        if (this.options.class) dom.addClass(this.menu, this.options.class);
    }
    #createMenuItems(){
        this.options.items.forEach((item)=>{
            this.#createMenuItem(item);
        });
    }
    #createMenuItem(item){
        const itemNode = document.createElement('oo-menu-item');
        this.menu.append(itemNode);
        if (item==='-'){
            itemNode.setAttribute('type', 'hr');
        }else{
            Object.keys(item).forEach((k)=>{
                if (k!=='command'){
                    itemNode.setAttribute(hyphenate(k), item[k]);
                }else{
                    itemNode.addEventListener('click', item.command);
                }
            });

            itemNode.addEventListener('click', (e)=>{
                this.menu.dispatchEvent(new CustomEvent('command', {detail: item}));
            });
        }
    }
    #positionMenu(){
        const targetSize = dom.getSize(this.target);
        const targetPosition = dom.getPosition(this.target, this.container);
        const menuSize = dom.getSize(this.menu);
        let top = targetPosition.y+targetSize.y;
        let left = targetPosition.x;
        let originX = 'left';
        let originY = 'top';

        if (this.area){
            const areaSize = dom.getSize(this.area);
            const p = dom.getPosition(this.container, this.area);
            if (p.x+left+menuSize.x>areaSize.x){
                left = left-menuSize.x+targetSize.x;
                originX = 'right';
            }
            if (p.y+top+menuSize.y>areaSize.y){
                top = top-menuSize.y-targetSize.y;
                originY = 'bottom';
            }
        }
        dom.setStyles(this.menu, {
            top: top+'px',
            left: left+'px',
            'transform-origin': `${originX} ${originY}`
        });
    }
    destroy(){
        this.menu.remove();
    }
}

export {Menu};
