import OOButton from './web-component/form/button.js';
import OOCalendar from "./web-component/widget/calendar.js";
import OOCalendarView from "./web-component/widget/calendarView.js";
import OOCapsulae from "./web-component/form/capsulae.js"
import OOCard from "./web-component/widget/card.js";
import OOCheckbox from "./web-component/form/check.js";
import OOCheckGroup from "./web-component/form/checkGroup.js";
import OODatetime from "./web-component/form/datetime.js";
import OOFile from "./web-component/widget/file.js";
import OOIcon from "./web-component/widget/icon.js";
import OOInput from './web-component/form/input.js';
import OOFiles from './web-component/form/files.js';
import OOOptionGroup from "./web-component/form/optionGroup.js";
import OOOption from "./web-component/form/option.js";
import OOOrg from "./web-component/widget/org.js";
import OOPagination from "./web-component/widget/pagination.js";
import OORadio from './web-component/form/radio.js';
import OORadioGroup from "./web-component/form/radioGroup.js";
import OOSelect from "./web-component/form/select.js";
import OOSelector from "./web-component/form/selector.js";
import OOSkeleton from "./web-component/widget/skeleton.js";
import OOSwitch from "./web-component/form/switch.js";
import OOTab from "./web-component/widget/tab.js";
import OOTabs from "./web-component/widget/tabs.js";
import OOTag from "./web-component/form/tag.js";
import OOTextarea from "./web-component/form/textarea.js";
import OONav from './web-component/widget/nav.js';
import OOCurrency from "./web-component/form/currency";
import OOCascade from './web-component/form/cascade.js';

export function defineComponent(){
    if (!customElements.get('oo-button')) customElements.define('oo-button', OOButton);
    if (!customElements.get('oo-calendar')) customElements.define('oo-calendar', OOCalendar);
    if (!customElements.get('oo-calendar-view')) customElements.define('oo-calendar-view', OOCalendarView);
    if (!customElements.get('oo-capsulae')) customElements.define('oo-capsulae', OOCapsulae);
    if (!customElements.get('oo-card')) customElements.define('oo-card', OOCard);

    if (!customElements.get('oo-checkbox-group')) customElements.define('oo-checkbox-group', OOCheckGroup);
    if (!customElements.get('oo-checkbox')) customElements.define('oo-checkbox', OOCheckbox);

    if (!customElements.get('oo-datetime')) customElements.define('oo-datetime', OODatetime);
    if (!customElements.get('oo-file')) customElements.define('oo-file', OOFile);
    if (!customElements.get('oo-icon')) customElements.define('oo-icon', OOIcon);

    if (!customElements.get('oo-input')) customElements.define('oo-input', OOInput);
    if (!customElements.get('oo-files')) customElements.define('oo-files', OOFiles);

    if (!customElements.get('oo-select')) customElements.define('oo-select', OOSelect);
    if (!customElements.get('oo-option-group')) customElements.define('oo-option-group', OOOptionGroup);
    if (!customElements.get('oo-option')) customElements.define('oo-option', OOOption);

    if (!customElements.get('oo-cascade')) customElements.define('oo-cascade', OOCascade);

    if (!customElements.get('oo-org')) customElements.define('oo-org', OOOrg);
    if (!customElements.get('oo-pagination')) customElements.define('oo-pagination', OOPagination);

    if (!customElements.get('oo-radio-group')) customElements.define('oo-radio-group', OORadioGroup);
    if (!customElements.get('oo-radio')) customElements.define('oo-radio', OORadio);


    if (!customElements.get('oo-selector')) customElements.define('oo-selector', OOSelector);
    if (!customElements.get('oo-skeleton')) customElements.define('oo-skeleton', OOSkeleton);
    if (!customElements.get('oo-switch')) customElements.define('oo-switch', OOSwitch);
    if (!customElements.get('oo-tab')) customElements.define('oo-tab', OOTab);
    if (!customElements.get('oo-tabs')) customElements.define('oo-tabs', OOTabs);
    if (!customElements.get('oo-tab')) customElements.define('oo-tag', OOTag);
    if (!customElements.get('oo-textarea')) customElements.define('oo-textarea', OOTextarea);

    if (!customElements.get('oo-nav')) customElements.define('oo-nav', OONav);

    if (!customElements.get('oo-currency')) customElements.define('oo-currency', OOCurrency);

    if (HTMLFormElement){
        const validityNodes = 'oo-input, oo-files, oo-radio-group, oo-checkbox-group, oo-selector, oo-select';

        if (HTMLFormElement.prototype.reportValidity && !HTMLFormElement.prototype.reportValidity.OOInputAdded){
            const o = HTMLFormElement.prototype.reportValidity;
            HTMLFormElement.prototype.reportValidity = function(){
                const flag = o.apply(this);
                if (!flag) return flag;
                const nodes = this.querySelectorAll(validityNodes);
                for (const i of nodes){
                    if (i.reportValidity){
                        if (!i.checkValidity()) return false;
                    }
                }
                return true;
            }
            HTMLFormElement.prototype.reportValidity.OOInputAdded = true;
        }
        if (HTMLFormElement.prototype.checkValidity && !HTMLFormElement.prototype.checkValidity.OOInputAdded){
            const o = HTMLFormElement.prototype.checkValidity;
            HTMLFormElement.prototype.checkValidity = function(){
                let flag = o.apply(this);
                const nodes = this.querySelectorAll(validityNodes);
                for (const i of nodes){
                    if (i.checkValidity){
                        flag = flag && i.checkValidity();
                    }
                }
                return flag;
            }
            HTMLFormElement.prototype.checkValidity.OOInputAdded = true;
        }
        if (HTMLFormElement.prototype.submit && !HTMLFormElement.prototype.submit.OOInputAdded){
            const o = HTMLFormElement.prototype.submit;
            HTMLFormElement.prototype.submit = function(){
                if (this.reportValidity()){
                    return o.apply(this);
                }
            }
            HTMLFormElement.prototype.submit.OOInputAdded = true;
        }
    }
}

export {Notice, notice} from './api/notice.js';
export {Menu} from './api/menu.js';
export {Dialog, dialog, confirm} from './api/dialog.js';
export {Tooltip, tooltip} from './api/tooltip.js';
export {mask, unmask} from './api/mask.js';
// export {confirm} from './api/confirm.js';
export {Draggable, Dragdrop} from './api/drag.js';
export {DatetimePicker, datetimePicker} from './api/datetimePicker.js';
