import '../web-component/calendar.js';
import {dom, hyphenate, typeOf} from "@o2oa/util";
import {Menu} from "./menu.js";

export class DatetimePicker extends Menu{
    #calendarArea = null;
    #className = null;
    #styleNode = null;
    options = {
        css: '',
        cssLink: '',
        skin: '',
        view: 'date', //date month year
        mode: 'datetime',
        baseDate: null,
        secondEnable: true,
        cleanEnable: false,
        todayEnable: true,
        datetimeRange: '',
        dateRange: '',
        timeRange: '',
        format: "",
        weekBegin: 1, //0表示周日，1表示周一
        value: ''
    };
    constructor(target, options) {
        super(target, options);
        this.options = Object.assign(this.options, options);
    }
    reset(options){
        this.options = Object.assign(this.options, options);
        if(this.calendar){
            for( let key in this.options ){
                if(typeOf(this.options[key])!=='null'){
                    this.calendar.setAttribute(hyphenate(key), this.options[key]);
                }
            }
        }
    }
    show(){
        if (!this.menu) this.#createMenu();
        this.#positionMenu()
        this.menu.show();
    }
    #createMenu(){
        this.menu = dom('oo-menu', this.options?.attr);
        this.container.append(this.menu);
        this.#calendarArea = dom('div', {
            slot: 'items',
            styles: {
                height: '100%'
            }
        });
        this.menu.append(this.#calendarArea);
        this.#renderCalendar();
        if (this.options.class) dom.addClass(this.menu, this.options.class);
    }
    #renderCalendar(){
        if( !this.calendar ){
            let template = `<oo-calendar`;
            for( let key in this.options ){
                if(typeOf(this.options[key])!=='null'){
                    template += ` ${hyphenate(key)}='${this.options[key]}'`;
                }
            }
            template += `></oo-calendar>`;
            dom.set(this.#calendarArea, 'html', template);
            this.calendar = this.#calendarArea.firstElementChild;

            this.calendar.addEventListener('change', (e)=>{
                this.target.dispatchEvent(new CustomEvent("change", e));
            })
        }
    }
    #positionMenu(){
        const targetSize = dom.getSize(this.target);
        const targetPosition = dom.getPosition(this.target, this.container);
        const menuSize = dom.getSize(this.menu);
        let top = targetPosition.y+targetSize.y;
        let left = targetPosition.x;
        let originX = 'left';
        let originY = 'top';

        if (this.area){
            const areaSize = dom.getSize(this.area);
            const p = dom.getPosition(this.container, this.area);
            if (p.x+left+menuSize.x>areaSize.x){
                left = left-menuSize.x+targetSize.x;
                originX = 'right';
            }
            if (p.y+top+menuSize.y>areaSize.y){
                top = top-menuSize.y-targetSize.y;
                originY = 'bottom';
            }
        }
        dom.setStyles(this.menu, {
            top: top+'px',
            left: left+'px',
            'transform-origin': `${originX} ${originY}`
        });
    }
}

export function datetimePicker(target, options){
    return new DatetimePicker(target, options);
}
