import {dom, hyphenate, typeOf, formatDate} from '@o2oa/util';
import {$Input} from './$input.js';
import css from './template/datetime.scope.css?inline';
import '../calendar.js';

const DATE = 'date';
const YEAR = 'year';
const MONTH = 'month';
const WEEK = 'week';
const TIME = 'time';

const FORMAT_DATE_TIME = 'YYYY-MM-DD HH:mm:ss';
const FORMAT_DATE = 'YYYY-MM-DD';
const FORMAT_YEAR = 'YYYY';
const FORMAT_MONTH = 'YYYY-MM';
const FORMAT_WEEK = 'YYYY WW';
const FORMAT_TIME = 'HH:mm:ss';
const FORMAT_TIME_WITHOUT_SECOND = 'HH:mm';

export default class OODatetime extends $Input {
    static calendarProp = {
        view: 'date', //date month year
        mode: 'datetime',
        baseDate: null,
        secondEnable: false,

        cleanEnable: true,
        todayEnable: true,

        datetimeRange: '',
        dateRange: '',
        timeRange: '',
  
        weekBegin: 1, //0表示周日，1表示周一

        value: '',
        format: '',

        rightIcon: 'calendar',
        read: false
    };
    static get observedAttributes() {
        OODatetime.prop = Object.assign({}, OODatetime.prop, OODatetime.calendarProp);
        return Object.keys(OODatetime.prop).map((att)=>{
            return hyphenate(att);
        });
    }


    constructor() {
        super();
        // OODatetime.prop = Object.assign({}, OODatetime.prop, OODatetime.calendarProp);
        this.#setPropMap();
        this._init('oo-datetime', OODatetime);
        this._useCss(css);
    }

    #setPropMap() {
        this._setPropMap = Object.assign(this._setPropMap, {
            disabled: () => {
                dom.toggleAttr(this._elements.input, 'disabled', this._props.disabled);
                dom.checkClass(this._elements.box, 'disabled', this._props.disabled);
                dom.checkClass(this._elements.label, 'disabled', this._props.disabled);
                dom.checkClass(this._elements.suffix, 'disabled', this._props.disabled);
            },
            value: () => {
                if (this._props.value){
                    try {
                        this._props.value = formatDate(new Date(this._props.value), this._props.format || this.getDefaultFormat());
                    }catch (e) {
                        this._props.value = '';
                    }
                }       
                if (this.#calendar) this.#calendar.setAttribute('value', this._props.value);
                this._elements.input.value = this._props.value;
                this._elements.viewText.textContent = this._props.value || '-';
            },
            timeRange: () => {
                if (this.#calendar) this.#calendar.setAttribute('time-range', this._props.timeRange);
            },
            read: ()=>{
            },
            readonly: ()=>{},
            readmode: ()=>{
                // dom.toggleAttr(this._elements.input, 'readonly', this._props.readmode);
                dom.checkClass(this._elements.box, 'readmode', this._props.readmode);
                dom.checkClass(this._elements.label, 'readmode', this._props.readmode);

                this._setPropMap.viewStyle();
                this._setPropMap.required();
            },
            $default: (key) => {
                if (OODatetime.calendarProp.hasOwnProperty(key)) {
                    if (this.#calendar) this.#calendar.setAttribute(hyphenate(key), this._props[key]);
                } else {
                    dom.toggleAttr(this._elements.input, key, this._props[key]);
                }
            }
        });
    }

    #droped = false;
    #calendarArea = null;
    #hideCalendarFun = null;
    #calendar = null;
    #optionsPositionNode = null;
    #reserveRange = {};
    #reserveCustomRange = {};

    getDefaultFormat() {
        return (this._props.mode === YEAR && FORMAT_YEAR) ||
            (this._props.mode === MONTH && FORMAT_MONTH) ||
            (this._props.mode === DATE && FORMAT_DATE) ||
            (this._props.mode === WEEK && FORMAT_WEEK) ||
            (this._props.mode === TIME && (this._props.secondEnable ? FORMAT_TIME : FORMAT_TIME_WITHOUT_SECOND)) ||
            FORMAT_DATE_TIME;
    }

    _render() {
        this.#droped = false;
        // this._elements.drop = dom('div.drop.ooicon-calendar');
        // this._elements.view.insertAdjacentElement('afterend', this._elements.drop);

        this._elements.input.setAttribute('readonly', 'readonly');

        this.#optionsPositionNode = this.querySelector('div.options-position-node');
        if (!this.#optionsPositionNode) {
            this.#optionsPositionNode = dom('div.options-position-node.hide');   
            this._elements.body.insertAdjacentElement('afterbegin', this.#optionsPositionNode);
            this.#optionsPositionNode.addEventListener('click', ()=>{
                this.#hideCalendar()
            });
        }

        this.#calendarArea = this.querySelector('div.calendar-area');
        if (!this.#calendarArea) {
            this.#calendarArea = dom('div.calendar-area.invisible');
            this.#optionsPositionNode.append(this.#calendarArea);
        }

        // if (!this.#calendarArea) {
        //     this.#calendarArea = dom('div.calendar-area.invisible');

        //     this._content.insertAdjacentElement('beforeend', this.#calendarArea);
        // }




        this._props.readonly = true;

        this._setPropMap.viewStyle();
        this._setPropMap.value();
    }

    _setEvent() {
        super._setEvent();
        this._elements.input.addEventListener('click', () => {
            if (!this._props.read && !this._props.readmode && !this._props.disabled){
                this.#droped = !this.#droped;
                if (this.#droped) {
                    this.#showCalendar();
                } else {
                    this.#hideCalendar();
                }
            }
        });
        this.#calendarArea.addEventListener('click', (e) => {
            e.stopPropagation();
        });
        this.#calendarArea.addEventListener('mousedown', (e) => {
            e.stopPropagation();
        });
        this._content.firstElementChild.addEventListener('mousedown', (e) => {
            e.stopPropagation();
        });
    }

    #positionCalendar() {
        // const position = dom.getPosition(this._elements.box, this._content);
        const size = dom.getSize(this._elements.box);
        // const x = position.x;
        // const y = position.y + size.y + 6 + size.y * 0.2;
        // const w = size.x;
        const p = dom.getPosition(this._elements.box, dom.getOffsetParent(this._elements.box));
        dom.setStyles(this.#optionsPositionNode, {
            left: p.x + 'px'
        });


        const y = size.y + 6 + size.y * 0.2;
        dom.setStyles(this.#calendarArea, {
            top: y + 'px',
            // left: x + 'px'
            //width: 360+'px'
        });
    }
    _connected(){
        if (!this.hasAttribute('right-icon')){
            this.setAttribute('right-icon', 'calendar');
        }
        this._elements.input.setAttribute('readonly', 'readonly');
    }
    #toggleStyles() {
        // dom.checkClass(this._elements.drop, 'down', this.#droped);
        dom.checkClass(this._elements.box, 'focus', this.#droped);
        dom.checkClass(this._elements.label, 'focus', this.#droped);
    }

    #showCalendar() {
        this.#droped = true;

        this.#toggleStyles();
        this.#positionCalendar();
        dom.removeClass(this.#optionsPositionNode, 'hide');

        window.setTimeout(()=>{
            dom.removeClass(this.#calendarArea, 'invisible');
            dom.addClass(document.body, 'ooui-select-visible');
        
            dom.addClass(this.#calendarArea, 'visible');
    
            this.ownerDocument.dispatchEvent(new MouseEvent('mousedown'));
    
            this.#renderCalendar();
    
            this.#hideCalendarFun = this.#hideCalendar.bind(this);
            this.ownerDocument.addEventListener('mousedown', this.#hideCalendarFun);
        }, 10);

        this.unInvalidStyle();
    }

    #hideCalendar() {
        this.#droped = false;
        this.#toggleStyles();
        dom.removeClass(this.#calendarArea, 'visible');
        dom.addClass(this.#calendarArea, 'invisible');
        dom.removeClass(document.body, 'ooui-select-visible');
        this.ownerDocument.removeEventListener('mousedown', this.#hideCalendarFun);

        window.setTimeout(()=>{
            dom.addClass(this.#optionsPositionNode, 'hide');
        }, 200);
    }

    #renderCalendar() {
        if (!this.#calendar) {

            let template = `<oo-calendar`;
            for (let [key] of Object.entries(OODatetime.calendarProp)) {
                if (typeOf(this._props[key]) !== 'null') template += ` ${hyphenate(key)}='${this._props[key]}'`;
            }
            template += `></oo-calendar>`;
            dom.set(this.#calendarArea, 'html', template);
            this.#calendar = this.#calendarArea.firstElementChild;

            this.#calendar.addEventListener('change', (e) => {
                this.setAttribute('value', e.detail.value);
                this.dispatchEvent(new CustomEvent('change', e));
            });

            const {datetimeRange, dateRange, timeRange} = this.#reserveRange;
            if(!!datetimeRange){
                this.#calendar.setDatetimeRange(datetimeRange);
            }else{
                !!dateRange && this.#calendar.setDateRange(dateRange);
                !!timeRange && this.#calendar.setTimeRange(timeRange);
            }

            for(var type in this.#reserveCustomRange) {
                this.#calendar.setCustomRangeMethod(type, this.#reserveCustomRange[type]);
            }
        }
    }

    setDatetimeRange(range){
        !!this.#calendar ?
            this.#calendar.setDatetimeRange(range) :
            this.#reserveRange.datetimeRange = range;
    }

    setDateRange(range){
        !!this.#calendar ?
            this.#calendar.setDateRange(range) :
            this.#reserveRange.dateRange = range;
    }

    setTimeRange(range){
        !!this.#calendar ?
            this.#calendar.setTimeRange(range) :
            this.#reserveRange.timeRange = range;
    }

    setCustomRangeMethod(type, method){
        !!this.#calendar ?
            this.#calendar.setCustomRangeMethod(type, method) :
            this.#reserveCustomRange[type] = method;
    }
}
