export default "* {\r\n    box-sizing: border-box;\r\n}\r\n.content {\r\n    --label: var(--oo-color-text2);\r\n    --label-width: 10em;\r\n    --icon-left: var(--oo-color-text2);\r\n    --icon-right: var(--oo-color-text2);\r\n    --focus: var(--oo-color-main);\r\n    --border: var(--oo-color-gray1);\r\n    --radius: var(--oo-default-radius);\r\n    --input: var(--oo-color-text);\r\n    --font-family: var(--oo-font-family);\r\n    --placeholder: var(--oo-color-text4);\r\n    --disabled: var(--oo-color-gray2);\r\n    --shadow: var(--oo-shadow-border);\r\n    --shadow-focus: var(--oo-shadow-border-focus);\r\n}\r\n.content {\r\n    height: 100%;\r\n    width: 100%;\r\n    /*padding: 2px;*/\r\n    position: relative;\r\n}\r\n.options-position-node{\r\n    position: absolute;\r\n    transition: background-color 0.2s;\r\n}\r\n.options-position-node.hide{\r\n    display: block !important;\r\n    opacity: 0 !important;\r\n    width: 0 !important;\r\n    height: 0 !important;\r\n}\r\n.labelArea {\r\n    display: flex;\r\n    align-items: center;\r\n    position: relative;\r\n    height: 100%;\r\n}\r\n.label {\r\n    white-space: nowrap;\r\n    transition: color 0.2s;\r\n    display: inline-flex;\r\n    align-items: center;\r\n    height: 100%;\r\n    color: var(--label);\r\n}\r\n.labelText {\r\n    padding: 0.5em 0.35em;\r\n    margin-right: 0.5em;\r\n    height: 100%;\r\n    display: flex;\r\n    align-items: center;\r\n}\r\n.flex-end {\r\n    justify-content: flex-end;\r\n}\r\n.flex-center {\r\n    justify-content: center;\r\n}\r\n.body{\r\n    flex: 1;\r\n}\r\n.box {\r\n    /*border-width: 1px;*/\r\n    /*border-style: solid;*/\r\n    /*border-color: var(--border);*/\r\n    border-radius: var(--radius);\r\n    box-shadow: var(--shadow);\r\n    display: flex;\r\n    align-items: center;\r\n    height: 100%;\r\n    width: 100%;\r\n    background: transparent;\r\n    transition: border-color 0.2s, box-shadow 0.2s, background-color 0.2s;\r\n}\r\n\r\n.box:has(.input.invalid) {\r\n    background: var(--oo-color-error-bg);\r\n    box-shadow: var(--oo-shadow-border-error);\r\n}\r\n\r\n.input {\r\n    border: 0;\r\n    outline: none;\r\n    color: var(--input);\r\n    border-radius: var(--radius);\r\n    padding: 0.5em 0.6em;\r\n    box-shadow: none;\r\n    height: 100%;\r\n    width: 100%;\r\n    font-size: 1em;\r\n    background: transparent;\r\n    font-family: var(--font-family);\r\n}\r\n.input[type='color'] {\r\n    padding: 0.2em;\r\n    height: revert;\r\n}\r\n\r\ninput[type='date']::-webkit-calendar-picker-indicator {\r\n    opacity: 0.8;\r\n    cursor: pointer;\r\n}\r\n\r\n.input[type='file'] {\r\n    position: absolute;\r\n    opacity: 0;\r\n}\r\n.file {\r\n    padding: 0.5em;\r\n    display: grid;\r\n    grid-template-columns: 1fr auto;\r\n    gap: 2em;\r\n    align-items: start;\r\n    flex: 1;\r\n}\r\n.fileList {\r\n    display: grid;\r\n    grid-template-columns: 1fr 1fr;\r\n    gap: 0.5em 1em;\r\n    align-items: stretch;\r\n    height: 100%;\r\n}\r\n.emptyInfo{\r\n    height: 100%;\r\n    display: flex;\r\n    align-items: center;\r\n    color: var(--oo-color-text4);\r\n}\r\n.fileInput{\r\n    position: absolute;\r\n    opacity: 0;\r\n    width: 0;\r\n    height: 0;\r\n}\r\ntextarea {\r\n    border: 0;\r\n    outline: none;\r\n    color: var(--input);\r\n    border-radius: var(--radius);\r\n    padding: 0.3em 0.6em;\r\n    box-shadow: none;\r\n    height: 100%;\r\n    width: inherit;\r\n    font-size: 1em;\r\n    background: transparent;\r\n}\r\n\r\ninput::placeholder,\r\ntextarea::placeholder {\r\n    color: var(--placeholder);\r\n}\r\n\r\ndiv.left-icon {\r\n    width: 1em;\r\n    text-align: right;\r\n    transition: color 0.2s;\r\n    font-size: 0.9em;\r\n    margin-left: 0.5em;\r\n    margin-top: 0.1875em;\r\n    color: var(--icon-left);\r\n}\r\ndiv.right-icon {\r\n    width: 1em;\r\n    text-align: left;\r\n    transition: color 0.2s;\r\n    font-size: 0.9em;\r\n    margin-right: 0.5em;\r\n    margin-top: 0.1875em;\r\n    color: var(--icon-right);\r\n}\r\n\r\n.box.focus {\r\n    /*border-color: var(--focus);*/\r\n    box-shadow: var(--shadow-focus);\r\n    outline: none;\r\n    color: var(--focus);\r\n}\r\n.box.focus div.left-icon {\r\n    color: var(--focus);\r\n}\r\n.box.focus div.right-icon {\r\n    color: var(--focus);\r\n}\r\n\r\n.label.focus {\r\n    color: var(--focus);\r\n    outline: none;\r\n}\r\n\r\n.box.disabled {\r\n    background-color: var(--disabled) !important;\r\n    outline: none;\r\n    cursor: not-allowed;\r\n    box-shadow: inset 0 0 0 1px var(--border);\r\n    color: inherit;\r\n}\r\n.box.disabled div.left-icon {\r\n    /*color: var(--oo-color-text4);*/\r\n    opacity: 0.6;\r\n    color: inherit;\r\n}\r\n.box.disabled div.right-icon {\r\n    /*color: var(--oo-color-text4);*/\r\n    opacity: 0.6;\r\n    color: inherit;\r\n}\r\n.label.disabled {\r\n    /*color: var(--oo-color-text4);*/\r\n    outline: none;\r\n    cursor: not-allowed;\r\n}\r\ninput:disabled,\r\ntextarea:disabled {\r\n    opacity: 0.45;\r\n    cursor: not-allowed;\r\n}\r\n\r\n.box.readmode {\r\n    outline: none;\r\n    cursor: default;\r\n    box-shadow: none;\r\n    background: transparent;\r\n}\r\n.box.readmode div.left-icon {\r\n    color: inherit;\r\n    display: none;\r\n}\r\n.box.readmode div.right-icon {\r\n    color: inherit;\r\n    display: none;\r\n}\r\n.label.readmode {\r\n    outline: none;\r\n    cursor: default;\r\n}\r\n.label.readmode.focus {\r\n    color: inherit;\r\n    outline: none;\r\n}\r\n\r\ninput:focus,\r\ntextarea:focus {\r\n    border: 0;\r\n    outline: none;\r\n    box-shadow: none;\r\n}\r\n.appearance::-webkit-inner-spin-button,\r\n.appearance::-webkit-outer-spin-button {\r\n    -webkit-appearance: none;\r\n    appearance: none;\r\n    margin: 0;\r\n}\r\n.hide {\r\n    display: none !important;\r\n}\r\ndiv.invalidHint {\r\n    /*position: absolute;*/\r\n    color: var(--oo-color-error);\r\n    height: 0;\r\n    overflow: hidden;\r\n    word-break: break-all;\r\n    transition: margin-top 0.2s, height 0.2s;\r\n}\r\ndiv.invalidHint.show {\r\n    margin: 0.3em 0 0 0em;\r\n    height: unset;\r\n    line-height: 1.2em;\r\n}\r\ndiv.invalidHint.show::before {\r\n    line-height: 1.4em;\r\n    margin-right: 0.2em;\r\n}\r\n.view {\r\n    position: relative;\r\n    overflow: hidden;\r\n    flex: 1;\r\n    height: 100%;\r\n}\r\n.view[type='file']{\r\n    position: absolute;\r\n    opacity: 0;\r\n}\r\n.readmode .input {\r\n    display: none;\r\n}\r\n.viewText {\r\n    width: 100%;\r\n    height: 100%;\r\n    top: 0;\r\n    left: 0;\r\n    overflow: hidden;\r\n    padding: 0.5em 0.6em;\r\n    display: none;\r\n}\r\n\r\n.readmode .viewText {\r\n    display: block;\r\n}\r\n.requiredFlag {\r\n    color: var(--oo-color-highlight);\r\n    padding-left: 0.3em;\r\n    font-size: 0.75em;\r\n}\r\n\r\n/* 移动端，标签在上，输入框下划线 skin-mode='mobile' */\r\n.content.mobile{\r\n    --oo-shadow-border: inset 0 -1px 0 0px var(--oo-color-gray-d);\r\n    --oo-shadow-border-focus: inset 0 -1px 0 0px var(--oo-color-main);\r\n    --oo-shadow-border-error: inset 0 -1px 0 0px var(--oo-color-error);\r\n    --oo-default-radius: 0;\r\n    --oo-area-radius: 0;\r\n}\r\n\r\n.content.mobile .labelArea {\r\n    display: flex !important;\r\n    align-items: unset !important;\r\n    position: static !important;\r\n    height: unset !important;\r\n    flex-direction: column;\r\n}\r\n\r\n.content.mobile .label {\r\n    color: var(--oo-color-text3);\r\n}\r\n\r\n.content.mobile .requiredFlag {\r\n    display: flex;\r\n    align-items: center;\r\n    box-shadow: var(--shadow);\r\n}\r\n.content.mobile .body>div{\r\n    align-items: stretch !important;\r\n}\r\n\r\n.content.mobile .labelText {\r\n    width: unset !important;\r\n    min-width: unset !important;\r\n    max-width: unset !important;\r\n    display: block !important;\r\n}\r\n.content.mobile .input {\r\n    padding: 0.5em 0.35em;\r\n    font-size: 1.125em;\r\n\r\n}\r\n.content.mobile .viewText {\r\n    padding: 0.5em 0.35em;\r\n    font-size: 1.125em;\r\n    background-color: transparent !important;\r\n    border-bottom: 1px solid #dddddd !important;\r\n}\r\n\r\n/* .content.mobile .hide {\r\n    display: none !important;\r\n} */\r\n\r\n/* --------------------------- */\r\n\r\n@media only screen and (max-width: 767px) {\r\n    .options-position-node{\r\n        position: fixed;\r\n        width: 100%;\r\n        height: 100%;\r\n        background-color: #cccccc33;\r\n        z-index: 300000;\r\n        top: 0 !important;\r\n        left: 0 !important;\r\n    }\r\n    \r\n    .options-area{\r\n        transform-origin: bottom !important;\r\n    }\r\n    .options-area{\r\n        top: unset !important;\r\n        min-width: unset !important;\r\n        width: 100% !important;\r\n        bottom: 0 !important;\r\n        height: 60%;\r\n        padding-bottom: 20px !important;\r\n    }\r\n    .arrow{\r\n        display: none;\r\n    }\r\n    .options-content{\r\n        height: 100%;\r\n        width: 100%;\r\n        max-height: unset !important;\r\n        min-height: unset !important;\r\n        font-size: 1.125em;\r\n        overscroll-behavior: contain;\r\n    }\r\n    \r\n    .calendar-area{\r\n        transform-origin: bottom !important;\r\n        top: unset !important;\r\n        min-width: unset !important;\r\n        width: 100% !important;\r\n        bottom: 0 !important;\r\n        display: flex;\r\n        justify-content: center;\r\n    }\r\n    .calendar-area.visible{\r\n        transform: scale(1);\r\n        opacity: 1;\r\n    }\r\n    .calendar-area.invisible{\r\n        transform: scale(1, 0);\r\n        opacity: 0;\r\n    }\r\n    \r\n    .calendar-area oo-calendar{\r\n        width: 100%;\r\n        padding-bottom: 20px !important;\r\n    }\r\n    .hide {\r\n        display: none !important;\r\n    }\r\n}"