export default "body{\r\n    display: inline-block;\r\n}\r\n* {\r\n    box-sizing: border-box;\r\n    user-select: none;\r\n}\r\n.content{\r\n    --color: var(--oo-color-text-white);\r\n    --bg: var(--oo-color-main);\r\n    --radius: var(--oo-default-radius);\r\n\r\n    --cancel-color: var(--oo-color-text2);\r\n    --cancel-bg: var(--oo-color-gray-d);\r\n\r\n    --simple-color: var(--oo-color-text3);\r\n    --simple-bg: transparent;\r\n\r\n    --light-color: var(--oo-color-text3);\r\n    --light-bg: var(--oo-color-bg-white);\r\n\r\n    --icon-color: var(--oo-color-text3);\r\n    --icon-bg: transparent;\r\n\r\n    --hover-bg: var(--oo-color-main-light);\r\n    --active-bg: var(--oo-color-main-deep);\r\n    --focus-bg: var(--oo-color-main-light);\r\n\r\n    --cancel-hover-bg: var(--oo-color-gray-d1);\r\n    --cancel-active-bg: var(--oo-color-gray-b);\r\n    --cancel-focus-bg: var(--oo-color-gray1);\r\n\r\n    --simple-hover-bg: var(--oo-color-gray-e1);\r\n    --simple-active-bg: var(--oo-color-gray-d);\r\n    --simple-focus-bg: var(--oo-color-gray1);\r\n\r\n    --light-hover-bg: var(--oo-color-main);\r\n    --light-active-bg: var(--oo-color-main-deep);\r\n    --light-focus-bg: var(--oo-color-bg-white);\r\n    --light-hover-color: var(--oo-color-text-white);\r\n    --light-active-color: var(--oo-color-text-white);\r\n    --light-focus-color: var(--oo-color-text3);\r\n\r\n    --icon-hover-bg: var(--oo-color-gray-e1);\r\n    --icon-active-bg: var(--oo-color-gray-d);\r\n    --icon-focus-bg: var(--oo-color-gray1);\r\n}\r\n\r\n.button {\r\n    border-radius: var(--radius);\r\n    border: 0;\r\n    padding: 0.375em 0.6em;\r\n    cursor: pointer;\r\n    color: var(--color);\r\n    background-color: var(--bg);\r\n    transition: background 0.1s, color 0.1s;\r\n    text-align: center;\r\n    display: flex;\r\n    align-items: baseline;\r\n    justify-content: center;\r\n    height: 100%;\r\n    width: 100%;\r\n    /*display: grid;*/\r\n    /*grid-template-columns: auto auto;*/\r\n    gap: 0.3em;\r\n}\r\n.button.cancel{\r\n    background-color: var(--cancel-bg);\r\n    color: var(--cancel-color);\r\n}\r\n.button.simple{\r\n    background-color: var(--simple-bg);\r\n    color: unset;\r\n    padding: 0.375em 0.375em;\r\n}\r\n.button.light{\r\n    background-color: var(--light-bg);\r\n    color: unset;\r\n    padding: 0.375em 0.8em;\r\n    border: 1px solid var(--oo-color-gray-b);\r\n}\r\n.button.icon{\r\n    background-color: var(--icon-bg);\r\n    align-items: center;\r\n    color: unset;\r\n    padding: 0.3125em;\r\n    gap: 0;\r\n}\r\n.button.icon .text{\r\n    display: none;\r\n}\r\n\r\ndiv.prefix{\r\n    display: none;\r\n    font-size: 0.9em;\r\n}\r\ndiv.suffix{\r\n    display: none;\r\n    font-size: 0.9em;\r\n}\r\ndiv.text{\r\n    height: 100%;\r\n    display: inline-flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n}\r\n\r\n.content>.icon{\r\n    display: block;\r\n}\r\n\r\n.button:hover{\r\n    background-color: var(--hover-bg);\r\n}\r\n.button:active{\r\n    background-color: var(--active-bg);\r\n}\r\n.button:focus{\r\n    background-color: var(--focus-bg);\r\n}\r\n.button.cancel:hover{\r\n    background-color: var(--cancel-hover-bg);\r\n}\r\n.button.cancel:active{\r\n    background-color: var(--cancel-active-bg);\r\n}\r\n.button.cancel:focus{\r\n    background-color: var(--cancel-focus-bg);\r\n}\r\n.button.simple:hover{\r\n    background-color: var(--simple-hover-bg);\r\n}\r\n.button.simple:active{\r\n    background-color: var(--simple-active-bg);\r\n}\r\n.button.simple:focus{\r\n    background-color: var(--simple-focus-bg);\r\n}\r\n\r\n.button.light:hover{\r\n    background-color: var(--light-hover-bg);\r\n    color: var(--light-hover-color);\r\n}\r\n.button.light:active{\r\n    background-color: var(--light-active-bg);\r\n    color: var(--light-active-color);\r\n}\r\n.button.light:focus{\r\n    background-color: var(--light-focus-bg);\r\n    color: var(--light-focus-color);\r\n}\r\n\r\n.button.icon:hover{\r\n    background-color: var(--icon-hover-bg);\r\n}\r\n.button.icon:active{\r\n    background-color: var(--icon-active-bg);\r\n}\r\n.button.icon:focus{\r\n    background-color: var(--icon-focus-bg);\r\n}\r\n\r\n\r\n.disabled, .disabled:hover, .disabled:active, .disabled:focus {\r\n    outline: none;\r\n    cursor: not-allowed;\r\n    opacity: 0.5;\r\n}\r\n.cancel.disabled, .cancel.disabled:hover, .cancel.disabled:active, .cancel.disabled:focus {\r\n    outline: none;\r\n    cursor: not-allowed;\r\n    opacity: 0.5;\r\n}\r\n.hide{\r\n    display: none !important;\r\n}\r\n"