import {dom as element} from "./element.js";
import {uuid} from "./common.js";
/**
 * @summary 引入外部html模板资源，并将html内容渲染到指定dom对象的某个位置。如请求html出现网络错误，抛出TypeError异常，如有请求得到404、500等错误，抛出一个cause为当前response的异常
 * @param {Object|String|Request} request 请求html的配置参数，如果是String，则为html的url地址，如果是对象，参考fetch的配置参数
 * @param {HTMLElement} dom html要渲染到哪个dom对象中
 * @param {Object} option 载入html文件的配置参数
 * <pre>
 *  {
 *      "reload":   如果相同路径的html文件已经引入了，是否重新载入,默认为：false
 *      "position": 渲染到的目标dom对象的位置，可以是以下值：'beforebegin' 'afterbegin' 'beforeend'（默认） 'afterend'
 *      "evalScripts": html模板中通过&lt;script&gt;引入或内嵌的javascript，是否要执行。默认为false
 *      "baseUrl": html模板中引用连接的baseUrl，默认为空
 *  }
 *  </pre>
 * */
async function loadHTML(request, dom, option) {
    const response = await fetch(request);
    if (response.ok){
        const htmltext = await response.text();
        if (!option) option = {};
        option.baseUrl = response.url;
        loadHTMLText(htmltext, dom, option);
    }else{
        const e = Error(`load html error: ${response.status}: ${response.statusText}`, {cause: response});
        e.name = 'HTTPStatusError';
        throw e;
    }
}

function loadHTMLText(htmltext, dom, option){
    const {text, scripts} = stripScriptSrcs(htmltext);
    const {html, scriptText} = stripScripts(text);
    const htmlStr = parseHtmlUrl(html, option.baseUrl);

    scripts.forEach((src)=>{
        const url = new URL(src, option.baseUrl).toString();
        const scriptNode = document.createElement('script');
        scriptNode.src = url;
        document.head.append(scriptNode);
    });
    dom.insertAdjacentHTML(option.position || 'beforeend', htmlStr);
}

function stripScriptSrcs(html){
    const scripts = [];
    const text = html.replace(/<script[^<]src\s*=\s*"([^"]*)[^\]]>/gi, (all, code)=>{
        scripts.push(code);
        return '';
    });
    return {text, scripts};
}
function stripScripts(h){
    let scriptText = '';
    var html = h.replace(/<script[^>]*>([\s\S]*?)<\/script>/gi, (all, code)=>{
        scriptText += code + '\n';
        return '';
    });
    return {html, scriptText};
}

function parseHtmlUrl(html, baseUrl){
    return html.replace(/(?:href|src)\s*=\s*"([^"]*)"/gi, (all, m)=>{
        return new URL(m, baseUrl).toString();
    });
}
function loadCssText(cssText, node) {
    const uid = `css${uuid()}`;
    let styleText = cssText.toString();
    if (styleText) {
        if (node) element.addClass(node, uid);
        //清除css注释
        styleText = styleText.replace(/\/\*(\s|\S)*?\*\//g, "");
        // styleText = this.execInterpolation(styleText);

        if (node){
            const rex = new RegExp("[^{}\\/]+\\s*(?=\\{)", "g");
            // const rex = new RegExp("(.+)(?=[\\r\\n]*{)", "g");
            const prefix = '.' + uid + ' ';
            styleText = styleText.replace(rex, (match)=>{
                const m = match.trim();
                if (m.startsWith('@') || m==='from' || m==='to') return m;

                const rules = m.split(/\s*,\s*/g).map(function (r) {
                    return prefix + r;
                });
                return  rules.join(", ");
            });
        }

        const style = element("style");
        style.setAttribute("type", "text/css");
        style.setAttribute("id", uid);

        document.head.appendChild(style);
        style.appendChild(document.createTextNode(styleText));
        return style;
    }
    return null;
}


export {loadHTML, loadCssText}
