import {dom, hyphenate, uuid, erase} from '@o2oa/util';
import {OOComponent} from '../base.js';
import html from './template/$check-group.html?raw';
import css from './template/$check-group.scope.css?raw';
import {I18n} from '../../common/i18n.js';

class $CheckGroup extends OOComponent {
    static prop = {
        disabled: false,
        value: '',
        name: '',
        size: '',
        col: '',
        label: '',
        labelStyle: '',
        labelAlign: 'right',
        viewStyle: '',
        skin: '',
        count: 0,
        readmode: false,
        skinMode: '',   //样式模式，在content元素上添加 class。默认模式（pc），可选（mobile）

        max: 0,
        maxlength: 0,
        min: 0,
        minlength: 0,
        pattern: '',
        required: false,
        step: 1,
        validity: '', //表单验证提示信息
        validityBlur: false, //失去焦点时是否进行有效性验证
    };

    static get observedAttributes() {
        return Object.keys(this.prop).map((att) => {
            return hyphenate(att);
        });
    }

    _items = [];
    #maxWidth = 0;
    _elements = {
        label: null,
        group: null,
        labelText: null,
        items: null,
        invalidHint: null,
        input: null,
        requiredFlag: null,
        itemsContent: null,
    };
    _setPropMap = {
        name: () => {
            if (this._props.name) {
                this._items.forEach((item) => {
                    item.setAttribute('name', this._props.name);
                });
            }
        },
        skin: () => {
            this._items.forEach((item) => {
                item.setAttribute('skin', this._props.skin);
            });
        },
        skinMode: (old)=>{
            if (old) dom.removeClass(this._content, old);
            if (this._props.skinMode) dom.addClass(this._content, this._props.skinMode);
        },
        disabled: () => {
            this._items.forEach((item) => {
                if (this._props.disabled) {
                    item.setAttribute('disabled', true);
                } else {
                    if (!item.disabled) {
                        item.removeAttribute('disabled');
                    }
                }
            });
            dom.toggleAttr(this._elements.input, 'disabled', this._props.disabled);
        },
        size: () => {
            if (this._props.size) {
                this._items.forEach((item) => {
                    item.setAttribute('size', this._props.size);
                });
            }
        },
        col: () => {
            this.rerender();
        },
        label: () => {
            if (this._props.label) {
                this._elements.labelText.textContent = this._props.label;
                dom.removeClass(this._elements.labelText, 'hide');
                dom.removeClass(this._elements.label.parentElement, 'hide');
            } else {
                this._elements.labelText.textContent = '';
                dom.addClass(this._elements.labelText, 'hide');
                dom.addClass(this._elements.label.parentElement, 'hide');
            }
        },
        labelStyle: () => {
            dom.toggleAttr(this._elements.labelText, 'style', this._props.labelStyle);
        },
        // labelAlign: ()=>{
        //     dom.checkClass(this._elements.labelText, 'flex-end', this._props.labelAlign==='right');
        // },
        labelAlign: () => {
            switch (this._props.labelAlign) {
                case 'right':
                    dom.setStyle(this._elements.labelText, 'justify-content', 'flex-end');
                    break;
                case 'center':
                    dom.setStyle(this._elements.labelText, 'justify-content', 'center');
                    break;
                default:
                    dom.setStyle(this._elements.labelText, 'justify-content', 'flex-start');
            }
        },
        viewStyle: () => {
            if (this._props.readmode) dom.toggleAttr(this._elements.itemsContent, 'style', this._props.viewStyle);
        },
        count: () => {
            this.rerender();
        },
        readmode: (old) => {
            this.rerender();
            dom.toggleAttr(this._elements.input, 'readonly', this._props.readmode);
            if (!this._props.readmode) {
                dom.toggleAttr(this._elements.itemsContent, 'style', '');
            } else {
                dom.toggleAttr(this._elements.itemsContent, 'style', this._props.viewStyle);
            }
            this._fillEmptyReadmode();
            this._setPropMap.required();
        },
        max: () => {
            this._props.max === 0 ? this._elements.input.removeAttribute('max') : this._elements.input.setAttribute('max', this._props.max);
        },
        maxlength: () => {
            this._props.maxlength === 0
                ? this._elements.input.removeAttribute('maxlength')
                : this._elements.input.setAttribute('maxlength', this._props.maxlength);
        },
        min: () => {
            this._props.min === 0 ? this._elements.input.removeAttribute('min') : this._elements.input.setAttribute('min', this._props.min);
        },
        minlength: () => {
            this._props.minlength === 0
                ? this._elements.input.removeAttribute('minlength')
                : this._elements.input.setAttribute('minlength', this._props.minlength);
        },
        pattern: () => {
            !this._props.pattern
                ? this._elements.input.removeAttribute('pattern')
                : this._elements.input.setAttribute('pattern', this._props.pattern);
        },
        required: () => {
            !this._props.required
                ? this._elements.input.removeAttribute('required')
                : this._elements.input.setAttribute('required', this._props.required);
            dom.checkClass(this._elements.group, 'required', this._props.required && !this._props.readmode && !this._props.disabled);
            dom.checkClass(this._elements.requiredFlag, 'hide', !this._props.required || this._props.readmode || this._props.disabled);
        },
        step: () => {
            this._props.step === 1
                ? this._elements.input.removeAttribute('step')
                : this._elements.input.setAttribute('step', this._props.step);
        },

        $default: () => {},
    };

    constructor() {
        super();
    }
    _connected() {
        this.#checkCount();
        this.#checkReadmode();
        this.#checkCol();
    }
    rerender() {
        this.#checkCount();
        this.#checkReadmode();
        this.#checkCol();
    }

    _init(name, clazz, ohtml, ocss) {
        this._initialize(name, clazz || $CheckGroup, ohtml || html, ocss || css);
    }

    #computeMaxWidth() {
        this.#maxWidth = 0;
        this._items.forEach((item) => {
            dom.setStyle(item, 'width', 'auto');
            const size = dom.getSize(item);
            if (size.x > this.#maxWidth) this.#maxWidth = size.x;
        });
    }

    _render() {
        if (!this._props.name) {
            this._props.name = uuid();
        }
        if (!parseInt(this._props.col)) dom.removeClass(this._elements.items, 'hide-items');
        if (!parseInt(this._props.label)) dom.addClass(this._elements.labelText, 'hide');

        this._itemChangeFun = this._itemChange.bind(this);
    }

    _removeItem(item) {
        this._items.splice(this._items.indexOf(item), 1);
        item.removeAttribute('slot');
        item.removeEventListener('change', this._itemChangeFun);
        dom.setStyle(item, 'width', 'auto');
        this.#checkCol();
    }

    _appendItem(item) {
        this._items.push(item);
        item.setAttribute('slot', 'items');
        item.setAttribute('name', this._props.name);
        item.setAttribute('skin', this._props.skin);
        item.setAttribute('skin-mode', this._props.skinMode);

        if (this._props.disabled) {
            item.setAttribute('disabled', true);
        } else {
            if (!item.disabled) {
                item.removeAttribute('disabled');
            }
        }
        if (this._props.size) item.setAttribute('size', this._props.size);

        item.addEventListener('change', this._itemChangeFun);

        this._appendItemCheckValue(item);

        this.#checkCol(true);

        if (this._props.count) {
            const valueLenght = this._props.values ? this._props.values.length : 0;
            if (valueLenght >= this._props.count && !item.checked) item.disabled = true;
        }
        if (this._props.readmode) item.readmode = true;

        // this._setPropMap.value();
    }

    _appendItemCheckValue(item) {
        if (this._props.values && this._props.values.length) {
            if (this._props.values.includes(item.value)) {
                item.checked = true;
            }
        }
    }

    #checkColTimer = null;

    #checkCol() {
        if (this.#checkColTimer) clearTimeout(this.#checkColTimer);
        this.#checkColTimer = setTimeout(() => {
            this.#col();
        }, 10);
    }

    #col() {
        if (!this._props.readmode) {
            this.#clearCol();
            const col = parseInt(this._props.col);
            if (col) {
                this.#computeMaxWidth();
                this._items.forEach((item, i) => {
                    if (this.#maxWidth) dom.setStyle(item, 'width', this.#maxWidth + 'px');
                    if ((i + 1) % col === 0) {
                        item.insertAdjacentElement('afterend', dom('br.group-col-separator', {slot: 'items'}));
                    }
                });
            }
        }
        dom.removeClass(this._elements.items, 'hide-items');
    }

    #clearCol() {
        const brs = this.querySelectorAll('br.group-col-separator');
        brs.forEach((br) => {
            br.remove();
        });
    }

    _itemChange(e) {
        if (e.currentTarget.checked) {
            if (!this._props.values) this._props.values = [];
            if (!this._props.values.includes(e.currentTarget.value)) this._props.values.push(e.currentTarget.value);
            if (!this._props.disabled) e.currentTarget.disabled = false;
        } else {
            if (this._props.values) erase(this._props.values, e.currentTarget.value);
        }

        this._elements.input.value = this._props.values.join(',');

        this.unInvalidStyle();
        if (this._props.validityBlur) {
            this.checkValidity();
        }
        this.#checkCount();
        // this.dispatchEvent(new Event('change', e));
    }
    #checkCount() {
        if (this._props.count) {
            const valueLenght = this._props.values ? this._props.values.length : 0;
            if (valueLenght >= this._props.count) {
                for (const item of this._items) {
                    if (!item.checked) item.disabled = true;
                }
            } else {
                for (const item of this._items) {
                    item.disabled = false;
                }
            }
        }
    }
    #checkReadmode() {
        if (!!this._props.readmode) {
            dom.addClass(this._elements.group, 'readmode');
            const brs = this.querySelectorAll('br');
            if (brs.length) {
                brs.forEach((br) => {
                    dom.addClass(br, 'hide');
                });
            }
            for (const item of this._items) {
                item.style.width = 'unset';
                item.readmode = !!this._props.readmode;
            }
        } else {
            dom.removeClass(this._elements.group, 'readmode');
            for (const item of this._items) {
                item.readmode = !!this._props.readmode;
            }
        }
    }

    _setEvent() {
        super._setEvent();

        this._elements.input.addEventListener('invalid', (e) => {
            this.#checkInvalid(e.target.validity);
        });
    }

    setCustomValidity() {
        return this._elements.input.setCustomValidity(...arguments);
    }
    checkValidity() {
        this.dispatchEvent(new CustomEvent('validity'));
        return this._elements.input.checkValidity(...arguments);
    }
    reportValidity() {
        this.dispatchEvent(new CustomEvent('validity'));
        return this._elements.input.reportValidity(...arguments);
    }

    #setValidationMessage() {
        if (!this._elements.input.validity.customError) {
            if (this._props.validity) this._elements.input.setCustomValidity(this._props.validity);
        }
        if (this._elements.invalidHint) this._elements.invalidHint.textContent = this._elements.input.validationMessage;
        return this._elements.input.validationMessage;
    }
    #checkInvalid(detail) {
        if (detail['valueMissing']) {
            lp = I18n.get('index').o('component');
            this._elements.input.setCustomValidity(lp.t('valueMissing', {label: this._props.label || lp['this']}));
        }
        this.dispatchEvent(new CustomEvent('invalid', {detail}));
        dom.addClass(this._elements.input, 'invalid');
        if (this.#setValidationMessage()) dom.addClass(this._elements.invalidHint, 'show');
    }
    unInvalidStyle() {
        this._elements.input.setCustomValidity('');
        dom.removeClass(this._elements.input, 'invalid');

        this._elements.invalidHint.textContent = '';
        dom.removeClass(this._elements.invalidHint, 'show');
    }

    _fillEmptyReadmode(){
        if (!this._elements.input.value && this._props.readmode){
            if (!this.emptyPlaceholderNode){
                this.emptyPlaceholderNode = dom('span.emptyPlaceholder');
                this.emptyPlaceholderNode.setAttribute('slot', 'items');
                this.emptyPlaceholderNode.textContent = '-';
            }
            this._elements.items.insertAdjacentElement('beforebegin', this.emptyPlaceholderNode);
        }else{
            if (this.emptyPlaceholderNode) this.emptyPlaceholderNode.remove();
        }
    }
}

export {$CheckGroup};
