import {OOComponent} from '../base.js';
import html from './template/switch.html?raw';
import css from './template/switch.scope.css?raw';
import {dom, hyphenate} from '@o2oa/util';

class OOSwitch extends OOComponent {
    static prop = {
        leftIcon: '',
        rightIcon: '',

        trueLocation: 'right',

        trueText: '',
        falseText: '',
        innerTrueText: '',
        innerFalseText: '',
        buttonTrueText: '',
        buttonFalseText: '',

        trueIcon: '',
        falseIcon: '',
        innerTrueIcon: '',
        innerFalseIcon: '',
        buttonTrueIcon: '',
        buttonFalseIcon: '',

        trueValue: '',
        falseValue: '',

        disabled: false,
        readonly: false,

        width: '',

        label: '',

        validity: '',   //表单验证提示信息
        validityBlur: false,    //失去焦点时是否进行有效性验证

        value: 'true',
        booleanValue: true,

        skin: ''
    };

    static get observedAttributes() {
        return Object.keys(this.prop).map((att) => {
            return hyphenate(att);
        });
    }

    _elements = {
        label: null,
        labelText: null,
        box: null,
        prefix: null,
        suffix: null,
        bar: null,
        barArea: null,
        button: null,
        buttonArea: null,
        invalidHint: null,

        prefixText: null,
        suffixText: null,

        barIconArea: null,
        barTextArea: null
    };

    _setIcon(node, old, icon, notHide) {
        dom.removeClass(node, `ooicon-${old}`);
        if (!!icon) {
            dom.addClass(node, `ooicon-${icon}`);
            dom.removeClass(node, 'hide');
        } else {
            if (!notHide) dom.addClass(node, 'hide');
        }
    }

    _getTrueLabelNode() {
        return this._props.trueLocation.toLowerCase() !== 'left' ? this._elements.suffixText : this._elements.prefixText;
    }

    _getFalseLabelNode() {
        return this._props.trueLocation.toLowerCase() !== 'left' ? this._elements.prefixText : this._elements.suffixText;
    }

    _setPropMap = {
        leftIcon: (old) => {
            this._setIcon(this._elements.prefix, old, this._props.leftIcon);
        },
        rightIcon: (old) => {
            this._setIcon(this._elements.suffix, old, this._props.rightIcon);
        },

        trueText: () => {
            const node = this._getTrueLabelNode();
            node.textContent = this._props.trueText;
            dom.checkClass(node, 'hide', !this._props.trueText);
        },
        falseText: () => {
            const node = this._getFalseLabelNode();
            node.textContent = this._props.falseText;
            dom.checkClass(node, 'hide', !this._props.trueText);
        },


        innerTrueText: () => {
            if (this.isTrue) {
                this._elements.barTextArea.textContent = this._props.innerTrueText;
            }
        },
        innerFalseText: () => {
            if (!this.isTrue) {
                this._elements.barTextArea.textContent = this._props.innerFalseText;
            }
        },
        buttonTrueText: () => {
            if (this.isTrue && !this._props.buttonTrueIcon) {
                this._elements.buttonArea.textContent = this._props.buttonTrueText.substring(0, 1);
            }
        },
        buttonFalseText: () => {
            if (!this.isTrue && !this._props.buttonFalseIcon) {
                this._elements.buttonArea.textContent = this._props.buttonFalseText.substring(0, 1);
            }
        },

        trueIcon: (old) => {
            this._setIcon(this.this._getTrueLabelNode(), old, this._props.trueIcon);
        },
        falseIcon: (old) => {
            this._setIcon(this._getFalseLabelNode(), old, this._props.falseIcon);
        },
        innerTrueIcon: (old) => {
            if (this.isTrue) {
                this._setIcon(this._elements.barIconArea, old, this._props.innerTrueIcon);
            }
        },
        innerFalseIcon: (old) => {
            if (!this.isTrue) {
                this._setIcon(this._elements.barIconArea, old, this._props.innerFalseIcon);
            }
        },

        buttonTrueIcon: (old) => {
            if (this.isTrue) {
                this._setIcon(this._elements.buttonArea, old, this._props.buttonTrueIcon, true);
            }
        },
        buttonFalseIcon: (old) => {
            if (!this.isTrue) {
                this._setIcon(this._elements.buttonArea, old, this._props.buttonFalseIcon, true);
            }
        },

        value: () => {
            this.value = this._props.value;
        },

        width: () => {
            if (this._props.width) {
                this._elements.bar.style.width = this._props.width;
            } else {
                this._elements.bar.style.width = 'unset';
            }
        },

        trueLocation: () => {
            const falseNode = this._getFalseLabelNode();
            const trueNode = this._getTrueLabelNode();
            dom.addClass(falseNode, 'falseLabel');
            dom.removeClass(falseNode, 'trueLabel');
            dom.addClass(trueNode, 'trueLabel');
            dom.removeClass(trueNode, 'falseLabel');

            dom.checkClass(this._elements.box, 'trueIsLeft', this._props.trueLocation.toLowerCase() === 'left');
        },


        disabled: () => {
            dom.checkClass(this._elements.box, 'disabled', this._props.disabled);
            dom.checkClass(this._elements.label, 'disabled', this._props.disabled);
        },
        readonly: () => {
            dom.checkClass(this._elements.box, 'readonly', this._props.readonly);
            dom.checkClass(this._elements.label, 'readonly', this._props.readonly);
        },


        label: () => {
            if (this._props.label) {
                this._elements.labelText.textContent = this._props.label;
                dom.removeClass(this._elements.labelText, 'hide');
                dom.removeClass(this._elements.label, 'hide');
            } else {
                this._elements.labelText.textContent = '';
                dom.addClass(this._elements.labelText, 'hide');
                dom.removeClass(this._elements.label, 'hide');
            }
        },
        skin: () => {
            this._useSkin(this._props.skin);
        },

        validity: () => {},

        $default: (key) => {}
    };

    constructor() {
        super();
        this._init('oo-switch');
    }

    _init(name, clazz, ohtml, ocss) {
        this._initialize(name, clazz || OOSwitch, ohtml || html, ocss || css);
    }

    _render() {
        super._render();
        this._setPropMap.value();
        if (this._props.label) this._setPropMap.label();
        this._setPropMap.trueLocation();
    }

    _setEvent() {
        super._setEvent();
        this._elements.bar.addEventListener('click', (e) => {
            if (!this._props.disabled && !this._props.readonly){
                this.value = this.#getReverseValue();
                this.dispatchEvent(new Event("change", e));
            }
        });
    }

    #getReverseValue() {
        if (this._props.trueValue && this._props.falseValue) {
            return this._props.booleanValue ? this._props.falseValue : this._props.trueValue;
        } else {
            return !this._props.booleanValue;
        }
    }

    set value(value) {
        if (this._props.trueValue && this._props.falseValue) {
            this._props.booleanValue = (value === this._props.trueValue);
        } else {
            this._props.booleanValue = !(!value || value === 'false');
        }
        dom.checkClass(this._elements.box, 'false', !this._props.booleanValue);

        [
            'innerTrueText',
            'innerFalseText',
            'buttonTrueText',
            'buttonFalseText',
            'innerTrueIcon',
            'innerFalseIcon',
            'buttonTrueIcon',
            'buttonFalseIcon'
        ].forEach((k) => {
            this._setPropMap[k]?.();
        });
    }

    get value() {
        if (this._props.trueValue && this._props.falseValue) {
            return this._props.booleanValue ? this._props.trueValue : this._props.falseValue;
        } else {
            return this._props.booleanValue;
        }
    }

    get isTrue() {
        return this._props.booleanValue;
    }
}

export default OOSwitch;
