import {dom, typeOf} from '@o2oa/util';
import {$Input} from './$input.js';

const html=`<div><oo-select option-width="auto" class="selectNode" style="width:100%"></oo-select>`
export default class OOCascade extends $Input{
    constructor() {
        super();
        this._init('oo-cascade', OOCascade, html, '');
    }
    _elements={
        selectNode: null
    }
    _setPropMap={
        $default: (key)=>{
            if (key!=='style'){
                dom.toggleAttr(this._elements.selectNode, key, this._props[key]);
            }  
        }
    };
    _render(){
        // this.selectNode = this.shadowRoot.querySelector('oo-select');
        const attrs = this.getAttributeNames();
        if (this._elements.selectNode){
            attrs.forEach((attr)=>{
                this._elements.selectNode.setAttribute(attr, this.getAttribute(attr));
            })
        }
    }
    /** 
     * data=[
     *  {
     *      text: "选项1",
     *      value: "value1",
     *      type: "group",
     *      children: [
     *          {
     *              text: "子选项",
     *              value: "subvalue1"
     *          }
     *      ]
     *  },
     *  {
     *      text: "选项1",
     *      value: "value1"
     *      children: ()=>{
     *          return Promise()
     *      }
     *  }
     * ]
    */
    setOption(data){
        this.#setOptionItem(data, this._elements.selectNode);
    }
    #loadSubOptionFun = null;
    #setOptionItem(data, node){
        if (data){
            if (typeOf(data)==='function'){
                const option = dom('oo-option');
                option.setAttribute('icon', 'loading');
                node.appendChild(option);

                this.#loadSubOptionFun = async ()=>{
                    const items = await data();
                    dom.empty(node);
                    this.#createSubOptions(items, node);
                    node.removeEventListener('show', this.#loadSubOptionFun);
                }
                node.addEventListener('show', this.#loadSubOptionFun);
            }else{
                this.#createSubOptions(data, node)
            }
            
        }
    }

    #createSubOptions(items, node){
        if (items && items.length){
            items.forEach((item)=>{
                const {text, label, icon, value, type, children} = item;
                const tag = type==='group' ? 'oo-option-group' : 'oo-option';
                const option = dom(tag);
                option.setAttribute('text', text || label);
                option.setAttribute('value', value || text);
                if (icon) option.setAttribute('icon', icon);
                node.appendChild(option);
                if (children) this.#setOptionItem(children, option);
            });
        }
    }

    async #getItemData(data){
        return (typeOf(data)==='function') ? (await data()) : data;
    }

    _setEvent(){}
    _connected(){}
}
