import {exec} from '@o2oa/util';
import lp from './zh-cn.json';
window.oo = window.oo || {
    I18n: {
        instances: {}
    }
}
/**国际化语言包处理类
 * @object I18n
 */
class I18n {
    /**当前系统支持的语言
     * @property supportedLanguages
     * @static
     */
    static supportedLanguages = ['zh-cn', 'en'];
    static defaultLng = 'zh-cn';

    /**检测当前语言环境
     * @method detect
     * @static
     * @return {String} 语言类型
     */
    static detect(){
        const params = (new URL(window.location)).searchParams;
        const lng = (params.get('lng') || params.get('lp') || localStorage.getItem('o2.language') || navigator.language || I18n.defaultLng).toLocaleLowerCase();
        if (I18n.supportedLanguages.includes(lng)) return lng;
        return I18n.supportedLanguages.find((l)=>{
            return lng.substring(0, lng.indexOf('-')) === l;
        }) || I18n.defaultLng;
    }
    static lng = I18n.detect();
    static instances = {};

    /**获取或创建指定名称的语言包
     * @method get
     * @static
     * @param {String} name （可选）语言包名称
     * @param {Object} defaultRes （可选） 默认语言包数据
     * @return {I18n} 语言包对象
     */
    static get = (name = 'index', defaultRes= {})=>{
        return window.oo.I18n.instances[name] || (window.oo.I18n.instances[name]=new I18n(name, defaultRes));
    }

    res = null;
    defaultRes = {}

    /** 国际化语言包
     * @method constructor
     * @param {String} name 语言包名称
     * @param {Object} defaultRes （可选） 默认语言包数据
     */
    constructor(name, defaultRes = {}){
        this.name = name;
        this.defaultRes = defaultRes
        if (name) window.oo.I18n.instances[name] = this;
    }

    /** 载入语言包数据
     * @method get
     * @return {I18n} 语言包对象
     */
    async load() {
        // if (this.name){
        //     if (this.name === 'index'){
        //         const res = await action.getJson(`/locales/${I18n.lng}.json`);
        //         this.res = Object.assign(this.res || {}, res);
        //     }
        // }
        return this;
    }

    /** 获取语言包文本
     * @method constructor
     * @param {String} key 语言包key
     * @param {Object} options （可选）语言信息插值对象
     * @return {String} 文本信息
     */
    t(key, options){
        return text(key, options, this.res||this.defaultRes);
    }

    /** 获取语言包对象
     * @method constructor
     * @param {String} key 语言包key
     * @return {I18n} 语言包对象
     */
    o(key){
        return obj(key, this.res||this.defaultRes)
    }
}

function text(key, options, res){
    let v = (exec(`return this.${key}`, res) || key).toString();
    if (options){
        Object.keys(options).forEach((n)=>{
            const reg = new RegExp(`\{${n}\}`, 'g');
            v = v.replace(reg, options[n]);
        })
    }
    return v;
}
function obj(key, res){
    const ob = (key) ? (exec(`return this.${key}`, res) || {}) : res;

    const t = {
        get() {
            return function(k, options){
                return text(k, options, this);
            };
        },
        enumerable : true,
        configurable : true
    };
    const o = {
        get() {
            return function(k){
                return obj(k, this);
            };
        },
        enumerable : true,
        configurable : true
    };

    Object.defineProperties(ob, {t, o});

    return ob;
}

async function loadAppLp(name, lps = {}) {
    const loadLp = lps?.[`./${I18n.lng}.json`];
    if (loadLp) {
        const json = await loadLp();
        return I18n.get(name, json.default).o();
    }
    return '';
}

I18n.get('index', lp);

export {I18n, loadAppLp};
