import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from './template/capsulae.scope.css?raw';

const html = `<div class="capsulae content"></div>`;
export default class OOCapsulae extends OOComponent{
    static prop = {
        skin: ''
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _setPropMap={
        skin: ()=>{
            this._useSkin(this._props.skin);
        }
    };
    constructor() {
        super();
        this._init('oo-capsulae');
    }
    _init(name, clazz, ohtml, ocss){
        this._initialize(name,clazz||OOCapsulae, ohtml||html, ocss||css);
    }
    _render(){
        const _capsulae = this;
        const observer = new MutationObserver(function(recordList){
            recordList.forEach((r)=>{
                r.addedNodes.forEach((n)=>{
                    if (n.nodeType===Node.ELEMENT_NODE){
                        this._content.appendChild(n);
                    }
                });
            });
        });
        observer.observe(this, {
            subtree: false,
            childList: true,
            attributes: false,
            characterData:false
        });
        this.#setContent();
    }
    #setContent(){
        let node = this.firstElementChild;
        while (node){
            this._content.appendChild(node)
            node = this.firstElementChild;
        }
    }
}
