import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from './base.js';
import css from "./$panel.scope.css?raw";

const html = `
<div class="content">
    <slot name="items"></slot>
</div>
`
class OO$Panel extends OOComponent{
    static prop = {
        css: '',
        cssLink: '',
        skin: '',
        isShow: false
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _setPropMap={
        css: ()=>{
            this.useCss(this._props.css);
        },
        cssLink: ()=>{
            this.useCssLink(this._props.cssLink);
        },
        skin: ()=>{
            this._useSkin(this._props.skin);
        },
        isShow: ()=>{
           if (this._props.isShow)window.setTimeout(()=>{this.show()}, 10);
        }
    }
    constructor() {
        super();
    }
    #from=null;
    #to = null;
    #hideFun = null;

    _init(name, clazz, ohtml, ocss){
        this._initialize(name,clazz||OO$Panel, ohtml||html, ocss||css);
    }
    _setEvent() {
        super._setEvent();
        this.shadowRoot.addEventListener('mousedown', (e)=>{
            e.stopPropagation();
        });
    }
    show(from, to){
        if (!this.#from && !this.#to){
            this.#from = Object.assign({}, {scale: 0}, from);
            this.#to = Object.assign({}, {scale: 1}, to);

            dom.setStyles(this, {scale: 0});
            dom.addClass(this, 'show');
            window.setTimeout(()=>{
                dom.addClass(this, 'transition');
                dom.setStyles(this, {scale: 1});
            }, 10);
            window.setTimeout(()=>{
                this.dispatchEvent(new Event('show'));
            }, 210);
            this._addHideEvent();

            this.isHide = false;
        }
    }
    _addHideEvent(){
        if (!this.#hideFun){
            this.#hideFun = ()=>{this.hide();}
        }
        document.addEventListener('mousedown', this.#hideFun);
    }
    hide(){
        if (this.#from && this.#to && !this.isHide){
            dom.setStyles(this, this.#from);
            window.setTimeout(()=>{
                dom.removeClass(this, 'transition');
                dom.removeClass(this, 'show');
                dom.setStyles(this, this.#to ?? {});
                this.#to = null;
                this.#from = null;
                this._afterHide();
                this.dispatchEvent(new Event('hide'));
            }, 200);
            this._removeHideEvent();

            this.isHide = true;
        }
    }
    _afterHide(){}
    _removeHideEvent(){
        if (this.#hideFun){
            document.removeEventListener('mousedown', this.#hideFun);
        }
    }

}
export {OO$Panel}
