import {
    dom,
    hyphenate,
    typeOf,
    formatCurrency,
    unformatCurrency,
    CurrencyPreset,
    CurrencyProp,
    camelCase
} from '@o2oa/util';
import {$Input} from './$input.js';


function setCursor(el, position) {
    const setSelectionRange = () => {
        el.setSelectionRange(position, position);
    };
    setSelectionRange();
    setTimeout(setSelectionRange, 1); // Android Fix
}

// function fixed(precision) {
//     return Math.max(0, Math.min(precision, 1000));
// }

export default class OOCurrency extends $Input {
    // {
    //     prefix: '¥', //前缀，如 US$ 1234.23
    //     suffix: '', //后缀，如 #
    //     thousands: ',', //千分位符
    //     decimal: '.', //小数点符号
    //     precision: 2, //小数点后保留几位
    //     allowblank: true, //是否允许空白
    //     disablenegative: true, //是否允许输入负数
    //     maximum: 9007199254740991, //最大值，默认值为js最大安全整数
    //     minimum: -9007199254740991, //最小值，默认值为js最小安全整数
    //     round: true, //是否四舍五入
    // };
    #currenCurrency = null;
    static currencyProp = {
        currency: 'CNY',
        prefixuse: 'symbol', //当有currency时，前缀使用预设的iso还是符号
        // symbol: '¥', //货币符号
        ...CurrencyProp
    };
    static preset = CurrencyPreset;
    static formatCurrency = formatCurrency;
    static unformatCurrency = unformatCurrency;
    static _name = 'OOCurrency';
    static get observedAttributes() {
        OOCurrency.prop = Object.assign({}, $Input.prop, OOCurrency.currencyProp);
        return Object.keys(OOCurrency.prop).map((att)=>{
            return hyphenate(att);
        });
    }

    attributeChangedCallback(name, oldValue, newValue) {
        if (!/{{.*}}/g.test(newValue)){
            const key = camelCase(name);
            const old = this._props[key];
            this.#setProps(this._props, key, newValue);
            this.#renderProps(key, old, oldValue);
        }
    }

    #renderProps(name, old, oldValue){
        const props = (name) ? [name] : Object.keys(this._props);
        props.forEach((k)=> {
            this._setProps(k, old, oldValue);
        });
    }



    #setProps(props, name, value){
        switch (typeOf(props[name])){
            case 'boolean':
                props[name] = (!!value && value!=='false');
                break;
            case 'number':
                props[name] = (!isNaN(value)) ? parseFloat(value) : value;
                break;
            case 'object':
                try{
                    props[name] = JSON.parse(value);
                }catch(e){
                    props[name] = value;
                }
                break;
            case 'array':
                try{
                    props[name] = JSON.parse(value);
                }catch(e){
                    props[name] = value ? value.split(/\s*,\s*/g) : [];
                }
                break;
            default:
                props[name] = value;
        }
    }


    constructor() {
        super();
        this.#setPropMap();
        this._init('oo-currency', OOCurrency);
        //this._useCss(css);
    }

    #moneyProp = {};
    #oldText = null;

    #initCurrencyProp(){
        this.#moneyProp = {};
        for( var key in OOCurrency.currencyProp){
            if (this._props.hasOwnProperty(key)) {
                this.#moneyProp[key] = this._props[key];
            }
        }
    }

    #setPropMap() {
        this._setPropMap = Object.assign(this._setPropMap, {
            disabled: () => {
                dom.toggleAttr(this._elements.input, 'disabled', this._props.disabled);
                dom.checkClass(this._elements.box, 'disabled', this._props.disabled);
                dom.checkClass(this._elements.label, 'disabled', this._props.disabled);
                dom.checkClass(this._elements.suffix, 'disabled', this._props.disabled);
            },
            value: ()=>{
                this._setText();
                this._props.value = unformatCurrency(this._props.text, this.#moneyProp, '');
            },
            text: ()=>{
                this._props.value = unformatCurrency(this._props.text, this.#moneyProp, '');
                this._setText();
            },
            readmode: ()=>{
                dom.toggleAttr(this._elements.input, 'readonly', this._props.readmode);
                dom.checkClass(this._elements.box, 'readmode', this._props.readmode);
                dom.checkClass(this._elements.label, 'readmode', this._props.readmode);
            },
            prefixuse: ()=>{
                this.#usePreset();
            },
            currency: ()=>{
                this.#usePreset();
            },
            $default: (key) => {
                if (OOCurrency.currencyProp.hasOwnProperty(key)) {
                    this.#moneyProp[key] = this._props[key];
                    if( this._props.value ){
                        switch (key){
                            case 'prefix':
                            case 'suffix':
                            case 'thousands':
                            case 'decimal':
                            case 'precision':
                                this._setText();
                                break;
                            case 'maximum':
                                (this._props.maximum < this._props.value) && this._setPropMap.value();
                                break;
                            case 'minimum':
                                (this._props.minimum > this._props.value) && this._setPropMap.value();
                                break;
                        }
                    }else if(key === 'allowblank'){
                        this._setText();
                    }
                } else {
                    dom.toggleAttr(this._elements.input, key, this._props[key]);
                }
            }
        });
    }

    #usePreset(){
        let changed = false;
        const { currency, prefixuse } = this._props;
        if( !!currency && this.#currenCurrency !== currency ){
            if( !CurrencyPreset[currency] ){
                throw new Error('This currency has no preset: ' + key + '')
            }else{
                changed = true;
                this.#currenCurrency = currency;
                Object.assign(this.#moneyProp, CurrencyPreset[currency]);
            }
        }

        if( !!currency && !!prefixuse ){
            if( ['symbol', 'iso'].includes(prefixuse) ){
                const prefix = CurrencyPreset[currency][prefixuse];
                if( prefix !== this.#moneyProp.prefix ){
                    changed = true;
                    this.#moneyProp.prefix = prefix;
                }
            }else{
                throw new Error('prefixUse must be "symbol" or "iso"');
            }
        }

        changed && this._setText();
    }

    _render() {
        this.#initCurrencyProp();
    }

    _dispatchChangeEvent() {
        this.dispatchEvent(new CustomEvent('change', {
            bubbles: true,
            composed: true,
            detail: {
                value: this._props.value,
                text: this._props.text,
            }
        }));
    }

    _setText(){
        const formatted = formatCurrency(this._props.value, this.#moneyProp, '');
        this._elements.input.value = formatted;
        this._elements.viewText.textContent = formatted;
        this._props.text = formatted;
    }

    _inputValue( text ) {
        if( this.#oldText === text ){
            return;
        }
        const moneyProp = { ...this.#moneyProp, precision : 'auto'};

        const {suffix, decimal} = moneyProp;
        const endsWithDcimal = !!decimal && text.endsWith(decimal+(suffix||''));
        const value = unformatCurrency(text, moneyProp, '', true) + (endsWithDcimal ? decimal : '');

        const formatted = formatCurrency(value, moneyProp, '');

        this._elements.input.value = formatted;
        this._elements.viewText.textContent = formatted;

        this._props.text = formatCurrency(value, this.#moneyProp, '');
        this._props.value = unformatCurrency(this._props.text, this.#moneyProp, '');

        this.#oldText = formatted;
        //this.#oldValue = this._props.value;
    }

    _input(e, forceSelectionEnd, decimalPressed){
        if (!this._props.read && !this._props.readmode && !this._props.disabled){
            const {suffix, decimal, prefix} = this.#moneyProp;

            const el = e.currentTarget;
            const value = el.value;

            const selectionEnd = forceSelectionEnd || el.selectionEnd;

            let positionFromEnd;
            if( value.length === 1 ){
                positionFromEnd = suffix.length;
            }else{
                positionFromEnd = value.length - (selectionEnd || 0);
            }

            this._inputValue(el.value);

            if( decimalPressed ){
                setCursor(el, el.value.indexOf(decimal)+1);
            }else{
                positionFromEnd = Math.max(positionFromEnd, suffix.length); // right
                positionFromEnd = el.value.length - positionFromEnd;
                positionFromEnd = Math.max(positionFromEnd, prefix.length); // left
                setCursor(el, positionFromEnd);
            }

            this.unInvalidStyle();
        }
    }

    _setEvent() {
        super._setEvent();

        let forceSelectionEnd;
        let decimalPressed = false; //小数点按键被按下

        this._elements.input.addEventListener('blur', (e) => {
            this._setText();
        })

        let isComposing = false; // 标记是否处于输入法输入中
        //输入法开始输入事件
        this._elements.input.addEventListener('compositionstart', () => { isComposing = true; });
        //输入法结束输入事件
        this._elements.input.addEventListener('compositionend', (e) => {
            isComposing = false;
            this._input( e, forceSelectionEnd, decimalPressed );
        });
        //常规的input事件
        this._elements.input.addEventListener('input', (e) => {
            !isComposing && this._input( e, forceSelectionEnd, decimalPressed );
        });

        // this._elements.input.addEventListener('input', (e) => {
        //     console.log('input', e.currentTarget.value, e);
        //     if (!this._props.read && !this._props.readmode && !this._props.disabled){
        //         const {suffix, decimal, prefix} = this.#moneyProp;
        //
        //         const el = e.currentTarget;
        //         const value = el.value;
        //
        //         const selectionEnd = forceSelectionEnd || el.selectionEnd;
        //
        //         let positionFromEnd;
        //         if( value.length === 1 ){
        //             positionFromEnd = suffix.length;
        //         }else{
        //             positionFromEnd = value.length - (selectionEnd || 0);
        //         }
        //
        //         this._inputValue(el.value);
        //
        //         if( decimalPressed ){
        //             setCursor(el, el.value.indexOf(decimal)+1);
        //         }else{
        //             positionFromEnd = Math.max(positionFromEnd, suffix.length); // right
        //             positionFromEnd = el.value.length - positionFromEnd;
        //             positionFromEnd = Math.max(positionFromEnd, prefix.length); // left
        //             setCursor(el, positionFromEnd);
        //         }
        //
        //         this.unInvalidStyle();
        //     }
        // });

        this._elements.input.addEventListener('keydown', (e) => {
            const {thousands, decimal} = this.#moneyProp;
            const el = e.currentTarget;
            let value = el.value

            let selectionEnd = el.selectionEnd;

            forceSelectionEnd = null;
            decimalPressed = e.key === decimal;

            const backspacePressed = e.code === 'Backspace';
            const isAtEndPosition = (value.length - (selectionEnd || 0)) === 0;

            if (backspacePressed){
                //删除的位置在千分位上
                const lastChar = value[selectionEnd-1];
                if( lastChar && lastChar === thousands ){
                    forceSelectionEnd = selectionEnd - 2;
                }
            }

            let number = unformatCurrency(value, this.#moneyProp, '');
            if (this._props.allowblank
                && backspacePressed
                && isAtEndPosition
                && number === 0
            ) {
                e.value = '';
                // this.value = '';
                // this._dispatchChangeEvent();
            }

            if (e.key === '+') {
                (typeof number === 'string') && (number = parseFloat(number))
                (number < 0) && (el.value = String(number * -1))
            }
        })
    }

    _connected(){
        if (!this.hasAttribute('right-icon')){
            this.setAttribute('right-icon', 'currency');
        }
    }
    // get preset(){
    //     return {...CurrencyPreset};
    // }
    get value(){
        return this._props.value;
    }
    set value(v){
        this.setAttribute('value', v);
    }
    get text(){
        return this._props.text;
    }
    set text(v){
        this._props.text = v;
    }
}
