import {typeOf} from "./common.js";

export const TimeUnits = {
    ms: 1,
    second: 1000,
    minute: 60000,
    hour: 3600000,
    day: 86400000,
    week: 608400000
}

export function cloneDate(date){
    return new Date(date.getTime());
}
function parseDate(date, isClone){
    if( !date )return null;
    return (date instanceof Date) ? (isClone ? cloneDate(date) : date) : new Date(date);
}
export function lastDayOfMonth(year, month){
    const date = new Date(year, month);
    date.setDate(0);
    return date;
}
export function lastTimeOfMonth(year, month){
    const date = new Date(parseInt(year), parseInt(month));
    date.setDate(0);
    return lastTimeOfDate(date);
}

export function lastTimeOfDate(date, isClone){
    if( !date )return null;
    const d = parseDate(date, isClone);
    d.setHours(23);
    d.setMinutes(59);
    d.setSeconds(59);
    d.setMilliseconds(999);
    return d;
}

export function clearTime(date, isClone){
    if( !date )return null;
    const d = parseDate(date, isClone);
    d.setHours(0);
    d.setMinutes(0);
    d.setSeconds(0);
    d.setMilliseconds(0);
    return d;
}

export function firstDayOfWeek(date, weekBegin) {
    if( !date )return null;
    const d = clearTime(parseDate(date, true));
    const dayOfWeek = d.getDay();  // 0表示星期日，1表示星期一，..., 6表示星期六
    const firstDayOfWeek = cloneDate(d);
    firstDayOfWeek.setDate(d.getDate() - dayOfWeek + (weekBegin || 0));
    return firstDayOfWeek;
}
export function lastDayOfWeek(date, weekBegin) {
    if( !date )return null;
    const d = clearTime(parseDate(date, true));
    const dayOfWeek = d.getDay();  // 0表示星期日，1表示星期一，..., 6表示星期六
    const lastDayOfWeek = cloneDate(d);
    const daysUntilSaturday = 6 - dayOfWeek;  // 从当前日期到星期六的天数
    lastDayOfWeek.setDate(d.getDate() + daysUntilSaturday + (weekBegin || 0));
    return lastDayOfWeek;
}
export function weekNumberOfDate( date, weekBegin ){
    const target  = cloneDate( date );
    const dayNumber   =  ( 7 + date.getDay() - parseInt( (weekBegin || 0) ) ) % 7;
    target.setDate(target.getDate() - dayNumber + 3);
    const firstThursday = target.valueOf();
    target.setMonth(0, 1);
    if (target.getDay() !== 4) {
        target.setMonth(0, 1 + ((4 - target.getDay()) + 7) % 7);
    }
    return 1 + Math.ceil((firstThursday - target) / TimeUnits.week); // 604800000 = 7 * 24 * 3600 * 1000
}

export function increment(date, interval = 'day', times = 1){
    switch (interval){
        case 'year':
            return increment(date,'month', times * 12);
        case 'month':
            const d = parseDate( date );
            const day = d.getDate();
            d.setMonth(d.getMonth() + times + 1, 0);
            if (day < d.getDate()){
                date.setDate(day);
            }
            return d;
        case 'week':
            return increment(date,'day', times * 7);
        case 'date':
        case 'day':
            date = parseDate( date );
            date.setDate( date.getDate() + times);
            return date;
    }

    if (!TimeUnits[interval]) throw new Error(interval + ' is not a supported interval');

    return date.setTime( this.getTime() + times * TimeUnits[interval]);
}


/** 将日期时间格式化为指定的格式，并返回格式化后的字符串
 * @param {Date} date 需格式化日期
 * @param {String} format 格式化字符串，如 YYYY-MM-DD HH:mm:ss
 * Y - 年份
 * M - 月份
 * D - 日期
 * H - 小时（24 小时制）
 * h - 小时（12 小时制）
 * m - 分
 * s - 秒
 * q - 季度
 * S - 毫秒
 * a - 上午/下午
 * A - AM/PM
 * d - 周几
 * W - 一年中的第几周
 * @example
 * const date = new Date();
 * formatDate(date, 'YYYY-MM-DD HH:mm:ss'); // 2023-02-16 08:25:05
 * formatDate(date, 'YYYY年MM月DD日 HH:mm:ss'); // 2023年02月16日 08:25:05
 * formatDate(date, 'YYYY-MM-DD HH:mm:ss S'); // 2023-02-16 08:25:05 950
 * formatDate(date, 'YYYY-MM-DD HH:mm:ss A'); // 2023-02-16 08:25:05 上午
 */
export function formatDate(date, format) {
    const o = {
        'M+': date.getMonth() + 1, // 月份
        'D+': date.getDate(), // 日
        'h+': date.getHours() % 12 === 0 ? 12 : date.getHours() % 12, // 小时
        'H+': date.getHours(), // 小时
        'm+': date.getMinutes(), // 分
        's+': date.getSeconds(), // 秒
        'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
        d: date.getDay(),
        S: date.getMilliseconds(), // 毫秒
        a: date.getHours() < 12 ? '上午' : '下午', // 上午/下午
        A: date.getHours() < 12 ? 'AM' : 'PM', // AM/PM
    };
    if (/(Y+)/.test(format)) {
        format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
    }
    if (/(W+)/.test(format)) {
        o['W+'] = weekNumberOfDate(date);
    }
    for (let k in o) {
        if (new RegExp('(' + k + ')').test(format)) {
            format = format.replace(
                RegExp.$1,
                RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length)
            );
        }
    }
    return format;
}


export function isIntersect( rangeA, rangeB, ignoreBoundary ){
    if( ignoreBoundary ){
        return (rangeA[0] < rangeB[0] && rangeB[0] < rangeA[1]) || // b starts in a
            (rangeA[0] <  rangeB[1] && rangeB[1]  <  rangeA[1]) || // b ends in a
            (rangeB[0] <= rangeA[0] && rangeA[1]  <= rangeB[1]); // a in b
    }else{
        return (rangeA[0] <= rangeB[0] && rangeB[0] <= rangeA[1]) || // b starts in a
            (rangeA[0] <= rangeB[1] && rangeB[1]   <= rangeA[1]) || // b ends in a
            (rangeB[0] <  rangeA[0] && rangeA[1]   <  rangeB[1]); // a in b
    }
}
export function isMultipleIntersect(...args) {
    const ignoreBoundary = (typeof args[args.length-1]) === 'boolean' ? !!args.pop() : false;
    for (let i = 0; i < args.length - 1; i += 1) {
        for (let j = i + 1; j < args.length; j += 1) {
            if ( isIntersect( args[i], args[j], ignoreBoundary)) return true;
        }
    }
    return false;
}
