import {$Button} from "./$Button.js";
import html from './template/tag.html?raw';
import css from './template/tag.scope.css?raw';
import {dom} from '@o2oa/util';

export default class OOTag extends $Button{
    static prop = {
        leftIcon: '',
        rightIcon: '',
        style: '',
        disabled: false,
        text: '',
        type: 'default',    //default or current
        close: 'on',
        menu: 'off',
        skin: ''
    };
    static events = {
        close: new Event("close", {composed: true}),
        menu: new Event("menu", {composed: true})
    }
    constructor() {
        super();
        this.#setPropMap();
        this._init('oo-tag', OOTag, html, css);
    }
    _elements={
        text: null,
        prefix : null,
        suffix: null,
        button: null,
        close: null,
        menu: null
    };
    #setPropMap(){
        this.setPropMap = Object.assign(this._setPropMap, {
            close: ()=>{
                if (this._props.close==='on'){
                    dom.removeClass(this._elements.close, 'hide');
                }else{
                    dom.addClass(this._elements.close, 'hide');

                }
            },
            menu: ()=>{
                if (this._props.menu==='on'){
                    dom.removeClass(this._elements.menu, 'hide');
                }else{
                    dom.addClass(this._elements.menu, 'hide');
                }
            },
            type: (old)=>{
                if (!this._props.type) this._props.type = "default";
                if (old) dom.removeClass(this._elements.button, old);
                dom.addClass(this._elements.button, this._props.type);
            }
        });
    }
    _setEvent(){
        if (this._elements.close){
            this._elements.close.addEventListener('click', (e)=>{
                this.shadowRoot.dispatchEvent(new MouseEvent("close", e));
                // this.remove();
                e.stopPropagation();
            })
        }
        if (this._elements.menu){
            this._elements.menu.addEventListener('click', (e)=>{
                this.shadowRoot.dispatchEvent(new MouseEvent("menu", e));
                e.stopPropagation();
            })
        }
    }
}
