import {dom, typeOf} from '@o2oa/util'

export class Draggable{
    #dragReady = null;
    #dragFun = null;
    #dragCheckFun = null;
    #dragEndFun = null;
    #dragCancelFun = null;
    #dragStartFun = null;
    #events = {};

    // oprions: {
    //      snap: 6, //拖动触发的鼠标移动距离
    //      dragNode: 'clone', //拖动对象或产生拖动对象的方法。
    //                      //可传入方法，function(thisDrag)，返回dom对象
    //                      //可传入dom对象
    //                      //可传入一个字符串：'none'：表示不产生dragNode,直接拖动node对象；'clone':克隆一个node对象（这是默认的）；'deepClone'：深度克隆node对象
    //      dropMark: ''    //当拖动到可放置对象时，标记drop对象。如果不需要标记，传入字符串 'none'
    //                      //可传入一个json对象，用于设置css。
    //                      //可传入方法，function(thisDrag)，返回dom对象
    //                      //可传入dom对象
    //      markContainer: null //标记drop对象 的容器，默认为应用的容器。
    // }
    constructor(node, options={}) {
        this.node = dom.el(node);
        this.options = options
        this.start = {x: 0, y: 0};
        this.container = dom.getPositionParent(this.node);

        this.#dragReady = this.#dragReady || ((e)=>{ this.dragReady(e); });
        this.node.addEventListener('mousedown', this.#dragReady);

        //拖动尚未开始，检查鼠标按下后，是否移动了足够距离来触发拖动
        this.#dragCheckFun = this.#dragCheckFun || ((e)=>{ this.dragChecK(e); });

        //在鼠标左键按下状态已经移动了足够距离，触发拖动事件开始
        this.#dragStartFun = this.#dragCheckFun || ((e)=>{ this.dragstart(e); });

        //目标在拖动中
        this.#dragFun = this.#dragFun || ((e)=>{ this.drag(e); });

        //拖动结束，（松开鼠标左键）
        this.#dragEndFun = this.#dragEndFun || ((e)=>{ this.dragend(e); });

        //拖动被取消，在鼠标左键被按下，但鼠标移动没有足够距离时就已经松开鼠标左键
        this.#dragCancelFun = this.#dragCancelFun || ((e)=>{ this.dragcancel(e); });


        //node对象触发事件
        //dragready 鼠标左键被按下时触发一次
        //dragchecK 当鼠标左键被按下后，移动鼠标，但移动距离尚未触发拖动时 触发
        //dragcancel 在鼠标左键被按下，但鼠标移动没有足够距离时就已经松开时触发一次
        //dragstart 在鼠标左键按下状态已经移动了足够距离，开始拖动时触发一次
        //dragmove 当对象被拖动时触发
        //dragend 当对象被拖动结束时触发一次
    }
    setOptions(opts={}){
        Object.assign(this.options, opts);
    }

    addEventListener(name, listener){
        if (!this.#events[name]) this.#events[name] = [];
        this.#events[name].push(listener);
    }
    removeEventListener(name, listener){
        if (this.#events[name]){
            if (listener){
                const i = this.#events[name].indexOf(listener);
                this.#events[name].splice(i, 1);
            }else{
                this.#events[name] = [];
            }
        }
    }
    dispatchEvent(name, e){
        if (this.#events[name]){
            this.#events[name].forEach((ev)=>{
                ev.apply(this, [e, this]);
            });
        }
    }

    dragReady(e){
        if (e.buttons===1){
            const {clientX:x, clientY:y} = e;
            this.start = {x, y};

            this.container.addEventListener('mousemove', this.#dragCheckFun);
            this.container.addEventListener('mouseup', this.#dragCancelFun);

            this.dispatchEvent('dragready', e);
        }
    }

    dragChecK(e){
        if (e.buttons===1){
            if (!e.clientX || !e.clientY) return;
            const {clientX:x, clientY:y} = e;
            const snap = this.options.snap || 6;

            if (Math.abs(this.start.x-x)>snap || Math.abs(this.start.y-y)>snap){
                this.dragCheckDetermined(e);
            }else{
                this.dispatchEvent('dragchecK', e);
            }

        }else{
            this.dragcancel();
        }
    }
    dragCheckDetermined(e){
        this.container.removeEventListener('mousemove', this.#dragCheckFun);
        this.container.addEventListener('mousemove', this.#dragFun);
        this.container.removeEventListener('mouseup', this.#dragCancelFun);
        this.container.addEventListener('mouseup', this.#dragEndFun);

        this.dragstart(e);
    }

    #craeteDragNode(){
        if (!this.options.dragNode) this.options.dragNode = 'clone';
        const type = typeOf(this.options.dragNode);
        switch (type){
            case 'string':
                return (this.options.dragNode==='none') ? this.node : this.#cloneNode();
            case 'function':
                return this.options.dragNode(this);
            case 'element':
                return this.options.dragNode;
        }
    }
    #cloneNode(){
        const deep = this.options.dragNode==='deepClone';
        const node = this.node.cloneNode(deep||true);
        dom.setStyles(node, { opacity: 0.5 });
        return node;
    }
    #setDragNodePosition(e){
        const size = dom.getSize(this.node);
        const position = dom.getPosition(this.container, document.body);
        const x = e.clientX-position.x;
        const y = e.clientY-position.y;

        console.log(x);

        // const x = position.x;
        // const y = position.y;

        this.start.pos = {x, y};
        dom.setStyles(this.dragNode, {
            position: 'absolute',
            width: `${size.x}px`,
            height: `${size.y}px`,
            left: `${x}px`,
            top: `${y}px`,
            background: '#dddddd',
            'z-index': '10000'
        });
        dom.addClass(this.dragNode, 'drag');
        dom.addClass(this.node, 'dragged');
        this.container.append(this.dragNode);
    }
    dragstart(e){
        if (!this.dragNode) this.dragNode = this.#craeteDragNode();
        this.#setDragNodePosition(e);

        this.dispatchEvent('dragstart', e);
    }
    drag(e){
        if (e.buttons===1){
            const {clientX:x, clientY:y} = e;
            const dx = x - this.start.x;
            const dy = y - this.start.y;

            dom.setStyles(this.dragNode, {
                left: `${this.start.pos.x + dx}px`,
                top: `${this.start.pos.y+dy}px`
            });

            this.dispatchEvent('dragmove', e);
        }else{
            this.dragend()
        }
    }

    dragcancel(e){
        this.removeAllEvents();
        this.dispatchEvent('dragcancel', e);
    }
    dragend(e){
        this.dispatchEvent('dragend', e);
        this.removeAllEvents();
        dom.removeClass(this.node, 'dragged');
        if (this.dragNode !== this.node) this.dragNode.remove();
    }

    removeAllEvents(){
        this.container.removeEventListener('mousemove', this.#dragCheckFun);
        this.container.removeEventListener('mousemove', this.#dragFun);
        this.container.removeEventListener('mouseup', this.#dragCancelFun);
        this.container.removeEventListener('mouseup', this.#dragEndFun);
    }
    destroy(){
        this.removeAllEvents();
        this.node.removeEventListener('mousedown', this.#dragReady);
    }
}

export class Dragdrop extends Draggable {
    // #dropenterFun = null;
    // #dropleaveFun = null;
    // #dropoverFun = null;
    dropNode = null;

    constructor(node, droppables, options={}) {
        super(node, options);
        this.droppables = droppables || [];
        this.droppableNodes = null;

        // //当拖动进入到某个dom对象时触发
        // this.#dropenterFun = this.#dropenterFun || ((e)=>{ this.dropenter(e); });
        //
        // // 当拖动到某个dom对象上时触发
        // this.#dropleaveFun = this.#dropleaveFun || ((e)=>{ this.dropleave(e); });
        //
        // //当拖动离开某个dom对象时触发
        // this.#dropoverFun = this.#dropoverFun || ((e)=>{ this.dropover(e); });
    }
    setDroppables(droppables){
        if (droppables) this.droppables = droppables;
    }

    getDroppableNodes(){
        const type = typeOf(this.droppables);
        switch (type){
            case 'string':
                return [...document.querySelectorAll(this.droppables)]
            case 'function':
                return this.droppables(this);
            case 'array':
                return this.droppables;
        }

    }
    dragCheckDetermined(e){
        super.dragCheckDetermined(e);
        this.droppableNodes = this.getDroppableNodes();
        this.droppablesRect = this.droppableNodes.map((node)=>{
            const rect = node.getBoundingClientRect();
            const area = rect.width * rect.height;
            return {node, rect, area}
        });
    }

    drag(e){
        super.drag(e)
        if (e.buttons===1){
            const drop = this.findDropNode(e);
            if (drop){
                if (this.dropNode !== drop){
                    if (this.dropNode) this.dropleave(e);
                    this.dropNode = drop;
                    this.dropenter(e);
                }else{
                    this.dropover(e);
                }
            }else{
                if (this.dropNode){
                    this.dropleave(e);
                    this.dropNode = drop;
                }
            }
        }
    }
    findDropNode(e){
        const mouseInNodes = this.droppablesRect.filter((r)=>{
            return (e.pageY > r.rect.top) && (e.pageY < r.rect.top+r.rect.height) && (e.pageX > r.rect.left) && (e.pageX < r.rect.left+r.rect.width);
        });
        if (mouseInNodes.length){
            let rect = mouseInNodes[0];
            mouseInNodes.forEach((r)=>{
                if (r.area<rect.area) rect = r;
            });
            return rect.node.node || rect.node;
        }
        return null;
    }

    createMarkNode(){
        if (!this.options.dropMark){
            this.options.dropMark = {
                'border': '2px solid var(--oo-color-main)',
                'z-index': '19999'
            }
        }
        switch (typeOf(this.options.dropMark)){
            case 'object':
                if (this.markNode) return this.markNode;
                const node = dom('div', {styles: this.options.dropMark});
                const sub = dom('div', {styles: {
                        'background-color': 'var(--oo-color-main-light)',
                        'width': '100%',
                        'height': '100%',
                        'opacity': '0.1',
                    }
                });
                node.append(sub);
                return node;
            case 'element':
                return this.options.dropMark;
            case 'function':
                return this.options.dropMark(this);
        }
    }

    positionMarkNode(){
        if (this.options.dropMark!=='none'){
            // if (!this.markNode){
                this.markNode = this.createMarkNode();
            // }

            if (this.markNode){
                const container = (this.options.markContainer || this.container);
                const size = dom.getSize(this.dropNode);
                const pos = dom.getPosition(this.dropNode, container);
                dom.setStyles(this.markNode, {
                    position: 'absolute',
                    'z-index': '9999',
                    transition: 'width 0.2s, height 0.2s, left 0.2s, top 0.2s'
                });
                container.append(this.markNode);

                window.setTimeout(()=>{
                    if (this.markNode) dom.setStyles(this.markNode, {
                        width: `${size.x}px`,
                        height: `${size.y}px`,
                        left: `${pos.x}px`,
                        top: `${pos.y}px`
                    });
                }, 10)
            }
        }
    }
    removeMarkNode(){
        if (this.markNode){
            this.markNode.remove();
        }
    }
    dropenter(e){
        this.positionMarkNode();
        this.dispatchEvent('dragenter', e);
        e.stopPropagation();
    }
    dropleave(e){
        this.dispatchEvent('dragleave', e);
        this.removeMarkNode();
        this.dropNode = null;
        e.stopPropagation();
    }
    dropover(e){
        this.dispatchEvent('dragover', e);
        e.stopPropagation();
    }

    dragend(e){
        if (this.dropNode){
            this.dispatchEvent('drop', e);
            this.dispatchEvent('dragend', e);
        }else{
            this.dispatchEvent('dragcancel', e);
        }
        this.removeAllEvents();
        dom.removeClass(this.node, 'dragged');
        if (this.dragNode !== this.node) this.dragNode.remove();
    }

    removeAllEvents(){
        super.removeAllEvents();
        this.removeMarkNode();
    }
}

