import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from './template/option.scope.css?raw';
const html = `
<div class="content">
    <div class="body">
        <div class="area">
            <div class="icon"></div>
            <div class="text"></div>
            <div class="label"></div>
        </div>
        <div class="arrow"></div>
    </div>
    <div class="optionsPosition options-position-node">
        <div class="optionsArea options-area invisible">
            <div class="optionsContent options-content"><slot name="items"></slot></div>
        </div> 
    </div>
</div>`;

export default class OOOption extends OOComponent {
    static prop = {
        value: '',
        text: '',
        icon: '',
        selected: false,
        disabled: false,
        skin: '',
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att) => {
            return hyphenate(att);
        });
    }
    _selectCascade(flag = true) {
        const cascade = this.optionCascade;
        if (cascade && cascade.length) {
            cascade.forEach((c) => {
                c.selected = flag;
                if (flag) {
                    c._showOptions();
                }
            });
        }
    }
    _checkSelected() {
        dom.checkClass(this._elements.body, 'selected', this._props.selected);
        if (this._props.selected) {
            this._selectCascade();
        } else {
            this._selectCascade(false);
        }
    }
    _setPropMap = {
        value: () => {},
        $default: () => {},
        text: () => {
            this.#renderText();
        },
        disabled: () => {
            dom.checkClass(this._content, 'disabled', !!this._props.disabled);
        },
        selected: () => {
            this._checkSelected();
            if (!this.#options || !this.#options.length) {
                this.select?._selected(this);
            }
        },
        icon: (old)=>{
            dom.removeClass(this._elements.icon, `ooicon-${old}`);
            if (!!this._props.icon) {
                dom.addClass(this._elements.icon, `ooicon-${this._props.icon}`);
            }
        },
        skin: () => {
            this._useSkin(this._props.skin);
        },
    };
    _elements = {
        body: null,
        text: null,
        label: null,
        area: null,
        optionsPosition: null,
        optionsArea: null,
        optionsContent: null,
        arrow: null,
        icon: null
    };
    #options = [];
    #groups = [];
    constructor() {
        super();
        this._initialize('oo-option', OOOption, html, css);
    }

    _render() {
        this.#renderText();
        this._setPropMap.disabled();
    }
    #renderText(){
        if (this.innerHTML.trim()){
            let node = this.firstChild;
            while(node){
                if (node.nodeType===Node.TEXT_NODE || (node.nodeType===Node.ELEMENT_NODE && !['oo-option', 'oo-option-group'].includes(node.tagName.toLowerCase()) )){
                    let n = node;
                    node = node.nextSibling;
                    this._elements.label.appendChild(n);
                }else{
                    node = node.nextSibling;
                }
            }
        }
        if (!this._elements.label.innerHTML.trim()) {
            if (this._props.text){
                this._elements.text.textContent = this._props.text;
            }else{
                this._elements.text.innerHTML = '&nbsp;';
            }
        }
        this._elements.area.title = this._elements.area.textContent;
    }

    parent = null;
    _connected() {
        this._connectToSelect();

        let node = this.firstElementChild;
        while (node) {
            node._connected?.();
            node = node.nextElementSibling;
        }
    }
    _connectToSelect() {
        const parent = this.parentElement;
        const tagName = parent.tagName.toLowerCase();
        if (['oo-select', 'oo-option-group', 'oo-option'].includes(tagName)) {
            this.parent = parent;
        }
        if (this.parent && this.parent._appendItem) {
            this.parent._appendItem(this);
            dom.show(this);
        } else {
            dom.hide(this);
        }
    }

    get select() {
        if (this.parent) {
            const tag = this.parent.tagName.toLowerCase();
            if (tag === 'oo-select') return this.parent;
            return this.parent.select;
        }
        return null;
    }
    get level() {
        let p = this.parent;
        let l = 1;
        while (p) {
            if (p.tagName.toLowerCase() === 'oo-option') {
                l++;
            }
            p = p.parent;
        }
        return l;
    }
    #inGroup() {
        return this.parent?.tagName.toLowerCase() === 'oo-option-group';
    }

    _disconnected() {
        this.parent?._removeItem(this);
        this.parent = null;
    }

    _setEvent() {
        this._content.addEventListener('click', (e) => {
            if (this.#options && this.#options.length) {
                this._showOptions();
            } else {
                if (!this._props.disabled) this.selected = true;
            }
            e.stopPropagation();
        });
        this._content.addEventListener('mouseover', (e) => {
            if (this.parent._currentCascade && this.parent._currentCascade !== this) {
                this.parent._currentCascade._hideOptions();
            }
            if (this.#options && this.#options.length) {
                this._showOptions();
            }
            e.stopPropagation();
        });

        if (this.level === 1) {
            const checkMaxWidth = () => {
                const sizeOption = dom.getSize(this._elements.text);
                this.select?.checkMaxWidth(sizeOption.x + (this.#inGroup() ? 20 : 0));
            };
            const observer = new MutationObserver(checkMaxWidth);
            observer.observe(this._content, {
                subtree: true,
                childList: true,
                attributes: true,
                characterData: true,
            });
        }
    }

    get optionCascade() {
        let p = this.parent;
        const value = [];
        while (p) {
            if (p.tagName.toLowerCase() === 'oo-option') {
                value.unshift(p);
            }
            p = p.parent;
        }
        return value;
    }
    get value() {
        let p = this.parent;
        const value = [this._props.value || this._props.text];
        while (p) {
            if (p.tagName.toLowerCase() === 'oo-option') {
                value.unshift(p._props.value || this._props.text);
            }
            p = p.parent;
        }
        return value && value.length === 1 ? value[0] : value;
    }

    get text() {
        let p = this.parent;
        const value = [this._props.text || this._elements.label.textContent];
        while (p) {
            if (p.tagName.toLowerCase() === 'oo-option') {
                value.unshift(p._props.text || p._elements.label.textContent);
            }
            p = p.parent;
        }
        return value && value.length === 1 ? value[0] : value;
    }

    #droped = false;

    #positionOptions() {
        const size = dom.getSize(this._content);
        const x = size.x + 2;
        dom.setStyles(this._elements.optionsArea, {
            left: x + 'px',
        });
    }
    #toggleStyles() {
        dom.checkClass(this._elements.body, 'focus', this.#droped);
        // dom.checkClass(this._elements.box, 'focus', this.#droped);
        // dom.checkClass(this._elements.label, 'focus', this.#droped);
    }
    _showOptions() {
        if (!this._props.disabled && !this.#droped) {
            this.#droped = true;
            this.#toggleStyles();
            this.#positionOptions();
            dom.removeClass(this._elements.optionsPosition, 'hide');

            dom.removeClass(this._elements.optionsArea, 'invisible');

            dom.addClass(this._elements.optionsArea, 'visible');

            this.dispatchEvent(new CustomEvent('show'));

            if (this.parent) this.parent._currentCascade = this;
        }
    }
    _hideOptions() {
        this.#droped = false;
        this.#toggleStyles();
        dom.removeClass(this._elements.optionsArea, 'visible');
        dom.addClass(this._elements.optionsArea, 'invisible');

        this.#options.forEach((op) => {
            op._hideOptions();
        });

        this.dispatchEvent(new CustomEvent('hide'));
    }

    //select
    _appendItem(option) {
        this.#options.push(option);

        option.setAttribute('slot', 'items');
        option.setAttribute('skin', this.select?._props.skin);

        if (this.select?._props.size) option.setAttribute('size', this.select._props.size);

        this._appendItemCheckValue(option);

        dom.addClass(this._elements.arrow, 'ooicon-arrow_forward');

        // this._setPropMap.$default('value');
    }
    _appendItemCheckValue(option) {
        const i = this.level - 1;
        const value = Array.isArray(this.select?.value) ? this.select.value?.[i] : '';
        if (value) {
            if (option.value === value) {
                option.selected = true;
            }
        }
    }
    _removeItem(option) {
        this.#options.splice(this.#options.indexOf(option), 1);
        option.removeAttribute('slot');
        if (!this.#options || !this.#options.length){
            dom.removeClass(this._elements.arrow, 'ooicon-arrow_forward');
        }
    }

    _removeGroup(group) {
        this.#groups.splice(this.#groups.indexOf(group), 1);
        group.removeAttribute('slot');
    }
    _appendGroup(group) {
        this.#groups.push(group);
        group.setAttribute('slot', 'items');
        group.setAttribute('skin', this.select?._props.skin);
        if (this.select?._props.size) option.setAttribute('size', this.select?._props.size);
    }

    // _setEvent(){
    //     this._content.addEventListener('click', ()=>{
    //         if (!this._props.disabled) this.selected = true;
    //     });

    //     const checkMaxWidth = ()=>{
    //         const sizeOption = dom.getSize(this._elements.text);
    //         this.#select?.checkMaxWidth(sizeOption.x + (this.#group ? 20 : 0));
    //     }
    //     const observer = new MutationObserver(checkMaxWidth);
    //     observer.observe(this._content, {
    //         subtree: true,
    //         childList: true,
    //         attributes: true,
    //         characterData: true
    //     });
    // }
}
