import {dom, loadCssText, typeOf, defer} from '@o2oa/util';

const cssModules = import.meta.glob('./styles/tooltip/*.css', { as: 'inline'});
export class Tooltip{
    static option = {
        position: 'top', //top, bottom, left, right,
        style: 'default',
        autoClose: 0,
        emerge: -8,
        offset: 6,
        defer: 600,
        events: {
            show: 'mouseover',
            hide: 'mouseout, mousedown',
        },
        showOnEnterCotent: false,
        resetContentOnShow: false,
        container: null
    }
    static loadedStyles = {};
    constructor(content, node, options) {
        this.content = content;
        this.target = node;
        this.options = Object.assign({}, Tooltip.option, options);
        this.#createNode();
        this.#observerParent();
        if (!this.showFun) this.showFun = this.show.bind(this);
        if (!this.hideFun) this.hideFun = this.hide.bind(this);
        if (!this.options.events){
            defer(this.showFun, this.options.defer, this);
            // this.show();
        }else{
            this.#loadCss();
        }
    }
    async #createNode() {
        this.container = dom('div.ootooltip_container');
        this.node = dom('div.ootooltip');
        this.triangle = dom(`div.ootooltip_triangle.${this.options.position}`);
        this.contentNode = dom(`div.ootooltip_content`);
        this.node.append(this.triangle);
        this.node.append(this.contentNode);
        this.container.append(this.node);
        this.#bindEvents();
    }
    #attachContainer(){
        (this.options.container || document.body).append(this.container);
    }
    #detachContainer(){
        this.container.remove();
    }

    #bindEvents(){
        if (this.options.events){
            
            if (this.options.events.show){
                const evts = this.options.events.show.split(/\s*,\s*/g);
                evts.forEach((evt)=>{
                    this.target.addEventListener(evt, (e)=>{
                        if (this.hideFun.clear) this.hideFun.clear();
                        defer(this.showFun, this.options.defer, this);
                    });
                })

                if( this.options.showOnEnterCotent ){
                    this.container.addEventListener('mouseenter', ()=>{
                        if (this.hideFun.clear) this.hideFun.clear();
                    })
                }
            }
            if (this.options.events.hide){
                const evts = this.options.events.hide.split(/\s*,\s*/g);
                evts.forEach((evt)=>{
                    this.target.addEventListener(evt, ()=>{
                        if (this.showFun.clear) this.showFun.clear();
                        defer(this.hideFun, 100, this);
                    });
                });

                if( this.options.showOnEnterCotent ) {
                    this.container.addEventListener('mouseleave', () => {
                        if (this.showFun.clear) this.showFun.clear();
                        defer(this.hideFun, 100, this);
                    })
                }
            }
        }
    }

    #getNodePostion(){
        // const p = dom.getPosition(this.target, this.options.container);
        const p = (this.options.container) ? {x: this.target.offsetLeft, y: this.target.offsetTop} : dom.getPosition(this.target);
        const s = dom.getSize(this.target);

        // const maxWidth = (s.x < 200) ? '12.5rem' : 'unset';
        // dom.setStyle(this.node, 'max-width', maxWidth);

        const nodeSize = dom.getSize(this.node);
        const flag = dom.getSize(this.triangle).x/2;

        let position = this.#getPostions(this.options.position, p, s, nodeSize, flag);
        switch (this.options.position){
            case 'top':
                if (position.y < 0) position = this.#changePosition('bottom', p, s, nodeSize, flag);
                break;
            case 'left':
                if (position.x < 0) position = this.#changePosition('right', p, s, nodeSize, flag);
                break;
        }
        if (this.options.position==='top' || this.options.position==='bottom'){
            if (position.x<0){
                const x = position.x;
                position.flagX += x;
                position.x = 0;
                position.fromX = 0;
            }
        }
        if (this.options.position==='left' || this.options.position==='right'){
            if (position.y<0){
                const y = position.y;
                position.flagY += y;
                position.y = 0;
                position.fromY = 0;
            }
        }

        return position;
    }
    #changePosition(ps, p, s, nodeSize, flag){
        dom.removeClass(this.triangle, this.options.position);
        dom.addClass(this.triangle, ps);
        this.options.position = ps;
        return this.#getPostions(this.options.position, p, s, nodeSize, flag);
    }
    #getPostions(ps, p, s, nodeSize, flag){
        const position = {}
        const o = this.options.emerge || 0;
        const offset = (this.options.offset || this.options.offset===0) ? this.options.offset : 3;

        switch (ps){
            case 'bottom':
                position.x = position.fromX = p.x + s.x/2 - nodeSize.x/2;
                position.y = p.y + s.y + flag + offset;
                position.fromY = position.y - o;

                position.flagX = nodeSize.x/2 - flag;
                position.flagY = 0 - flag;
                break;
            case 'left':
                position.x = p.x - nodeSize.x - flag - offset;
                position.fromX = position.x + o;
                position.y = position.fromY = p.y + s.y/2 - nodeSize.y/2;
                position.flagX = nodeSize.x - flag;
                position.flagY = nodeSize.y/2 - flag;
                break;
            case 'right':
                position.x = p.x + s.x + flag + offset;
                position.fromX = position.x - o;
                position.y = position.fromY = p.y + s.y/2 - nodeSize.y/2;
                position.flagX = 0 - flag;
                position.flagY = nodeSize.y/2 - flag;
                break;
            default:
                position.x = position.fromX = p.x + s.x/2 - nodeSize.x/2;
                position.y = p.y - nodeSize.y - flag - offset;
                position.fromY = position.y + o;

                position.flagX = nodeSize.x/2 - flag;
                position.flagY = nodeSize.y - flag;
        }
        return position;
    }


    #checkClose(){
        if (this.options.autoClose>0){
            window.setTimeout(()=>{
                this[!!this.options.events ? 'hide' : 'close']();
            }, this.options.autoClose);
        }
    }
    close(){
        dom.setStyles(this.node, {
            'opacity': 0
        });
        this.hideTimeout = window.setTimeout(()=>{
            this.container.remove();
        }, 600);
    }
    hide(){
        dom.setStyles(this.node, {
            'opacity': 0
        });
        this.hideTimeout = window.setTimeout(()=>{
            dom.addClass(this.node, 'hide');
            dom.removeClass(this.node, 'show');
            if (this.p) dom.setStyles(this.node, {
                'left': this.p.fromX + 'px',
                'top': this.p.fromY + 'px'
            });

            this.#detachContainer();
        }, 600);
    }

    async #getStyle(styleName) {
        const cssText = await cssModules[`./styles/tooltip/${styleName}.css`]();
        return loadCssText(cssText.default, this.container);
    }
    async #loadCss() {
        const style = Tooltip.loadedStyles[this.options.style || 'default'];
        if (!style){
            const styleName = this.options.style || 'default';
            Tooltip.loadedStyles[styleName] = this.#getStyle(styleName);
            Tooltip.loadedStyles[styleName] = await Tooltip.loadedStyles[styleName];
        }else{
            const styleNode = await Promise.resolve(style);
            dom.addClass(this.container, styleNode.id);
        }
    }

    async #setContent() {
        if (this.options.resetContentOnShow || !this.contentLoaded){
            this.target.dispatchEvent(new MouseEvent('tooltip'));
            dom.empty(this.contentNode);
            this.contentLoaded = true;
            await this.#setNodeContent(this.content);
            return ''
        }
    }
    async #setNodeContent(content) {
        switch (typeOf(content)) {
            case 'string':
                dom.set(this.contentNode, 'html', content);
                break;
            case 'element':
                this.contentNode.append(content);
                break
            case 'function':
                const v = await Promise.resolve(content());
                await this.#setNodeContent(v);
        }
    }

    #observerParent(){
        this.observerNode = this.observerNode || this.target.parentElement;
        var observer = new MutationObserver((mutationList, observer)=>{
            if (mutationList[0].removedNodes && mutationList[0].removedNodes.length){
                for (let node of mutationList[0].removedNodes){
                    if (node === this.target){
                        this.hide();
                        observer.disconnect();
                        break;
                    }
                }
            }
        });
        if (this.observerNode) observer.observe(this.observerNode, {childList: true});
    }

    async show() {
        if (!this.target || !this.target.isConnected) return null;
        if (this.hideTimeout){
            clearTimeout(this.hideTimeout);
            this.hideTimeout = null;
        }
        dom.removeClass(this.node, 'hide');
        dom.addClass(this.node, 'show');

        this.#attachContainer();

        await this.#loadCss();
        await this.#setContent();

        if (this.contentNode.childNodes.length){
            dom.removeClass(this.node, 'hide');
            dom.addClass(this.node, 'show');

            // window.setTimeout(()=>{
                this.p = this.#getNodePostion();

                dom.setStyles(this.node, {
                    'left': this.p.fromX + 'px',
                    'top': this.p.fromY + 'px'
                });
                dom.setStyles(this.triangle, {
                    'left': this.p.flagX + 'px',
                    'top': this.p.flagY + 'px'
                });

                window.setTimeout(() => {
                    dom.setStyles(this.node, {
                        'opacity': 1,
                        'left': this.p.x + 'px',
                        'top': this.p.y + 'px'
                    });
                    this.#checkClose();
                }, 5);
            // }, 2);

        }

        if (!this.target || !this.target.isConnected){
            this.hide();
        }
    }
}

export function tooltip(content, node, options){
    return new Tooltip(content, node, options);
}
