import {dom} from '@o2oa/util';
import {$Input} from './$input.js';
import html from './template/textarea.html?raw';
import cssInput from "./template/$input.scope.css?raw";
import css from './template/textarea.css?raw';

export default class OOTextarea extends $Input{
    constructor() {
        super();
        this.#setPropMap();
        this._init('oo-textarea');
        this._useCss(css);
    }
    #setPropMap() {
        this._setPropMap = Object.assign(this._setPropMap, {
            autoSize: () => {
                dom.toggleClass(this._elements.input, 'autosize', this._props.autoSize);
                this.setHeight();
            },
            value: ()=>{
                try{
                    this._elements.input.value = this._props.value;
                }catch(e){}
                // this._elements.viewText.textContent = this._props.value;
                if (this._props.value){
                    this._elements.viewText.textContent = this._props.value;
                }else{
                    this._elements.viewText.innerHTML = '-';
                }

                window.setTimeout(()=>{
                    this.setHeight();
                }, 10);
            },
        });
    }
    _init(name){
        this._initialize(name, OOTextarea, html, cssInput);
    }

    _setEvent() {
        super._setEvent();
        this._elements.input.addEventListener('input', ()=>{
            this.setHeight();
        });
        dom.toggleClass(this._elements.input, 'autosize', this._props.autoSize);
        this.setHeight();
    }

    setHeight(){
        if (this._props.autoSize){
            // const size = dom.getSize(this._elements.input);
            this._elements.input.style.height = 'auto';
            const height = this._elements.input.scrollHeight;
            this._elements.input.style.height = height+'px';
        }
    }
}

