export default "* {\r\n    box-sizing: border-box;\r\n}\r\n.container{\r\n    /*position: fixed;*/\r\n    width: 100%;\r\n    top: 0;\r\n    left: 0;\r\n    padding: 0.714em;\r\n    min-height: 5em;\r\n    /* transition: all 0.5s; */\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: flex-start;\r\n}\r\n.content{\r\n    display: flex;\r\n    width: inherit;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n.align_left{\r\n    justify-content: flex-start;\r\n}\r\n.align_right{\r\n    justify-content: flex-end;\r\n}\r\n.align_center{\r\n    justify-content: center;\r\n}\r\n\r\n.valign_top{\r\n    align-items: flex-start;\r\n}\r\n.valign_bottom{\r\n    align-items: flex-end;\r\n}\r\n.valign_center{\r\n    align-items: center;\r\n}\r\n.icon {\r\n    color: var(--oo-color-text-white);\r\n    font-size: calc(var(--oo-font-size-largest)*2);\r\n    text-align: center;\r\n    padding: 0.1em;\r\n}\r\n.message {\r\n    padding: 0.357em;\r\n}\r\n.title {\r\n    font-weight: bold;\r\n    color: var(--oo-color-text-white);\r\n    padding: 0 0.357em 0.357em 0.357em;\r\n}\r\n.text{\r\n    color: var(--oo-color-text-white);\r\n    padding: 0 0.357em;\r\n}\r\n.close{\r\n    cursor: pointer;\r\n    color: var(--oo-color-text-white);\r\n}\r\n\r\n.info.container{\r\n    background: var(--oo-color-info);\r\n}\r\n.error.container{\r\n    background: var(--oo-color-error);\r\n}\r\n.warn.container{\r\n    background: var(--oo-color-warn);\r\n}\r\n.success.container{\r\n    background: var(--oo-color-success);\r\n}\r\n"