import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import html from './template/$button.html?raw';
import css from './template/$button.scope.css?raw';

class $Button extends OOComponent{
    static prop = {
        leftIcon: '',
        rightIcon: '',
        styles: '',
        disabled: false,
        text: '',
        type: '',    //default or cancel or light or simple or icon
        skin: ''
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _elements={
        text: null,
        prefix : null,
        suffix: null,
        button: null
    };
    #eventListeners = {};
    _setPropMap={
        leftIcon: (old)=>{
            dom.removeClass(this._elements.prefix, `ooicon-${old} icon`);
            if (this._props.leftIcon) dom.addClass(this._elements.prefix, `ooicon-${this._props.leftIcon} icon`);
        },
        rightIcon: (old)=>{
            dom.removeClass(this._elements.suffix, `ooicon-${old} icon`);
            if (this._props.rightIcon) dom.addClass(this._elements.suffix, `ooicon-${this._props.rightIcon} icon`);
        },
        styles: ()=>{
            // dom.toggleAttr(this._elements.button, 'style', this._props.style);
        },
        disabled: (old)=>{
            dom.checkClass(this._elements.button, 'disabled', this._props.disabled);
            dom.toggleAttr(this._elements.button, 'disabled', this._props.disabled);
            this.#disable(this._props.disabled);
        },
        type: (oldValue)=>{
            if (!this._props.type) this._props.type = "default";
            if (oldValue) dom.removeClass(this._elements.button, oldValue);
            dom.addClass(this._elements.button, this._props.type);
        },
        text: ()=>{
            if (!this.innerHTML.trim()) this._elements.text.textContent = this._props.text;
        },
        skin: ()=>{
            this._useSkin(this._props.skin);
        },
        $default: (key)=>{
            if (key==='value') this.value = this._props[key];
            dom.toggleAttr(this._elements.button, key, this._props[key]);
        }
    };
    #disable(flag){
        if (flag){
            this.#eventListeners = this.#eventListeners || {};
            for (let prop in this) {
                if (prop.startsWith('on') && !!this[prop]) {
                    this.#eventListeners[prop] = this[prop];
                    this[prop] = null;
                }
            }
            this.style.pointerEvents = 'none';
        }else {
            if (this.#eventListeners) {
                for (let prop in this.#eventListeners) {
                    if (!!this.#eventListeners[prop]) {
                        this[prop] = this.#eventListeners[prop];
                        this.#eventListeners[prop] = null;
                    }
                }
            }
            this.style.pointerEvents = 'auto';
        }
    }
    constructor() {
        super();
    }
    _init(name, clazz, ohtml, ocss){
        this._initialize(name,clazz||$Button, ohtml||html, ocss||css);
    }
    _render(){
        const _bt = this;
        const observer = new MutationObserver(function(){
            _bt.#setContent();
        });
        observer.observe(this, {
            subtree: true,
            childList: true,
            attributes: true,
            characterData:true
        });
        this.#setContent();
    }
    _connected(){}
    #setContent(){
        if (this.innerHTML.trim()){
            dom.empty(this._elements.text);
            this._elements.text.insertAdjacentHTML('beforeend', this.innerHTML);
        }
    }
}
export {$Button}
