import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from './template/optionGroup.scope.css?raw';

const html = `
<div class="content">
    <div class="text"></div>
    <div class="label"></div>
    <div class="options-content">
        <slot name="items"></slot>
    </div>
</div>`;
export default class OOOptionGroup extends OOComponent{
    static prop = {
        text: '',
        skin: ''
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _setPropMap={
        $default: ()=>{},
        text: ()=>{
            this._elements.text.textContent = this._props.text;
        },
        label: ()=>{
            this._elements.label.textContent = this._props.label;
        },
        skin: ()=>{
            this._useSkin(this._props.skin);
        },
    };
    _elements={
        text: null,
        label: null
    }
    _render() {
        this._elements.text.textContent = this._props.text || this._props.label;
    }
    constructor() {
        super();
        this._initialize('oo-option-group', OOOptionGroup, html, css);
    }
    #select = null;

    parent = null
    #options = [];
    _connected() {
        // const select = this.parentElement;
        const parent = this.parentElement;
        const tagName = parent.tagName.toLowerCase();

        if (['oo-select', 'oo-option'].includes(tagName)){
            this.parent =  parent;
        }
        if (this.parent){
            this.parent._appendGroup?.(this);
            dom.show(this);
        }else{
            dom.hide(this);
        }
        // if (select.tagName.toLowerCase()==='oo-select') {
        //     this.#select = select;
        // }else if (select.tagName.toLowerCase()==='oo-option'){
        //     this.#cascade = select;
        // }
        // if (this.#select || this.#select){
        //     (this.#select || this.#cascade)._appendGroup(this);
        //     dom.show(this);
        // }else{
        //     dom.hide(this);
        // }
    }
    get select(){
        if (this.parent){
            const tag = this.parent.tagName.toLowerCase();
            if (tag==='oo-select') return this.parent;
            return this.parent.select;
        }
        return null;
    }
    get level(){
        let p = this.parent;
        let l = 1;
        while (p){
            if (p.tagName.toLowerCase()==='oo-option'){
                l++;
            }
            p = p.parent
        }
        return l;
    }

    _disconnected(){
        this.parent?._removeGroup(this);
        this.parent = null;
    }
    _removeItem(option){
        if( this.select )this.select._removeItem(option)
        this.#options.splice(this.#options.indexOf(option), 1);
    }
    _appendItem(option) {
        if( this.select )this.select._appendItem(option)
        this.#options.push(option);
    }
}
