import {camelCase, cloneObject, dom, typeOf, hyphenate} from '@o2oa/util';

const templates = {};

function use(html, css, name, isIcon){
    if (name){
        if (templates[name]) return templates[name];
    }
    const template = document.createElement('template');
    template.innerHTML = html;
    const templateContent = template.content;
    if (css){
        const style = document.createElement('style');
        style.textContent = css;
        templateContent.prepend(style);
    }
    if (isIcon){
        const iconStyle = document.querySelector('#oo-css-skin');
        if (iconStyle){
            const iconStyleCopy = iconStyle.cloneNode(true);
            iconStyleCopy.removeAttribute('id');
            templateContent.prepend(iconStyleCopy);
        }
    }
    document.body.appendChild(template);

    if (name){
        templates[name] = template.content;
    }
    return template.content;
}



class OOComponent extends HTMLElement{
    _setEvent(){}
    _afterRender(){}
    _render(name){}
    _connected(){}
    _disconnected(){}

    #customColorStyle = null;

    constructor() {
        super();
    }
    _initialize(name, clazz, html, css){
        if (Object.hasOwn(this, 'value')){
            if (this.value){
                this.setAttribute('value', this.value);
            }
            Reflect.deleteProperty(this, "value")
        }

        this._props = (clazz) ? this._getProps(clazz.prop) : {};

        const template = use(html, css, name, true);
        this._useTemplate(template);
        this._render(name);
        this._setEvent();
        this._afterRender();

        this._createProperties(clazz);
        this.dispatchEvent(new CustomEvent('load'));
    }
    _createProperties(clazz){
        Object.keys(this._props).forEach((k)=>{
            let key = k;
            while (this.hasOwnProperty(key) || clazz.prototype.hasOwnProperty(key)){
                key = `_${key}`;
            }
            if (!Object.hasOwn(this, key)){
                Object.defineProperty(this, key, {
                    get: ()=> {
                        return this._props[key];
                    },
                    set: (v)=>{
                        if (v!==this._props[key]){
                            this.setAttribute(hyphenate(key), v);
                        }
                    }
                });
            }
        });
    }

    #setProps(props, name, value){
        switch (typeOf(props[name])){
            case 'boolean':
                props[name] = (!!value && value!=='false');
                break;
            case 'number':
                props[name] = (!isNaN(value)) ? parseInt(value) : value;
                break;
            case 'object':
                try{
                    props[name] = JSON.parse(value);
                }catch(e){
                    props[name] = value;
                }
                break;
            case 'array':
                try{
                    props[name] = JSON.parse(value);
                }catch(e){
                    props[name] = value ? value.split(/\s*,\s*/g) : [];
                }
                break;
            default:
                props[name] = value;
        }
    }
    _getProps(prop){
        const props = cloneObject(prop);
        this.getAttributeNames().forEach((name)=>{
            const n = camelCase(name);
            if (props.hasOwnProperty(n)){
                const attr = this.getAttribute(name);
                if (!/{{.*}}/g.test(attr)){
                    this.#setProps(props, n, attr);
                }
            }
        });
        return props;
    }
    _useCss(css){
        const style = document.createElement('style');
        style.textContent = css;
        this._content.insertAdjacentElement('beforebegin', style);
        return style;
    }
    _useCssLink(href){
        const link = document.createElement('link');
        link.rel = 'stylesheet';
        link.type = 'text/css';
        link.charSet = 'UTF-8';
        link.href = href;
        this._content.insertAdjacentElement('beforebegin', link);
        return link;
    }
    useCss(css){
        if (this.styleNode){
            this.styleNode.remove();
            this.styleNode = null;
        }
        if (css) this.styleNode = this._useCss(css);
    }
    useCssLink(css){
        if (this.styleLinkNode){
            this.styleLinkNode.remove();
            this.styleLinkNode = null;
        }
        if (css) this.styleLinkNode = this._useCssLink(css);
    }
    _useTemplate(template, mode='open'){
        const shadowRoot = this.shadowRoot || this.attachShadow({mode: mode});
        shadowRoot.appendChild(template.cloneNode(true));
        this.#getEls();
    }
    connectedCallback() {
        this._connected();
    }
    disconnectedCallback(){
        this._disconnected();
    }
    attributeChangedCallback(name, oldValue, newValue) {
        if (!/{{.*}}/g.test(newValue)){
            const key = camelCase(name);
            const old = this._props[key];
            this.#setProps(this._props, key, newValue);
            this.#renderProps(key, old, oldValue);
        }
    }
    #getEls(){
        for (const k in this._elements){
            this._elements[k] = this.shadowRoot.querySelector(`.${k}`);
        }
        this._content = this.shadowRoot.querySelector('.content');
    }
    #renderProps(name, old, oldValue){
        const props = (name) ? [name] : Object.keys(this._props);
        props.forEach((k)=> {
            this._setProps(k, old, oldValue);
        });
    }
    _setProps(key, old, oldValue){
        const value = this._props[key];
        if( oldValue !== value ){
            this._setPropMap[key] ? this._setPropMap[key](old, value) : this._setPropMap['$default'](key, old, value);
        }
    }
    _useSkin(skin){
        if (this.#customColorStyle) this.#customColorStyle.remove();
        this.#customColorStyle = null;
        if (skin){
            const skins = skin.split(/\s*;\s*/g);
            this.#customColorStyle = dom('style');
            let content = '';
            skins.forEach((s)=>{
                const styles = s.split(/\s*:\s*/g);
                content += `--${styles[0]}: ${styles[1]};\n\t`
            });
            this.#customColorStyle.textContent = `\n.content{\n\t${content}\n}\n`;
            this._content.insertAdjacentElement('beforebegin', this.#customColorStyle);
        }
    }
    _fillContent(slot){
        const observer = new MutationObserver(function(recordList){
            recordList.forEach((r)=>{
                r.addedNodes.forEach((n)=>{
                    if (n.nodeType===Node.ELEMENT_NODE){
                        n.setAttribute('slot', slot);
                    }
                });
            });
        });
        observer.observe(this, {
            subtree: false,
            childList: true,
            attributes: false,
            characterData:false
        });
        this.#setContent(slot);
    }
    #setContent(slot){
        let node = this.firstElementChild;
        while (node){
            node.setAttribute('slot', slot);
            node = node.nextElementSibling
        }
    }
    _canRender(andPars, orPars){
        // if (!this.isConnected) return false;
        if (andPars && andPars.length) for (const par of andPars){
            if (!this.getAttribute(par)) return false;
        }
        if (orPars && orPars.length) for (const par of orPars){
            if (this.getAttribute(par)) return true;
        }
        return true;
    }
}

export {use, OOComponent};
