export function selectOrg() {

}

function isDn(value) {
    return (typeof (value) === 'object' && (value.dn || value.distinguishedName)) || value?.includes?.('@');
}

function isId(value) {
    return '';
}

const orgTypeMap = {
    'Person': 'P',
    'Identity': 'I',
    'Unit': 'U',
    'Group': 'G',
    'Role': 'R',
    'UnitDuty': 'UD',
    'UnitAttribute': 'UA',
    'PersonAttribute': 'PA',
    'PersonParameter': 'PP',
    'UnitPost': 'UP',
};

/**
 * 判断一个字符串，是否是o2oa组织的 dn，并判断类型
 * @param value {String|Object} 要判断的字符串
 * @returns {string} 返回类型：I(身份)、P(个人)、U(组织)、G(群组)、R(角色)、UD(职务)、UA(组织属性)、PA(个人属性)、unknown
 */
function typeOfOrg(value) {
    if (isDn(value)) {
        const name = (typeof (value) === 'object') ? (value.dn || value.distinguishedName || '') : value;
        const dnList = name.split('@');
        const type = dnList?.[2] || 'unknown';
        return ['I', 'P', 'U', 'G', 'R', 'UD', 'UA', 'PA', 'PP', 'UP', 'PROCESS'].includes(type) ? type : 'unknown';
    }

    const flag = isId(value);
    if (flag) {
        return orgTypeMap[flag] || 'unknown';
    }
}

function cn(value) {
    if (Array.isArray(value)) {
        return value.flat(Infinity).map((v) => {
            return cn(v);
        });
    } else {
        const dnList = value.split('@');
        return dnList?.[0] || value;
    }
}

function ou(value) {
    if (Array.isArray(value)) {
        return value.flat(Infinity).map((v) => {
            return cn(v);
        });
    } else {
        const dnList = value.split('@');
        return dnList?.[1] || value;
    }
}

function dn(n) {
    return (typeof n) === 'object' ? (n.dn || n.id || n.unique) : n;
}

function dns(names) {
    return names.map(n => ((typeof n) === 'object' ? (n.dn || n.id || n.unique) : n));
}


/** 获取群组信息
 * @param id {String} 要查询的群组id、dn或unique
 * @returns {object|Array<object>} 返回群组对象。
 **/
async function getGroup(id) {
    if (Array.isArray(id)) {
        return await o2.Actions.load('x_organization_assemble_express').GroupAction.listObject({groupList: dns(id)}).then(json => json.data);
    } else {
        return await o2.Actions.load('x_organization_assemble_express').GroupAction.listObject({groupList: dns([id])}).then((json) => {
            const dataList = json.data;
            return dataList[0];
        });
    }
}

/** 获取身份完整信息
 * @param id {String|Array<string>} 要查询的身份id、dn
 * @returns {object|Array<object>} 返回身份对象。
 **/
async function getIdentity(id) {
    if (Array.isArray(id)) {
        return await o2.Actions.load('x_organization_assemble_express').IdentityAction.listObject({identityList: dns(id)}).then(json => json.data);
    } else {
        return await o2.Actions.load('x_organization_assemble_express').IdentityAction.listObject({
            identityList: dns([id]),
            referenceFlag: 'true'
        }).then((json) => {
            const dataList = json.data;
            return dataList[0];
        });
    }
}

/** 获取个人完整信息
 * @param id {String|Array<string|object>} 要查询的人员名称、id、dn或unique，或人员数组。
 * @returns {object|Array<object>} 返回完整的个人对象。包括所属群组、角色和其所有身份和所属组织。如果id传入字符串，则返回单个个人对象，如果id传入数组，则返回个人对象数组。
 **/
async function getPerson(id) {
    if (Array.isArray(id)) {
        return await o2.Actions.load('x_organization_assemble_express').PersonAction.listObject({personList: dns(id)}).then(json => json.data);
    } else {
        return await o2.Actions.load('x_organization_assemble_express').PersonAction.get(id).then(json => json.data);
    }
}

function getPersonIcon(id) {
    return !id ? `/x_organization_assemble_personal/jaxrs/person/icon` :
        `/x_organization_assemble_control/jaxrs/person/${encodeURI(dn(id))}/icon`;
}


