const processRoot = 'x_processplatform_assemble_surface'
function polyfillDistinguishedName(data, type){
    if (Array.isArray(data)){
        return data.map( d => polyfillDistinguishedName(d, type) )
    }else{
        if (!data.dn){
            data.dn = `${data.id}@${type.toUpperCase()}@${data.name}`;
        }
        return data;
    }
}

async function invoke(root, act, method, type, bind, ...par) {
    const service = await o2.Actions.load(root);
    return polyfillDistinguishedName((await service[act][method].apply(bind, par)), type);
}

const processDesigner = {
    async invoke(act, method, type, ...par) {
        return await invoke(processRoot, act, method, type, this, ...par);
    },
    async listApplication() {
        return await this.invoke('ApplicationAction', 'list', 'app');
    },
    async listProcess(key) {
        return await this.invoke('ProcessAction', 'listWithApplication', 'process', key);
    },
    async getApplication(key) {
        return await this.invoke('ApplicationAction', 'get', 'app', key);
    },
    async getProcess(key) {
        return await this.invoke('ProcessAction', 'getProcess', 'process', key);
    },
    async getProcessDesign(key) {
        return await this.invoke('ProcessAction', 'get', 'process', key);
    },
    async getProcessWithApp(key) {
        return new Promise((resolve)=>{
            window.setTimeout(async () => {
                const id = ou(key);
                const service = await o2.Actions.load(processRoot);
                const json = await this.invoke('ProcessAction', 'get', 'process', id);
                const appJson = await service.ApplicationAction.get(json.data.application);
                json.data.application = appJson.data;
                resolve(json.data);
            }, 500)
        });
    },
}




export {getGroup, getIdentity, getPerson, getPersonIcon, getRole, getUnit, processDesigner, cn, ou, typeOfOrg};
