const CurrencyProp = {
    prefix: '¥', //前缀，如 CNY1,234.56
    suffix: '', //后缀，如 #
    thousands: ',', //千分位符
    decimal: '.', //小数点符号
    precision: 2, //小数点后保留几位
    allowblank: false, //是否允许空白
    disablenegative: false, //是否允许输入负数
    maximum: 9007199254740991, //最大值，默认值为js最大安全整数
    minimum: -9007199254740991, //最小值，默认值为js最小安全整数
    round: true, //是否四舍五入
    // number: true, // true表示数字类型，false表示字符串类型
    // focusOnRight: false, //是否在输入时光标定位在小数点后面
    // minimumNumberOfCharacters: 0, //最小输入字符数，默认0
}

const RESTRICTED_CHARACTERS = ['+', '-']; // and number [0-9]
const RESTRICTED_OPTIONS = ['decimal', 'thousands', 'prefix', 'suffix'];

function validateRestrictedInput(value, caller) {
    if (RESTRICTED_CHARACTERS.includes(value)) {
        console.warn(`oo-currency "${caller}" property don't accept "${value}" as a value.`);
        return false;
    }
    if ((/\d/g).test(value)) {
        console.warn(`oo-currency "${caller}" property don't accept "${value}" (any number) as a value.`);
        return false;
    }
    return true;
}

function validateRestrictedOptions(opt) {
    for (const target of RESTRICTED_OPTIONS) {
        const isValid = validateRestrictedInput(opt[target], target);
        if (!isValid) {
            return false;
        }
    }
    return true;
}

export function filterOptRestrictions(opt){
    for (const option of RESTRICTED_OPTIONS) {
        // Remove numbers from option prop
        opt[option] = opt[option].replace(/\d+/g, '');
        for (const character of RESTRICTED_CHARACTERS) {
            // Remove restricted characters from option prop
            opt[option] = opt[option].replaceAll(character, '');
        }
    }
    return opt;
}

/** 检查字符串是否为有效的整数
 * @param {String} str 需要检查的字符串
 * @returns {Boolean}
 */
function isValidInteger(str) {
    return (typeof str === 'string') ? (/^-?[\d]+$/g).test(str) : false;
}


/** 检查字符串是否为有效的浮点数
 * @param {String} str 需要检查的字符串
 * @returns {boolean}
 */
function isValidFloat(str) {
    return (typeof str === 'string') ? (/^-?[\d]+(\.[\d]+)$/g).test(str) : false;
}

/** 移除字符串开头的零
 * @param {String} string 需要处理的字符串
 */
function removeLeadingZeros(string) {
    return string.replace(/^(-?)0+(?!\.)(.+)/, '$1$2');
}

/** 将字符串中的指定位置替换为指定字符
 *  @param {String} str 原字符串
 *  @param {Number} index 替换位置
 *  @param {String} chr 替换字符
 */
function replaceChartAt(str, index, chr) {
    if (index > str.length - 1) return str;
    return str.substring(0, index) + chr + str.substring(index + 1);
}

/** 删除字符串中的指定位置的字符
 * @param {String} str 原字符串
 * @param {Number} index 删除位置
 * @param {Number} length 删除的长度
 */
function removeCharAt(str, index, length = 1) {
    return str.slice(0, index) + str.slice(index + length);
}

/**
 * 过滤输入字符串，只保留数字和小数点
 * @param {string|number} input - 需要过滤的输入值
 * @param {string} decimal 小数分隔符
 * @param {string} thounds 千分位分隔符
 * @returns {string} 只包含数字和小数点的字符串
 */
function onlyNumbers(input, decimal, thounds) {
    input = input ? input.toString() : '';
    if( thounds === '.' ){
        input = input.replaceAll(thounds, '@@');
    }
    if( !!decimal && decimal !== '.' ){
        input = input.replaceAll(decimal, '.');
    }
    // 正则说明：\D 匹配非数字，[^\d.] 匹配非数字和非小数点
    input = input.replace(/[^\d.]+/g, '') || '0';
    //保留第一个小数点，去掉第二个小数点及其后面的内容
    return input.replace(/^(\d*\.?\d*).*$/, '$1');
}

/** 将整数部分添加千分位分隔符
 * @param {String} integer 整数部分字符串
 * @param {String} separator 分隔符
 */
function addThousandSeparator(integer, separator) {
    return integer.replace(/(\d)(?=(?:\d{3})+\b)/gm, `$1${separator}`);
}

/** 将文本格式化为指定精度的数字字符串
 * @param {String} string 数字字符串
 * @param {Number} precision 精度，小数点后位数
 * @returns {String} 格式化后的字符串
 */
function roundString(string, precision) {
    const diff = precision - guessFloatPrecision(string);
    if (diff >= 0) {
        return string;
    }

    let firstPiece = string.slice(0, diff);
    const lastPiece = string.slice(diff);

    if (firstPiece.charAt(firstPiece.length - 1) === '.') {
        firstPiece = firstPiece.slice(0, -1);
    }

    if (parseInt(lastPiece.charAt(0), 10) >= 5) {
        for (let i = firstPiece.length - 1; i >= 0; i -= 1) {
            const char = firstPiece.charAt(i);
            if (char !== '.' && char !== '-') {
                const newValue = parseInt(char, 10) + 1;
                if (newValue < 10) {
                    return replaceChartAt(firstPiece, i, newValue);
                }

                firstPiece = replaceChartAt(firstPiece, i, '0');
            }
        }

        return `1${firstPiece}`;
    }
    return firstPiece;
}


/** 将数字字符串转换为货币格式
 * @param {String} input 数字字符串
 * @param {Number} precision 保留小数位数
 * @param {String} decimal 小数点符号
 * @returns {string|string}
 */
function normalizeCurrencyInput(input, precision, decimal = '.') {
    if( !precision || precision < 0 ){
        precision = 0;
    }
    if( precision === 0 ){
        if( input.includes(decimal) ){
            const parts = input.split(".");
            input = parts[0];
        }
    }else{
        if( input.includes(decimal) && !input.endsWith(decimal) ){
            const parts = input.split(".");
            input = `${parts[0]}${decimal}${parts[1].substring(0, precision)}`
        }else{
            input = `${input}${decimal}${'0'.repeat(precision)}`
        }
    }

    return removeLeadingZeros(input);

    // input = input.padStart(precision + 1, '0');
    // return precision === 0 ? input : `${input.slice(0, -precision)}.${input.slice(-precision)}`;
}

/** 计算数字字符串的小数位数
 * @param {String} string 数字字符串
 */
function guessFloatPrecision(string) {
    if( typeof string === 'number')string = string.toString();
    const index = string.indexOf('.');
    return index === -1 ? 0 : (string.length - index - 1)
}

class BigNumber {
    number = 0n;

    decimal = 0;

    /*
    @param {bigint | number | string} number - 可以是 bigint、number 或字符串格式的数字
     */
    constructor(number) {
        this.setNumber(number);
    }

    getNumber() {
        return this.number;
    }

    getDecimalPrecision() {
        return this.decimal;
    }

    setNumber(number) {
        this.decimal = 0;

        if (typeof number === 'bigint') {
            this.number = number;
        } else if (typeof number === 'number') {
            this.setupString(number.toString());
        } else {
            this.setupString(number);
        }
    }

    toFixed(precision = 0, shouldRound = true) {
        let string = this.toString();
        const diff = precision - this.getDecimalPrecision();
        if (diff > 0) {
            // diff bigger than zero pads zeros at the end
            if (!string.includes('.')) {
                // if it is an integer, add a dot
                string += '.';
            }
            return string.padEnd(string.length + diff, '0');
        }
        if (diff < 0) {
            // diff smaller than zero need to be sliced...
            if (shouldRound) {
                // ... and rounded
                return roundString(string, precision);
            }
            return string.slice(0, diff);
        }
        return string;
    }

    toString() {
        let string = this.number.toString();
        if (this.decimal) {
            let isNegative = false;
            if (string.charAt(0) === '-') {
                string = string.substring(1);
                isNegative = true;
            }
            string = string.padStart(string.length + this.decimal, '0');
            string = `${string.slice(0, -this.decimal)}.${string.slice(-this.decimal)}`;
            string = removeLeadingZeros(string);

            return (isNegative ? '-' : '') + string;
        }
        return string;
    }

    lessThan(thatBigNumber){
        const [thisNumber, thatNumber] = this.adjustComparisonNumbers(thatBigNumber);
        return thisNumber < thatNumber;
    }

    biggerThan(thatBigNumber) {
        const [thisNumber, thatNumber] = this.adjustComparisonNumbers(thatBigNumber);
        return thisNumber > thatNumber;
    }

    isEqual(thatBigNumber) {
        const [thisNumber, thatNumber] = this.adjustComparisonNumbers(thatBigNumber);
        return thisNumber === thatNumber;
    }

    setupString(number) {
        number = removeLeadingZeros(number);

        if (isValidInteger(number)) {
            this.number = BigInt(number);
        } else if (isValidFloat(number)) {
            this.decimal = guessFloatPrecision(number);
            this.number = BigInt(number.replace('.', ''));
        } else {
            throw new Error(`BigNumber has received and invalid format for the constructor: ${number}`);
        }
    }

    adjustComparisonNumbers(thatNumberParam){
        let thatNumber;
        if (thatNumberParam.constructor.name !== 'BigNumber') {
            thatNumber = new BigNumber(thatNumberParam);
        } else {
            thatNumber = thatNumberParam;
        }

        const diff = this.getDecimalPrecision() - thatNumber.getDecimalPrecision();

        let thisNum = this.getNumber();
        let thatNum = thatNumber.getNumber();

        if (diff > 0) {
            thatNum = thatNumber.getNumber() * (10n ** BigInt(diff));
        } else if (diff < 0) {
            thisNum = this.getNumber() * (10n ** BigInt(diff * -1));
        }

        return [thisNum, thatNum];
    }
}

/** 将整数和小数部分连接为一个字符串
 * @param {String} integer  整数部分
 * @param {String} decimal  小数部分
 * @param {String} separator 分隔符
 * @return {String} 返回连接后的字符串
 */
function joinIntegerAndDecimal(integer, decimal, separator) {
    return decimal ? integer + separator + decimal : integer;
}

function fixed(precision) {
    return Math.max(0, Math.min(precision, 1000));
}

/** 将输入格式化为货币字符串
 * @param {string | number | null | undefined} input 输入值，可以是字符串、数字、null或undefined
 * @param {Object} options  选项
 * {
 *     round: true, // 是否四舍五入
 *     precision: 2, // 小数点后保留几位
 *     prefix: '¥', // 前缀
 *     suffix: '', // 后缀
 *     thousands: ',', // 千分位符
 *     decimal: '.', // 小数点符号
 *     allowblank: true, // 是否允许空白
 *     disablenegative: false, // 是否允许负数
 *     minimum: null, // 最小值
 *     maximum: null, // 最大值
 *     number: true, // true表示数字类型，false表示字符串类型
 * }
 * @param {String} currency 币种
 * @return {String} 返回格式化后的货币字符串
 */
function formatCurrency(input,  options ={}, currency= 'CNY') {
    const preset = (currency && CurrencyPreset.hasOwnProperty(currency)) ? CurrencyPreset[currency] : {};
    const opt = Object.assign({}, CurrencyProp, preset, options);

    if( !!currency ){
        opt.prefix = opt.prefixUse === 'symbol' ?
            opt.symbol :
            (opt.prefixUse === 'iso' ? opt.iso : opt.prefix);
    }

    if( !validateRestrictedOptions(opt) ){
        return 'error';
    }

    const {round, prefix, suffix, thousands, decimal, allowblank, disablenegative, minimum, maximum, number} = opt;

    const precision = opt.precision === 'auto' ? guessFloatPrecision(onlyNumbers(input)) : opt.precision;

    if (input === null || input === undefined) {
        input = '';
    }else if (typeof input === 'number') {
        if (round) {
            input = input.toFixed(fixed(precision));
        } else {
            input = input.toFixed(fixed(precision) + 1).slice(0, -1);
        }
    // } else if (number && isValidInteger(input)) {
    //     input = Number(input).toFixed(fixed(precision));
    } else if(round && isValidFloat(input) && decimal === '.') {
        input = roundString(input, fixed(precision));
    } else if (!disablenegative && input === '-') {
        return input;
    }

    const negative = disablenegative ? '' : (input.indexOf('-') >= 0 ? '-' : '');

    let filtered = input.replace(prefix, '')
        .replace(suffix, '').
        replace(decimal.repeat(2), decimal);

    if (filtered.startsWith(decimal)) {
        filtered = '0'+filtered;
    }else if(filtered.startsWith(`-${decimal}`)){
        filtered = `-0${decimal}${filtered}`;
    }

    let endsWidthDecimal = false;
    if(filtered.endsWith(decimal)){
        filtered = filtered.slice(0, -1);
        endsWidthDecimal = true;
    }

    const parts = filtered.split(decimal);
    if (parts.length > 1) {
        filtered = `${parts[0]}${decimal}${parts.slice(1).join('')}`;
    }

    // if (!precision && thousands !== '.' && isValidFloat(filtered)) {
    //     filtered = roundString(filtered, 0);
    // }

    let numbers = onlyNumbers(filtered);

    if (round && thousands !== '.') {
        numbers = roundString(numbers, precision || 0);
    }

    //const bigNumber = new BigNumber(negative + normalizeCurrencyInput(numbers, precision, decimal));

    const bigNumber = new BigNumber(negative + normalizeCurrencyInput(numbers, precision, '.'));

    // minimum and maximum must be a valid float or integer
    if (maximum) {
        if (bigNumber.biggerThan(maximum)) {
            bigNumber.setNumber(maximum);
        }
    }
    if (minimum) {
        if (bigNumber.lessThan(minimum)) {
            bigNumber.setNumber(minimum);
        }
    }

    const money = bigNumber.toFixed(fixed(precision), round);

    // test if it is zero 0, or 0.0 or 0.00 and so on...
    if ((/^0(\.0+)?$/g).test(money) && allowblank) {
        return '';
    }

    let [integerStr, decimalStr] = money.split('.');

    // const decimalLength = decimalStr !== undefined ? decimalStr.length : 0;
    //
    // integerStr = integerStr.padStart(minimumNumberOfCharacters - decimalLength, '0');



    integerStr = addThousandSeparator(integerStr, thousands);

    if( opt.precision === 'auto' && endsWidthDecimal ){
        return prefix + integerStr + decimal + suffix;
    }else{
        return prefix
            + joinIntegerAndDecimal(integerStr, decimalStr, decimal)
            + suffix;
    }
}


/** 将输入的货币字符串转换为数字或字符串格式
 * @param {String | Number | null | undefined} input 输入值，可以是字符串、数字、null或undefined
 * @param {Object} options  选项
 * {
 *     round: true, // 是否四舍五入
 *     precision: 2, // 小数点后保留几位
 *     prefix: '¥', // 前缀
 *     suffix: '', // 后缀
 *     thousands: ',', // 千分位符
 *     decimal: '.', // 小数点符号
 *     allowblank: true, // 是否允许空白
 *     disablenegative: false, // 是否允许负数
 *     minimum: null, // 最小值
 *     maximum: null, // 最大值
 *     number: true, // true表示数字类型，false表示字符串类型
 * }
 * @param {String} currency 币种
 * @param{boolean} toString 转成字符串
 * @return {String | Number} 返回数字
 */
function unformatCurrency(input, options={}, currency = 'CNY', toString) {
    const preset = (currency && CurrencyPreset.hasOwnProperty(currency)) ? CurrencyPreset[currency] : {};
    const opt = Object.assign({}, CurrencyProp, preset, options);

    if( !!currency ){
        opt.prefix = opt.prefixUse === 'symbol' ?
            opt.symbol :
            (opt.prefixUse === 'iso' ? opt.iso : opt.prefix);
    }

    if( !validateRestrictedOptions(opt) ){
        return 'error';
    }

    const {round, disablenegative, prefix, suffix, decimal, minimum, maximum, thousands} = opt;

    const precision = opt.precision === 'auto' ? guessFloatPrecision(onlyNumbers(input, decimal, thousands)) : opt.precision;

    if (!disablenegative && input === '-'){
        return input;
    }

    const negative = disablenegative ? '' : (input.indexOf('-') >= 0 ? '-' : '');

    let filtered = input.replace(prefix, '').replace(suffix, '');

    if (filtered.startsWith(decimal)) {
        filtered = '0'+filtered;
    }else if(filtered.startsWith(`-${decimal}`)){
        filtered = `-0${decimal}${filtered}`;
    }

    if(filtered.endsWith(decimal)){
        filtered = filtered.slice(0, -1);
    }

    let numbers = onlyNumbers(filtered, decimal, thousands);

    if (round && thousands !== '.') {
        numbers = roundString(numbers, precision || 0);
    }

    //const bigNumber = new BigNumber(negative + normalizeCurrencyInput(numbers, precision, decimal));

    const bigNumber = new BigNumber(negative + normalizeCurrencyInput(numbers, precision, '.'));

    /// minimum and maximum must be a valid float or integer
    if (maximum) {
        if (bigNumber.biggerThan(maximum)) {
            bigNumber.setNumber(maximum);
        }
    }
    if (minimum) {
        if (bigNumber.lessThan(minimum)) {
            bigNumber.setNumber(minimum);
        }
    }

    let output = bigNumber.toFixed(fixed(precision), round);

    // if (!!toString) {
    //     output = parseFloat(output);
    // }
    //
    // return output;

    return !toString ? parseFloat(output) : output;
}

export {
    CurrencyPreset,
    CurrencyProp,
    formatCurrency,
    unformatCurrency
};
