import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from "./template/tab.scope.css?raw";

const html = `
<div class="content">
    <slot name="content"></slot>
</div>
`
export default class OOTab extends OOComponent{
    static prop = {
        css: '',
        cssLink: '',
        skin: '',
        label: '',
        icon: '',
        rightIcon: ''
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _setPropMap={
        css: ()=>{
            this.useCss(this._props.css);
        },
        cssLink: ()=>{
            this.useCssLink(this._props.cssLink);
        },
        skin: ()=>{
            this._useSkin(this._props.skin);
        },
        label: ()=>{
            if (this.label){
                this.label.querySelector('.text').textContent = this._props.label;
                this.tabs.showSlider();
            }
        },
        icon: (old)=>{
            if (this.label){
                const iconNode = this.label.querySelector('.icon');
                if (this._props.icon){
                    if (!iconNode){
                        this.label.insertAdjacentElement('afterbegin', dom(`div.icon.ooicon-${this._props.icon}`))
                    }else{
                        dom.removeClass(iconNode, `ooicon-${old}`);
                        dom.addClass(iconNode, `ooicon-${this._props.icon}`);
                    }
                }else{
                    if (iconNode) iconNode.remove()
                }
                if (this.tabs) this.tabs.showSlider();
            }
        },
        rightIcon: (old)=>{
            if (this.label){
                const iconNode = this.label.querySelector('.rightIcon');
                if (this._props.rightIcon){
                    if (!iconNode){
                        this.label.insertAdjacentElement('beforeend', dom(`div.rightIcon.ooicon-${this._props.rightIcon}`))
                    }else{
                        dom.removeClass(iconNode, `ooicon-${old}`);
                        dom.addClass(iconNode, `ooicon-${this._props.rightIcon}`);
                    }
                }else{
                    if (iconNode) iconNode.remove()
                }
                if (this.tabs) this.tabs.showSlider();
            }
        }
    }
    constructor() {
        super();
        this._initialize('oo-tab',OOTab, html, css);
    }
    _render(){
        this._fillContent('content');
    }

    label = null;
    tabs = null;
    _connected() {
        this.tabs = dom.getParent(this, 'oo-tabs');
        if (this.tabs) this.#addToTabs();
    }
    _disconnected(){
        if (this.label){
            this.label.remove();
        }
    }
    #addToTabs(){
        if (!this.label) this.#createLabel();
        const prevTab = this.previousElementSibling;
        if (prevTab && prevTab.label){
            prevTab.label.insertAdjacentElement('afterend', this.label);
        }else{
            this.tabs._elements.labels.insertAdjacentElement('afterbegin', this.label);
        }
        this.setAttribute('slot', 'pane');
        if (this!==this.tabs.currentTab) dom.addClass(this, 'hide');
        this.tabs._checkCurrentTab();
    }
    #createLabel(){
        this.label = dom('div.label');
        if (this._props.icon){
            this.label.append(dom(`div.icon.ooicon-${this._props.icon}`));
        }
        this.label.append(dom(`div.text`, {text: this._props.label || 'New Tab'}));
        if (this._props.rightIcon){
            this.label.append(dom(`div.rightIcon.ooicon-${this._props.rightIcon}`));
        }
        this.label.addEventListener('click', ()=>{
            this.tabs.setCurrent(this);
        });
    }

    selected(){
        if (this.label) dom.addClass(this.label, 'current');
        dom.removeClass(this, 'hide');
        this.dispatchEvent(new CustomEvent('current'));
    }

    unselected(){
        if (this.label) dom.removeClass(this.label, 'current');
        dom.addClass(this, 'hide');
        this.dispatchEvent(new CustomEvent('uncurrent'));
    }

}
