export default "* {\r\n    box-sizing: border-box;\r\n}\r\n.content{\r\n    --border: var(--oo-color-text2);\r\n    --check: var(--oo-color-text-white);\r\n    --bg: var(--oo-color-text-white);\r\n    --hover: var(--oo-color-main);\r\n    --checked: var(--oo-color-main);\r\n    --disabled: var(--oo-color-gray2);\r\n    --radius: var(--oo-default-radius);\r\n    --checked-bg: var(--oo-color-text-white);\r\n    --checked-color: var(--oo-color-main);\r\n\r\n    --hover-bg: var(--oo-color-hover);\r\n    --padding: 0;\r\n    --margin: 0;\r\n}\r\nlabel {\r\n    display: flex;\r\n    align-items: center;\r\n    cursor: pointer;\r\n    word-break: break-all;\r\n    transition: background-color 0.2s;\r\n    padding: var(--padding);\r\n    margin: var(--margin);\r\n    position: relative;\r\n}\r\nlabel:hover{\r\n    /*background-color: var(--hover-bg);*/\r\n    color: var(--hover);\r\n}\r\n\r\nlabel.checked{\r\n    /*background-color: var(--checked-bg);*/\r\n}\r\n.abc .label{\r\n    padding: 10px;\r\n}\r\n\r\n.button{\r\n    border-radius: var(--radius);\r\n    width: 1em;\r\n    min-width: 1em;\r\n    max-width: 1em;\r\n    height: 1em;\r\n    border: 0.1em solid var(--border);\r\n    background-color: var(--bg);\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    transition: border 0.2s, color 0.2s, background-color 0.2s;\r\n    color: var(--oo-color-gray-9);\r\n    position: relative;\r\n}\r\n.button:hover{\r\n    border: 0.1em solid var(--hover);\r\n}\r\n.button.checked{\r\n    color: var(--checked-color);\r\n    border: 0.1em solid var(--checked);\r\n    background-color: var(--checked-bg);\r\n}\r\n.button.disabled{\r\n    opacity: 0.4;\r\n    background-color: var(--disabled);\r\n    cursor: not-allowed;\r\n}\r\n\r\n.check-icon{\r\n    width: 1em;\r\n    height: 1em;\r\n    margin: auto;\r\n    position: absolute;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.check{\r\n    border-radius: var(--radius);\r\n    width: 30%;\r\n    height: 30%;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    color: var(--check);\r\n    transition: width 0.2s, height 0.2s, background 0.2s;\r\n}\r\n.check>div{\r\n    scale: 0.5;\r\n}\r\n.check.checked{\r\n    width: 100%;\r\n    height: 100%;\r\n    color: var(--checked-bg);\r\n    background-color: var(--checked);\r\n}\r\n.check.disabled{}\r\n.check.checked.disabled{}\r\n\r\n.label{\r\n    display: flex;\r\n    align-items: center;\r\n    padding-left: 0.3em;\r\n    transition: color 0.2s;\r\n    flex: auto;\r\n}\r\n\r\n.label.checked{\r\n    color: var(--checked-color);\r\n}\r\n.label.disabled{\r\n    opacity: 0.4;\r\n    cursor: not-allowed;\r\n}\r\n.label.readmode.checked{\r\n    color: inherit;\r\n}\r\n.input{\r\n    width: 1px;\r\n    height: 1px;\r\n    overflow: hidden;\r\n    opacity: 0;\r\n    position: absolute;\r\n    bottom: 1px;\r\n    left: 50%;\r\n}\r\n.slot{\r\n    flex: auto;\r\n}\r\nbr{\r\n    height: 0;\r\n}\r\n\r\n/* 移动端，标签在上，输入框下划线 skin-mode='mobile' */\r\n.content.mobile .label>.text{\r\n    color: var(--oo-color-text);\r\n}\r\n/* ---------------------------------------------- */"