import {$CheckGroup} from './$checkGroup';

export default class OOCheckGroup extends $CheckGroup {
    constructor() {
        super();
        this.#setPropMap();
        this._init('oo-checkbox-group', OOCheckGroup);
    }

    #setPropMap() {
        this._setPropMap = Object.assign(this._setPropMap, {
            value: () => {
                this._props.values = (this._props.value) ? this._props.value.split(/\s*,\s*/g) : [];
                this._setPropMap.values();
                this.rerender();
            },
            values: () => {
                if (this._props.values && this._props.values.length) {
                    for (const item of this._items) {
                        if (this._props.values.includes(item.value)) {
                            item.checked = true;
                            vs.push(item.value);
                        } else {
                            item._unchecked();
                        }
                    }
                } else {
                    for (const item of this._items) {
                        item._unchecked();
                    }
                }
                this._elements.input.value = this._props.values?.join(',') || '';
                this._fillEmptyReadmode();
            }
        });
    }

    #getValue() {
        const v = [];
        this._items.forEach((item)=>{
            if (item.checked){
                v.push(item.value)
            }
        });
        return v;
        // return this._props.values || [];
    }

    #getText() {
        const v = [];
        this._items.forEach((item) => {
            if (item.checked) {
                v.push(item.text);
            }
        });
        return v;
    }

    get value() {
        return this.#getValue();
    }

    set value(v) {
        if (Array.isArray(v)){
            if (this._props.values !== v) {
                this._props.values = v;
                this._setPropMap.values();
            }
        }else{
            if (this._props.value !== v) {
                this._props.value = v;
                this._setPropMap.value();
            }
        }
    }

    get text() {
        return this.#getText();
    }

}
