import {$CheckGroup} from './$checkGroup';
import {dom} from '../../../../o2oa-util';

export default class OORadioGroup extends $CheckGroup {
    constructor() {
        super();
        this.#setPropMap();
        this._init('oo-radio-group');
    }

    #setPropMap() {
        this.setPropMap = Object.assign(this._setPropMap, {
            value: () => {
                if (this._props.value) {
                    for (const item of this._items) {
                        if (item.value === this._props.value) {
                            item.setAttribute('checked', true);
                            break;
                        }
                    }
                }
                this._elements.input.value = this._props.value;
                this._fillEmptyReadmode();
            }
        });
    }

    _appendItemCheckValue(item) {
        if (this._props.value) {
            if (item.value === this._props.value) {
                item.setAttribute('checked', true);
            }
        } else {
            item._unchecked();
        }
    }

    _itemChange(e) {
        if (e.currentTarget.checked) {
            this.value = e.currentTarget.value;
        } else {
            this.value = '';
        }
        this._elements.input.value = this.value;
        this.unInvalidStyle();
        // this.dispatchEvent(new Event('change', e));
    }

    _removeItem(item) {
        // if (item.checked) this.value = '';
        super._removeItem(item);
    }

    #getText() {
        for (const item of this._items) {
            if (item.checked) return item.text;
        }
        return '';
    }

    get text() {
        return this.#getText();
    }

    // get value(){
    //     return this._props.value;
    // }
    // set value(v){
    //     if (this._props.value!==v){
    //         this._props.value = v;
    //         this._setPropMap.value();
    //     }
    // }
}
