import {dom, hyphenate, capitalize} from "@o2oa/util";
import '../web-component/feedback/notice.js';

class Notice{
    static env = {
        notices: [],
        height: 0
    };
    options = {
        title: '',
        text: '',
        icon: '',
        type: 'info',
        skin: 'default',
        style: '',
        showClose: true,
        contentAlign: '',
        contentValign: '',
        width: '',
        height: '',
        container: '',
        duration: 5000,
        location: 'topCenter',
        position: 'absolute',
        marginTop: 10,
        marginBottom: 10,
        marginLeft: 10,
        marginRight: 10,
    };
    where = {
        x: 'center',
        y: 'top'
    };
    location = {
        fromTop: null,
        toTop: null,
        fromBottom: null,
        toBottom: null,
        fromLeft: null,
        toLeft: null,
        fromRight: null,
        toRight: null
    }

    constructor(options) {
        this.options = Object.assign(this.options, options);
        this.container = (this.options.container) ? (dom.el(this.options.container) || document.body) : document.body;
        const position = dom.isBody(this.container) ? 'fixed' : (this.options.position || 'absolute');
        this.node = dom('oo-notice.'+this.options.skin+((position==='fixed') ? '.fixed' : '.absolute'));
        ['title', 'text', 'icon', 'type', 'skin', 'style', 'showClose', 'contentAlign', 'contentValign', 'width', 'height'].forEach((p)=>{
            this.node.setAttribute(p, this.options[p]);
        });
        this.container.append(this.node);

        this.node.addEventListener('skinLoaded', () => {
            this.#showNotice();
        });
    }
    resetPosition(height){
        const offset = height + this.options.marginTop + this.options.marginBottom;
        const w = capitalize(this.where.y);
        this.location[`to${w}`] = this.location[`to${w}`] - offset;
        this.location[`from${w}`] = this.location[`from${w}`] - offset;
        const css = {};
        css[this.where.y] = this.location[`to${w}`]+'px';
        this.#setNodeStyle(css, true);
    }
    #positionFrom(){
        if (!Notice.env.notices.includes(this)){
            Notice.env.notices.push(this);
            this.#initPosition();
            if (this.node._elements.close){
                this.node._elements.close.addEventListener('click', ()=>{
                    this.#hideNotice();
                });
            }
        }
        return this.#locationFrom();
    }
    #locationFrom(trans){
        return this.#setNodeStyle({
            top: this.location.fromTop+'px',
            left: this.location.fromLeft+'px',
            bottom: this.location.fromBottom+'px',
            right: this.location.fromRight+'px',
            opacity: 0
        }, trans);
    }
    #locationTo(trans){
        return this.#setNodeStyle({
            top: this.location.toTop + 'px',
            left: this.location.toLeft+'px',
            bottom: this.location.toBottom+'px',
            right: this.location.toRight+'px',
            opacity: '1'
        }, trans);
    }
    async #showNotice() {
        await this.#positionFrom();
        const e = await this.#locationTo(true);
        if (e && this.options.duration){
            setTimeout(()=>{this.#hideNotice()}, this.options.duration || 3000);
        }
    }
    async #hideNotice() {
        if (!this.isHidden){
            this.isHidden = true;
            this.#hideNoticeFromEnv();
            const e = await this.#locationFrom(true);
            if (e && e.propertyName === 'opacity'){
                this.node.remove();
                this.node = null;
            }
        }
    }
    #hideNoticeFromEnv(){
        const idx = Notice.env.notices.indexOf(this);
        const w = capitalize(this.where.y);
        const height = Math.abs(this.location[`to${w}`] - this.location[`from${w}`]);
        Notice.env.height = Notice.env.height - height - this.options.marginTop - this.options.marginBottom;
        for (let i = idx+1; i<Notice.env.notices.length; i++){
            Notice.env.notices[i].resetPosition(height);
        }
        Notice.env.notices.splice(idx, 1);
    }
    #setNodeStyle(style, trans){
        return new Promise((resolve)=>{
            if (trans){
                dom.addClass(this.node, 'transition');
                const transitionend = (e)=>{
                    dom.removeClass(this.node, 'transition');
                    this.node.removeEventListener("transitionend", transitionend);
                    resolve(e);
                }
                this.node.addEventListener("transitionend", transitionend);
                 setTimeout(()=>{
                    dom.setStyles(this.node, style);
                }, 10);

            }else{
                dom.removeClass(this.node, 'transition');
                setTimeout(()=>{
                    dom.setStyles(this.node, style);
                    resolve();
                }, 10);
            }
            
        });
    }
    #initPosition(){
        const ps = this.#parsePosition();
        const referNode = dom.isBody(this.container) ? document.documentElement : this.container
        const position = this.#getReferPosition(referNode);
        this.#initPositionVertical[ps.y](referNode, position);
        this.#initPositionHorizontal[ps.x](referNode, position);
    }
    #getReferPosition(referNode, v, h){
        const subElement = dom('div');
        referNode.append(subElement);
        const positionElement = dom.getOffsetParent(subElement);
        subElement.remove();
        return (positionElement === referNode) ? {x: 0, y: 0} : (()=>{
            return dom.getPosition(referNode, dom.getOffsetParent(referNode))
        })();
    }
    #initPositionVertical = {
        bottom: (referNode, position)=>{
            // const containerSize = dom.getSize(referNode);
            // const bodySize = dom.getSize(document.documentElement);

            const size = dom.getSize(this.node);
            // this.location.toBottom = Notice.env.height + this.options.marginTop + (bodySize.y-containerSize.y-position.y);
            // this.location.fromBottom = this.location.toBottom - size.y;

            this.location.toBottom = Notice.env.height + this.options.marginTop + this.options.marginBottom + position.y
            // (bodySize.y-containerSize.y-position.y);
            this.location.fromBottom = this.location.toBottom - size.y;

            Notice.env.height += (size.y + this.options.marginTop + this.options.marginBottom);
        },
        top: (referNode, position)=>{
            const size = dom.getSize(this.node);
            this.location.toTop = Notice.env.height + this.options.marginTop + position.y;
            this.location.fromTop = this.location.toTop - size.y;
            Notice.env.height += (size.y + this.options.marginTop + this.options.marginBottom);
        },
        middle:()=>{}
    }
    #initPositionHorizontal = {
        left: (referNode, position)=>{
            this.location.fromLeft = this.location.toLeft = position.x + this.options.marginLeft + this.options.marginRight;
        },
        right: (referNode, position)=>{
            const containerSize = dom.getSize(referNode);
            const pSize = dom.getSize(dom.getOffsetParent(referNode));
            const right = pSize.x - containerSize.x - position.x
            // const bodySize = dom.getSize(document.documentElement);
            // this.location.fromRight = this.location.toRight = position.x + this.options.marginLeft + this.options.marginRight;
            this.location.fromRight = this.location.toRight = right + this.options.marginRight;
        },
        center: (referNode, position)=>{
            if (dom.isHtml(referNode)){
                dom.addClass(this.node, 'body-center');
            }else{
                dom.removeClass(this.node, 'body-center');

                const containerSize = dom.getSize(referNode);
                const size = dom.getSize(this.node);

                this.location.fromLeft = this.location.toLeft = position.x + containerSize.x/2 - size.x/2;
            }
        }
    }
    #parsePosition(){
        const p = {left: 'x', right: 'x', center: 'x', top: 'y', bottom: 'y', middle: 'y'};
        hyphenate(this.options.location).split('-').forEach((key)=>{
            if (p[key]) this.where[p[key]] = key;
        });
        return this.where;
    }
}


/**消息提醒框
 * @Object notice
 */
const notice = {
    /**显示错误消息
     * @method error
     * @param {String} title 错误标题
     * @param {String} text  错误描述
     * @param {Object} options  消息框显示参数
     * @param {boolean} normal 是否使用默认样式
     */
    error: (title, text, options= {}, normal)=>{
        const normalStyle =  {skin: 'default', location: 'topRight', marginTop: 10, duration: 5000};
        if (normal) options = Object.assign(options, normalStyle);
        const opts = Object.assign({
            title, text, duration: 0, skin: 'banner', marginTop: 0, type: 'error'
        }, options);
        new Notice(opts);

        let e;
        // if (opts.err && Error.prototype.isPrototypeOf(opts.err)){
        //     e = opts.err;
        // }else{
        e = new Error(text, {cause: opts.err});
        e.name = title;
        // }
        throw e;
    },


    /**显示消息
     * @method msg
     * @param {String} title 消息标题
     * @param {String} text  消息描述
     * @param {String} type  消息类型 error success info warn
     * @param {Object} options 消息框显示参数
     */
    msg: (title, text, type, options = {})=>{
        const opts = Object.assign({  title, text, duration: 5000, type, location: 'topRight', marginTop: 10}, options);
        new Notice(opts);
    },

    /**显示成功消息
     * @method msg
     * @param {String} title 消息标题
     * @param {String} text  消息描述
     * @param {Object} options 消息框显示参数
     */
    failed:  (title, text, options= {} )=>{
        const opts = Object.assign({duration: 8000}, options);
        notice.msg(title, text, 'error', opts);
    },

    /**显示成功消息
     * @method msg
     * @param {String} title 消息标题
     * @param {String} text  消息描述
     * @param {Object} options 消息框显示参数
     */
    success:  (title, text, options= {} )=>{
        notice.msg(title, text, 'success', options);
    },

    /**显示普通消息
     * @method msg
     * @param {String} title 消息标题
     * @param {String} text  消息描述
     * @param {Object} options 消息框显示参数
     */
    info:  (title, text, options = {})=>{
        notice.msg(title, text, 'info', options);
    },

    /**显示警告消息
     * @method msg
     * @param {String} title 消息标题
     * @param {String} text  消息描述
     * @param {Object} options 消息框显示参数
     */
    warn:  (title, text, options = {})=>{
        notice.msg(title, text, 'warn', options);
    }
}

export {Notice, notice};

