export default ".content{\r\n    --option-bg: var(--oo-color-text-white);\r\n    --option-over: var(--oo-color-text2);\r\n    --option-over-bg: var(--oo-color-gray-e);\r\n    --option-cascade-focus-bg: var(--oo-color-hover);\r\n    --option-select: var(--oo-color-main);\r\n    --option-select-bg: var(--oo-color-text-white);\r\n    --option-shadow-color: var(--oo-color-gray-d);\r\n    --option-shadow:  0 0 10px 1px var(--option-shadow-color);\r\n    --option-border: var(--oo-color-gray-d);\r\n    --option-selected: var(--oo-color-main);\r\n    --option-selected-bg: var(--oo-color-light);\r\n    --option-group-bg: var(--oo-color-text-white);\r\n    --option-group-color: var(--oo-color-gray-9);\r\n}\r\n.content {\r\n    height: 100%;\r\n    width: 100%;\r\n    /*padding: 2px;*/\r\n    position: static;\r\n}\r\n.labelArea {\r\n    display: flex;\r\n    align-items: center;\r\n    position: static;\r\n    height: 100%;\r\n}\r\n.drop{\r\n    text-align: left;\r\n    transition: color 0.2s;\r\n    font-size: 0.8em;\r\n    margin-right: 0.5em;\r\n    color: var(--label);\r\n    cursor: pointer;\r\n    transform-origin: center;\r\n}\r\n.drop.down{\r\n    transform: rotate(180deg);\r\n}\r\n.readmode .drop{\r\n    display: none;\r\n}\r\n\r\n.box{\r\n    position: relative;\r\n}\r\n.box.focus .drop{\r\n    color: var(--focus);\r\n}\r\n.box.disabled .drop.disabled{\r\n    opacity: 0.6;\r\n    cursor: not-allowed;\r\n    color: var(--label);\r\n}\r\n.box.readmode.focus{\r\n    color: var(--label);\r\n}\r\n.arrow{\r\n    position: absolute;\r\n    top: -6px;\r\n    width: 10px;\r\n    height: 10px;\r\n    z-index: -1;\r\n}\r\n.arrow:before {\r\n    border-bottom-color: transparent!important;\r\n    border-right-color: transparent!important;\r\n    border-top: 1px solid var(--option-border);\r\n    border-left: 1px solid var(--option-border);\r\n    border-top-left-radius: 3px;\r\n    background-color: var(--option-bg);\r\n    right: 0;\r\n    position: absolute;\r\n    width: 10px;\r\n    height: 10px;\r\n    z-index: -1;\r\n    content: \" \";\r\n    transform: rotate(45deg);\r\n}\r\n\r\n.options-area{\r\n    position: absolute;\r\n    border: 1px solid var(--option-border);\r\n    background-color: var(--option-bg);\r\n    border-radius: var(--oo-area-radius);\r\n    box-shadow: var(--option-shadow);\r\n    z-index: 100;\r\n    /*min-width: 10em;*/\r\n    transform-origin: top;\r\n    transition: height 0.2s, opacity 0.2s, transform 0.2s;\r\n}\r\n.options-area.visible{\r\n    transform: scale(1);\r\n    opacity: 1;\r\n}\r\n.options-area.invisible{\r\n    transform: scale(1, 0.001);\r\n    opacity: 0;\r\n}\r\n.options-content{\r\n    max-height: 17em;\r\n    min-height: 3em;\r\n    padding: 0.5em 0;\r\n    border-radius: var(--oo-area-radius);\r\n    overflow: auto;\r\n}\r\n\r\n.options-content::-webkit-scrollbar {\r\n    width: 8px;\r\n    height: 8px;\r\n    border-radius: 8px;\r\n    background-color: transparent;\r\n}\r\n.options-content::-webkit-scrollbar-thumb{\r\n    width: 8px;\r\n    border-radius: 8px;\r\n    background-color: transparent;\r\n    cursor: pointer;\r\n    opacity: 0;\r\n}\r\n.options-content:hover::-webkit-scrollbar-thumb{\r\n    background-color: #dddddd;\r\n}\r\n.options-content:hover::-webkit-scrollbar-thumb:hover{\r\n    background-color: #cccccc;\r\n}\r\n\r\n.view{\r\n    position: relative;\r\n    overflow: hidden;\r\n}\r\n.input{\r\n    width: 100%;\r\n    cursor: pointer;\r\n    text-overflow: ellipsis;\r\n}\r\n.readmode .input {\r\n     display: none;\r\n    cursor: default;\r\n}\r\n.viewText{\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n    display: none;\r\n    /*align-items: center;*/\r\n    top: 0;\r\n    left: 0;\r\n    overflow: hidden;\r\n    word-break: keep-all;\r\n    text-overflow: ellipsis;\r\n    cursor: pointer;\r\n    padding: 0.5em 0.6em;\r\n}\r\n.readmode .viewText{\r\n    position: static;\r\n}\r\n.disabled .viewText{\r\n    cursor: not-allowed;\r\n    opacity: 0.