export default "* {\r\n    box-sizing: border-box;\r\n}\r\n.content{\r\n    --label: var(--oo-color-text2);\r\n    --icon-left: var(--oo-color-text2);\r\n    --icon-right: var(--oo-color-text2);\r\n    --focus: var(--oo-color-main);\r\n    --border: var(--oo-color-gray1);\r\n    --radius: var(--oo-area-radius);\r\n    --input: var(--oo-color-text);\r\n    --placeholder: var(--oo-color-text4);\r\n    --disabled: var(--oo-color-gray2);\r\n}\r\ntextarea{\r\n    resize: none;\r\n    font-size: 1em;\r\n    height: 5em;\r\n    min-height: 2em;\r\n}\r\ntextarea.resize{\r\n    resize: both;\r\n}\r\ntextarea.autosize{\r\n    resize: none;\r\n    overflow: hidden;\r\n}\r\ntextarea::-webkit-scrollbar {\r\n    width: 8px;\r\n    height: 8px;\r\n    border-radius: 8px;\r\n    background-color: var(--oo-color-gray-d);\r\n}\r\n\r\ntextarea::-webkit-scrollbar-thumb {\r\n    width: 8px;\r\n    border-radius: 8px;\r\n    background-color: var(--oo-color-gray-b);\r\n    cursor: pointer;\r\n}\r\n\r\ntextarea::-webkit-scrollbar-thumb:hover {\r\n    width: 8px;\r\n    border-radius: 8px;\r\n    background-color: var(--oo-color-text2);\r\n    cursor: pointer;\r\n}\r\nlabel {\r\n    display: flex;\r\n    position: relative;\r\n    height: 100%;\r\n    align-items: center;\r\n}\r\n.requiredFlag{\r\n    color: var(--oo-color-highlight);\r\n    padding-left: 0.3em;\r\n    font-size: 0.75em;\r\n}\r\n\r\n/* 移动端，标签在上，输入框下划线 skin-mode='mobile' */\r\n.content.mobile>label {\r\n    display: flex !important;\r\n    align-items: unset !important;\r\n    position: relative !important;\r\n    height: unset !important;\r\n    flex-direction: column;\r\n}\r\n\r\n.content.mobile .labelText {\r\n    width: unset !important;\r\n    min-width: unset !important;\r\n    max-width: unset !important;\r\n    display: block !important;\r\n}\r\n.content.mobile .input {\r\n    padding: 0.5em 0.35em;\r\n    font-size: 1.125em;\r\n\r\n}\r\n.content.mobile .viewText {\r\n    padding: 0.5em 0.35em;\r\n    font-size: 1.125em;\r\n    background-color: transparent !important;\r\n}\r\n/* ---------------------------------------------- */"