import {dom, defer, hyphenate} from '@o2oa/util';
import {$Input} from './$input.js';
import {notice} from '../../api/notice.js';

class OOFiles extends $Input {
    static prop = {
        type: 'file', //file
    };
    static get observedAttributes() {
        OOFiles.prop = Object.assign({}, $Input.prop, OOFiles.prop);
        return Object.keys(OOFiles.prop).map((att) => {
            return hyphenate(att);
        });
    }

    constructor() {
        super();
        this.setAttribute('type', 'file');
        this.#setPropMap();
        this._init('oo-files');
    }

    #setPropMap() {
        this._setPropMap = Object.assign(this._setPropMap, {
            col: () => {
                const i = this._props.col ? parseInt(this._props.col) : 2;
                dom.setStyles(this._elements.fileList, {
                    'grid-template-columns': `repeat(${i}, 1fr)`,
                });
            },
            type: () => {
                this._checkTypeFile();
            },
            del: (old) => {
                if (old !== this._props.del) {
                    this._elements.fileList.querySelectorAll('oo-file').forEach((file) => {
                        if (this._props.del) {
                            file.setAttribute('del', 'true');
                        } else {
                            file.setAttribute('del', 'false');
                        }
                    });
                }
            },
            size: () => {}, //单个附件大小
            min: () => {},
            max: () => {},
            value: () => {},
        });
    }
    _inputInput() {
        this._elements.input.addEventListener('input', () => {
            this.unInvalidStyle();
        });
    }

    _fileInputChange(){
        if (this._elements.fileInput) this._elements.fileInput.addEventListener('change', (e) => {
            //type=file时，选择文件后触发
            this._canValidityBlur = true;
            this.unInvalidStyle();
            this._changeFileValue(e);
        });
    }

    _setEvent() {
        super._setEvent();
        this.addEventListener('validity', (e) => {
            if (this._props.min) {
                if (this.files.length < this._props.min) {
                    e.target.setCustomValidity(`文件数量不能少于${this._props.min} `);
                }
            }
            // if (this._props.max){
            //     if (this.files.length > this._props.max) {
            //         e.target.setCustomValidity(`文件数量不能超过${this._props.max} `);
            //     }
            // }
        });
    }
    #fileList = [];
    _changeFileValue(e) {
        if (this._props.type === 'file') {
            if (e.target.files.length) {
                for (const file of e.target.files) {
                    const i = this.#fileList.findIndex((f) => {
                        return f.name === file.name;
                    });
                    if (i === -1) this.addFile(file);
                }
            }
            //清空<input>元素，避免无法选择同名文件
            this._elements.fileInput.value = '';
        }
    }

    _removeFile(file, fileNode) {
        if (this.removeFile) {
            //为oo-files对象绑定 remove 方法以实现将附件从服务器端删除
            return this.removeFile(file, fileNode);
        }
        return Promise.resolve();
    }

    _remove(fileNode) {
        const file = fileNode.file;
        return this._removeFile(file, fileNode)
            .then(() => {
                const i = this.#fileList.findIndex((f) => {
                    return f.name === file.name;
                });
                if (i != -1) this.#fileList.splice(i, 1);
                this._elements.input.value = this.#fileList.map((f) => f.name).join(', ');

                this.dispatchEvent(
                    new CustomEvent('removefile', {
                        detail: {file, fileNode},
                    }),
                );
                this.dispatchEvent(
                        new CustomEvent('change', {
                            detail: {file, fileNode},
                        }),
                    );

                if (!this.#fileList.length) {
                    dom.show(this._elements.emptyInfo);
                }
                this.checkValidity();
            })
            .catch((e) => {
                notice.failed('文件删除错误', `${e.message}`, {
                    container: this._elements.box,
                });
                fileNode.setAttribute('status', 'warn');
                throw e;
            });
    }

    #noticeFIleLength(max) {
        notice.warn('文件数量限制', `文件数量不能超过${max}个`, {
            container: this._elements.box,
        });
    }
    #noticeFIleSize(max) {
        notice.warn('文件大小限制', `单个文件大小不能超过${max}M`, {
            container: this._elements.box,
        });
    }

    //检查是否可以添加文件
    _checkFile(file) {
        if (!file.name || !file.size || (!file.url && !(file instanceof File))) {
            return false;
        }

        const size = this._props.size ? parseFloat(this._props.size) : 0;
        if (size && file.size > size * 1024 * 1024) {
            defer(this.#noticeFIleSize, 20, this, [this._props.size]);
            return false;
        }

        const max = this._props.max ? parseInt(this._props.max) : 0;
        if (max && this.#fileList.length >= max) {
            defer(this.#noticeFIleLength, 20, this, [max]);
            return false;
        }

        return true;
    }

    _uploadFile(file, fileNode) {
        if (this.uploadFile && file instanceof File) {
            //为oo-files对象绑定 upload 方法以实现将附件上传到服务器
            return this.uploadFile(file, fileNode);
        }
        return Promise.resolve();
    }

    //添加文件
    addFile(file, node) {
        if (this._checkFile(file)) {
            const fileNode =
                node ||
                dom('oo-file', {
                    filename: file.name,
                    size: file.size,
                    url: file.url ?? '',
                    'preview-url': file.previewUrl ?? '',
                    preview: true,
                });
            const isDel = !(this._props.readonly || this._props.readmode || this._props.disabled) && this._props.del;
            fileNode.setAttribute('del', isDel);
            fileNode.files = this;
            fileNode.file = file;
            this._elements.fileList.append(fileNode);

            this.unInvalidStyle();
            if (this._elements.emptyInfo) dom.hide(this._elements.emptyInfo);
            if (this._elements.fileEmptyFlag){
                dom.hide(this._elements.fileEmptyFlag);
            } 

            return this._uploadFile(file, fileNode)
                .then((f) => {
                    this.#fileList.push(file);
                    this._elements.input.value = this.#fileList.map((f) => f.name).join(', ');

                    this.dispatchEvent(
                        new CustomEvent('addfile', {
                            detail: {file, fileNode},
                        }),
                    );
                    this.dispatchEvent(
                        new CustomEvent('change', {
                            detail: {file, fileNode},
                        }),
                    );
                    if (this._props.validityBlur && this._canValidityBlur) {
                        defer(this.checkValidity, 100, this);
                    }
                })
                .catch((e) => {
                    notice.failed('文件上传错误', `${e.message}`, {
                        container: this._elements.box,
                    });
                    fileNode.setAttribute('status', 'error');
                    throw e;
                });
        }
    }

    get files() {
        return this.#fileList.map((file) => {
            return {
                id: file.id,
                url: file.url,
                previewUrl: file.previewUrl,
                lastModified: file.lastModified,
                lastModifiedDate: file.lastModifiedDate,
                name: file.name,
                size: file.size,
                type: file.type,
            };
        });
    }
    set files(files) {
        if (files && Array.isArray(files)) {
            this.#fileList = [];
            this._elements.fileList.querySelectorAll('oo-file').forEach((fileNode) => {
                fileNode.remove();
            });
            files.forEach((file) => {
                this.addFile(file);
            });
        }
    }
    get value() {
        return this.files;
    }
    set value(files) {
        this.files = files;
    }
}
export default OOFiles;
