import {hyphenate, dom, openWindow} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from './template/file.scope.css?raw';
import iconJson from './file.json';
import {confirm} from '../../api/dialog.js';
import {notice} from '../../api/notice.js';
import {I18n} from '../../common/i18n.js';

const html = `
<div class="content normal">
    <div class="area">
        <div class="icon">
            <oo-icon></oo-icon>
        </div>
        <div class="filename">
            <div class="name"></div>
            <div class="size"></div>
        </div>
        <div class="actions">
            <oo-button class="del" type="icon" left-icon="delete"></oo-button>

        </div>
<!--   <div class="preview hide">预览</div>-->
    </div>
    <progress class="progress" max="100" value="0"></progress>
</div>
`;
export default class OOFile extends OOComponent {
    static prop = {
        filename: '',
        size: '',
        preview: true,
        previewUrl: '',
        download: true,

        url: '',
        cssLink: '',
        skin: '',
        del: false,
        status: 'normal', // normal, uploading, error, warn
        progress: 0,
        delComfirm: '',
        delTitle: '',
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att) => {
            return hyphenate(att);
        });
    }
    _elements = {
        icon: null,
        name: null,
        size: null,
        preview: null,
        del: null,
        progress: null,
    };
    _setPropMap = {
        progress: () => {
            this._elements.progress.value = this._props.progress || 0;
        },
        status: () => {
            this._content.className = `content ${this._props.status}`;
        },
        filename: () => {
            this.#deferRender();
        },
        size: () => {
            this.#deferRender();
        },
        url: () => {
            this.#deferRender();
        },
        preview: () => {
            this.#deferRender();
        },
        cssLink: () => {
            this.useCssLink(this._props.cssLink);
        },
        skin: () => {
            this._useSkin(this._props.skin);
        },
        del: () => {
            dom.checkClass(this._elements.del, 'hide', !this._props.del);
        },
        previewUrl: () => {},
        download: () => {},
    };
    constructor() {
        super();
        const lp = I18n.get('index').o('component');
        ['delTitle', 'delComfirm'].forEach((k) => (OOFile.prop[k] = lp[k]));
        this._initialize('oo-file', OOFile, html, css);
    }

    #deferRenderTimer = null;
    #deferRender() {
        if (!this.#deferRenderTimer) {
            setTimeout(() => {
                this._render();
                this.#deferRenderTimer = null;
            }, 5);
        }
    }
    async _render() {
        if (this._props.filename) {
            const size = this._props.size ? this.#formatSize(parseFloat(this._props.size)) : '';

            this.extension = this._props.filename.substring(this._props.filename.lastIndexOf('.') + 1);

            dom.empty(this._elements.icon);
            const img = dom('oo-icon');
            img.setAttribute('type', iconJson[this.extension] || iconJson['unknown']);
            this._elements.icon.append(img);
            this._elements.name.textContent = this._props.filename;
            this._elements.size.textContent = size;
            dom.checkClass(this._elements.del, 'hide', !this._props.del);
        }
    }
    _setEvent() {
        super._setEvent();
        this._elements.icon.addEventListener('click', (e) => {
            e.stopPropagation();
            if ((this._props.preview && this._props.url) || this._props.previewUrl) {
                this.#preview();
            }
        });

        this._elements.del.addEventListener('click', (e) => {
            e.stopPropagation();
            if (this._props.status === 'error') {
                this._remove();
            } else {
                const content = this._props.delComfirm.replace('{name}', this._props.filename);
                confirm.info(this._props.delTitle, content, this._content, this._content).then((d) => {
                    if (d.status === 'ok') this._remove();
                    d.dlg.close();
                });
            }
        });

        this._elements.name.addEventListener('click', (e) => {
            if (this._props.download && this._props.url) {
                openWindow(this._props.url);
            }
        });
    }

    _removeFile() {
        if (this.files) {
            return this.files._remove(this);
        }
        return Promise.resolve();
    }
    _remove() {
        this._removeFile().then(()=>{
            this.dispatchEvent(new CustomEvent('remove'));
            this.remove();
        });
    }

    #preview() {
        const previewType = this.#isImage(this.extension)
            ? 'Image'
            : this.#isVideo(this.extension)
            ? 'Video'
            : this.#isAudio(this.extension)
            ? 'Audio'
            : '';

        if (previewType) {
            const maskNode = dom('div.preview_mask');
            const previewArea = dom('div.preview_area');

            const p = dom.getPosition(this._elements.icon);
            dom.setStyles(maskNode, {
                top: p.y + 'px',
                left: p.x + 'px',
            });
            dom.setStyles(previewArea, {
                top: p.y + 'px',
                left: p.x + 'px',
            });

            document.body.append(maskNode);
            document.body.append(previewArea);

            setTimeout(() => {
                dom.addClass(maskNode, 'show');
                dom.addClass(previewArea, 'show');
            }, 5);

            previewArea.addEventListener('click', () => {
                maskNode.remove();
                previewArea.remove();
            });

            this[`_preview${previewType}`](previewArea);
        } else if (this._props.previewUrl) {
            openWindow(this._props.previewUrl);
        }
    }
    _previewImage(previewArea) {
        const previewNode = dom('img.preview');
        previewNode.addEventListener('click', (e) => {
            e.stopPropagation();
        });
        previewArea.append(previewNode);
        previewNode.src = this._props.previewUrl || this._props.url;
    }
    _previewVideo(previewArea) {
        const previewNode = dom('video.preview', {controls: 'play'});
        previewNode.addEventListener('click', (e) => {
            e.stopPropagation();
        });
        previewArea.append(previewNode);
        previewNode.src = this._props.previewUrl || this._props.url;
    }
    _previewAudio(previewArea) {
        const previewNode = dom('audio.preview', {controls: 'play'});
        previewNode.addEventListener('click', (e) => {
            e.stopPropagation();
        });
        previewArea.append(previewNode);
        previewNode.src = this._props.previewUrl || this._props.url;
    }

    #isImage(extension) {
        return [
            'apng',
            'avif',
            'bmp',
            'gif',
            'ico',
            'cur',
            'jpg',
            'jpeg',
            'jfif',
            'pjpeg',
            'pjp',
            'png',
            'svg',
            'tif',
            'tiff',
            'webp',
        ].includes(extension.toString().toLowerCase());
    }
    #isVideo(extension) {
        return ['avi', 'mkv', 'mov', 'ogg', 'mp4', 'mpa', 'mpe', 'mpeg', 'mpg', 'rmvb', 'rm'].includes(extension.toString().toLowerCase());
    }
    #isAudio(extension) {
        return ['mp3', 'wav', 'wma', 'wmv'].includes(extension.toString().toLowerCase());
    }
    #formatSize(bytes) {
        if (!bytes) return '';
        const units = ['B', 'KB', 'MB', 'GB'];
        let unitIndex = 0;

        while (bytes >= 1024 && unitIndex < units.length - 1) {
            bytes /= 1024;
            unitIndex++;
        }

        return bytes.toFixed(2) + ' ' + units[unitIndex];
    }
}
