import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from "./template/menuItem.scope.css?raw";

const html = `
<div class="menuItem content enabled">
    <slot name="left"></slot>
    <slot name="item"></slot>
    <slot name="right"></slot>
</div>
`
class OOMenuItem extends OOComponent{
    static prop = {
        css: '',
        cssLink: '',
        skin: '',
        icon: '',
        label: '',
        type: 'command', //command, checkbox, radio, hr
        disabled: false,
        checked: false,
        radiogroup: ''
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _elements={};
    _setPropMap={
        css: ()=>{
            this.useCss(this._props.css);
        },
        cssLink: ()=>{
            this.useCssLink(this._props.cssLink);
        },
        skin: ()=>{
            this._useSkin(this._props.skin);
        },
        label: ()=>{
            switch (this._props.type) {
                case 'command':
                    if (this._elements.label) this._elements.label.textContent = this._props.label;
                    break;
                case 'checkbox':
                case 'radio':
                    if (this._elements.input) this._elements.input.text = this._props.label;
                    break;
                case 'hr':
                    //nothing
                default:
                    //nothing
            }
        },
        disabled: ()=>{
            dom.removeClass(this._content, 'disabled');
            dom.addClass(this._content, 'enabled');
            switch (this._props.type) {
                case 'command':
                    if (!!this._props.disabled){
                        dom.removeClass(this._content, 'enabled');
                        dom.addClass(this._content, 'disabled');
                    }
                    break;
                case 'checkbox':
                case 'radio':
                    this._elements.input.disabled = this._props.disabled;
                    if (!!this._props.disabled){
                        dom.removeClass(this._content, 'enabled');
                    }
                    break;
                case 'hr':
                //nothing
                default:
                //nothing
            }
        },
        icon: (old)=>{
            if (this._elements.icon){
                dom.removeClass(this._elements.icon, `ooicon-${old}`);
                if (this._props.icon) dom.addClass(this._elements.icon, `ooicon-${this._props.icon}`);
            }
        },
        radiogroup: (old)=>{
            if (this._elements.radio){
                this._elements.radio.name = this._props.radiogroup;
            }
        },
        type: (old)=>{
            if (old!==null && old!==this._props.type){
                this._renderItem();
            }
        }
    };
    constructor() {
        super();
        this._initialize('oo-menu-item', OOMenuItem, html, css);
    };
    _setEvent() {
        super._setEvent();
        this.addEventListener('click', ()=>{
            if (this.type === 'command'){
                const menu = dom.getParent(this, 'oo-menu');
                if (menu) menu.hide();
            }
        });
    }

    _connected(){
        const type = this.getAttribute('type');
        if (!type)  this.setAttribute('type', 'command');
        const menu = dom.getParent(this, 'oo-menu');
        if (menu){
            this.setAttribute('slot', 'items');
        }
        this._renderItem();
    }
    _renderItem(){
        dom.empty(this);
        this._elements.icon = null;
        this._elements.label = null;
        this._elements.input = null;
        this._elements.hr = null;
        this.checkbox = null;
        this.radio = null;
        dom.addClass(this._content, 'enabled');

        switch (this._props.type){
            case 'command':
                this._elements.icon = dom('div.icon', {slot: 'item'});
                this._elements.label = dom('div.label', {text: this._props.label, slot: 'item'});
                if (this._props.icon) dom.addClass(this._elements.icon, `ooicon-${this._props.icon}`);
                this.append(this._elements.icon);
                this.append(this._elements.label);
                break;
            case 'checkbox':
                this._elements.input = dom('oo-checkbox', {slot: 'item'});
                this.append(this._elements.input);
                this._elements.input.text = this._props.label;
                this.checkbox = this._elements.input;
                break;
            case 'radio':
                this._elements.input = dom('oo-radio', {slot: 'item'});
                this.append(this._elements.input);
                this._elements.input.text = this._props.label;
                this._elements.input.name = this._props.radiogroup;
                this.radio = this._elements.input;
                break;
            case 'hr':
                this._elements.hr = dom('hr', {slot: 'item'});
                this.append(this._elements.hr);
                dom.removeClass(this._content, 'enabled');
                break;
        }
    }

}
if (!customElements.get('oo-menu-item')) customElements.define('oo-menu-item', OOMenuItem);
