import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from './template/pagination.scope.css?inline';
import '../button.js';

const html = `
<div class="content">
    <oo-button class="first" type="light"></oo-button>
    <div class="prev ooicon-arrow_back"></div>
    <div class="pages"></div>
    <div class="next ooicon-arrow_forward"></div>
    <oo-button  class="last" type="light"></oo-button>
    <div class="jumper hide">
        <span class="jumperTextLeft"></span>
        <oo-input class="jumperInput" appearance="none" type="number" input-style="text-align: center"></oo-input>
        <span>/</span>
        <span class="jumperPageCount"></span>
        <span class="jumperTextRight"></span>
    </div>
</div>
`;

function defer(func, bind) {
    func.apply(bind || this);
}

export default class OOPagination extends OOComponent {
    static prop = {
        total: 200,
        pageSize: 20,
        pageCount: 0,
        pages: 10,
        current: 1,
        showSingle: true, //仅一页是否显示

        jumper: false,
        first: '第一页',
        last: '最后一页',

        jumperText: '到第{n}页',

        border: true,
        cssLink: '',
        skin: '',
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att) => {
            return hyphenate(att);
        });
    }

    _elements = {
        first: null,
        last: null,
        prev: null,
        next: null,
        pages: null,
        jumper: null,
        jumperTextLeft: null,
        jumperTextRight: null,
        jumperPageCount: null,
        jumperInput: null,
    };

    _setPropMap = {
        cssLink: () => {
            this.useCssLink(this._props.cssLink);
        },
        skin: () => {
            this._useSkin(this._props.skin);
        },

        current: (old, value) => {
            if (old !== value) defer(this.#renderPages, this);
        },
        total: (old, value) => {
            if (old !== value) defer(this.#renderPages, this);
        },
        pageSize: (old, value) => {
            if (old !== value) defer(this.#renderPages, this);
        },
        pageCount: () => {},
        pages: (old, value) => {
            if (old !== value) defer(this.#renderPages, this);
        },

        first: () => {
            this.#renderFirst();
        },
        last: () => {
            this.#renderLast();
        },
        jumper: () => {
            dom.checkClass(this._elements.jumper, 'hide', !this._props.jumper);
        },
        jumperText: () => {
            if (!this._props.jumperText) {
                this._props.jumperText = '到第{n}页';
            }
            this.#renderJumper();
        },
    };

    constructor() {
        super();
        this._initialize('oo-pagination', OOPagination, html, css);
    }
    async _render() {
        this.#getCurrent();

        if (this._props.pageCount > 1 || this._props.showSingle) {
            this.#renderFirst();
            this.#renderLast();
            defer(this.#renderPages, this);
            this.#renderJumper();
        }
    }
    _setEvent() {
        this._elements.prev.addEventListener('click', () => {
            this.gotoPrev();
        });
        this._elements.next.addEventListener('click', () => {
            this.gotoNext();
        });
        this._elements.first.addEventListener('click', () => {
            this.gotoFirst();
        });
        this._elements.last.addEventListener('click', () => {
            this.gotoLast();
        });
        if (this._elements.jumperInput) {
            this._elements.jumperInput.addEventListener('keydown', (e) => {
                if (e.key === 'Enter' || e.keyCode === 13) {
                    const input = this._elements.jumperInput;
                    if (!isNaN(input.value) && input.value) {
                        this.gotoPage(parseInt(input.value));
                        input.value = '';
                    }
                }
            });
        }
    }

    #renderFirst() {
        const firstText = this._props.first;
        this._elements.first.textContent = firstText;
        dom.checkClass(this._elements.first, 'hide', !firstText);
    }
    #renderLast() {
        const lastText = this._props.last;
        this._elements.last.textContent = lastText;
        dom.checkClass(this._elements.last, 'hide', !lastText);
    }
    #renderPages(pageCount, pages, current) {
        this.#getCurrent();

        dom.empty(this._elements.pages);

        if (this._props.pageCount > 1 || this._props.showSingle) {
            if (this._props.pageCount <= this._props.pages) {
                for (let i = 1; i <= this._props.pageCount; i++) {
                    this.#createPage(i);
                }
            } else {
                this.#createPage(1);

                const computePages = this._props.pages - 2;
                const half = parseInt(computePages / 2);

                let end;
                if (this._props.current - half <= 2) {
                    for (let i = 1; i <= computePages; i++) {
                        end = i + 1;
                        this.#createPage(end);
                    }
                } else if (this._props.current + half >= this._props.pageCount - 1) {
                    this.#createPagesBack();
                    for (let i = computePages; i >= 1; i--) {
                        end = this._props.pageCount - i;
                        this.#createPage(end);
                    }
                } else {
                    this.#createPagesBack();

                    for (let i = 0; i < computePages; i++) {
                        end = this._props.current - (half - i);
                        this.#createPage(end, current);
                    }
                }
                if (end < this._props.pageCount - 1) {
                    this.#createPagesForward();
                }
                this.#createPage(this._props.pageCount);
            }
        }
    }
    #getCurrent() {
        this._props.pageCount = Math.ceil(this._props.total / this._props.pageSize);
        if (this._props.pageCount < 1) this._props.pageCount = 1;
        if (this._props.current < 1) this._props.current = 1;
        if (this._props.current > this._props.pageCount) this._props.current = this._props.pageCount;
    }
    setCurrent(current) {
        this._props.current = current;
        this.#getCurrent();
    }

    #createPage(count) {
        const page = document.createElement('div');
        page.textContent = count;
        if (count === this._props.current) dom.addClass(page, 'current');
        page.addEventListener('click', () => {
            this.gotoPage(count);
        });
        this._elements.pages.append(page);
    }
    #createPagesForward() {
        const page = document.createElement('div');
        dom.addClass(page, 'ooicon-point3 point3');
        page.addEventListener('click', () => {
            this.pagesForward();
        });
        this._elements.pages.append(page);
    }
    #createPagesBack() {
        const page = document.createElement('div');
        dom.addClass(page, 'ooicon-point3 point3');

        page.addEventListener('click', () => {
            this.pagesBack();
        });

        this._elements.pages.append(page);
    }

    #renderJumper() {
        const text = this._props.jumperText.split('{n}');
        this._elements.jumperTextLeft.textContent = text[0];
        this._elements.jumperTextRight.textContent = text?.[1] ?? '';
        this._elements.jumperPageCount.textContent = this._props.pageCount;
    }
    gotoPage(count) {
        this.setCurrent(count);
        this.dispatchEvent(new CustomEvent('page', {detail: this._props.current}));
        this.setAttribute('current', this._props.current);
        defer(this.#renderPages, this);
    }
    pagesForward() {
        this.gotoPage(this._props.current + (this._props.pages - 2));
    }
    pagesBack() {
        this.gotoPage(this._props.current - (this._props.pages - 2));
    }
    gotoFirst() {
        this.gotoPage(1);
    }
    gotoLast() {
        this.gotoPage(this._props.pageCount);
    }
    gotoPrev() {
        this.gotoPage(--this._props.current);
    }
    gotoNext() {
        this.gotoPage(++this._props.current);
    }
}
