import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from "./template/skeleton.scope.css?raw";

const html = `
<div class="content">
</div>
`
export default class OOSkeleton extends OOComponent{
    static prop = {
        rows: '5',
        cssLink: '',
        skin: ''
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _setPropMap={
        rows: ()=>{
            this.#renderRows();
        },
        cssLink: ()=>{
            this.useCssLink(this._props.cssLink);
        },
        skin: ()=>{
            this._useSkin(this._props.skin);
        }
    }
    constructor() {
        super();
        this._initialize('oo-skeleton',OOSkeleton, html, css);
    }
    #renderRows(){
        dom.empty(this._content);
        try{
            const rows = parseInt(this._props.rows);
            for (let i=0; i<rows; i++){
                this._content.append(dom('div.item'));
            }
        }catch(e){}
    }
    _render() {
        this.#renderRows();
    }
}
