import {$Selector} from './$selector.js';

export default class OOSelector extends $Selector{
    constructor() {
        super();
        this._init('oo-selector');
    }
    get value(){
        const v = (typeOf(this._props.value)==='string' && !!this._props.value) ? this._props.value.split(/\s*,\s*/g) : this._props.value;
        if (this._props.count===1){
            return v[0];
        }
        return (this._props.count) ? v.slice(0, this._props.count) : v;
    }
    set value(v){
        this.setAttribute('value', v);
    }
}
