import {dom, hyphenate, typeOf, clearTime, formatDate, increment} from '@o2oa/util';
import html from './template/calendarView.html?raw';
import css from './template/calendarView.scope.css?inline';
import {OOComponent} from "../base";
import {datetimePicker} from "../../api/datetimePicker.js";

export default class OOCalendarView extends OOComponent{
    static prop = {
        baseDate: "",

        weekBegin: 1, //0表示周日，1表示周一
        format: "YYYY年MM月",

        months: ["一月", "二月", "三月", "四月", "五月", "六月", "七月", "八月", "九月", "十月", "十一月", "十二月"],
        daysTitles: ["周日", "周一", "周二", "周三", "周四", "周五", "周六"],

    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }

    static parseDate(date, isClone){
        if( !date )return null;
        return typeOf(date) === "date" ? (isClone ? new Date(date.getTime()) : date) : new Date(date);
    }

    _elements={
        content: null,
        top: null,
        prev: null,
        next: null,
        date : null,
        dateContent: null
    };
    #eventListeners = {};
    #currentDate = null;
    #picker = null;
    _setPropMap={
        skin: ()=>{
            this._useSkin(this._props.skin);
        },
        baseDate: ()=>{
            this.setCurrentDate();
            this.setDateContent();
        },
        weekBegin: ()=>{
            this.setWeekTitle();
           this.setDateContent();
        },
        $default: (key)=>{
            //if (key==='value') this.value = this._props[key];
            //dom.toggleAttr(this._elements.button, key, this._props[key]);
        }
    };
    constructor() {
        super();
        this._initialize('oo-calendar-view', OOCalendarView, html, css);
    }
    _render() {
        this.setCurrentDate();
    }

    _connected() {
        this.setWeekTitle();
        this.setDateContent();
    }

    _setEvent(){
        this._elements.prev.addEventListener('click', e=>{
            increment(this.#currentDate, 'month', -1);
            //this._elements.date.setAttribute('value', formatDate(this.#currentDate, 'YYYY-MM'));
            this.setDateContent();
            this.changeDate();
        });
        this._elements.next.addEventListener('click', e=>{
            increment(this.#currentDate, 'month', 1);
            //this._elements.date.setAttribute('value', formatDate(this.#currentDate, 'YYYY-MM'));
            this.setDateContent();
            this.changeDate();
        });
        this._elements.date.addEventListener('change', e=>{
            this.#currentDate = e.detail.date;
            this._elements.date.textContent = formatDate(this.#currentDate, this._props.format);
            this.setDateContent();
        })
    }

    setCurrentDate(){
        this.#currentDate = this._props.baseDate ? OOCalendarView.parseDate(this._props.baseDate, true) : new Date();
        this.changeDate()
    //     this._elements.date.setAttribute('baseDate', this._props.baseDate);
    //     this._elements.date.setAttribute('value', formatDate(this.#currentDate, 'YYYY-MM'));
    }

    changeDate(){
        this._elements.date.textContent = formatDate(this.#currentDate, this._props.format);
        if( this.#picker ){
            this.#picker.reset({
                value: formatDate(this.#currentDate, 'YYYY-MM')
            })
        }else{
            this.#picker = datetimePicker(this._elements.date, {
                mode: 'month',
                format: this._props.format,
                container: document.body,
                value: formatDate(this.#currentDate, 'YYYY-MM'),
            })
        }
        this.dispatchEvent(new CustomEvent("change", {
            detail: {
                date: this.#currentDate,
                value: formatDate(this.#currentDate, 'YYYY-MM')
            }
        }));
    }

    setWeekTitle(){
        const ths = this._elements.dateContent.querySelectorAll("th");
        this._props.daysTitles.forEach((text, i)=>{
            ths[ (7 + i - this._props.weekBegin ) % 7 ].textContent = text;
        })
    }
    setDateContent(){
        const todayMillisecond = clearTime(new Date()).getTime();

        const baseDate = clearTime(this.#currentDate, true);
        baseDate.setDate(1);

        const tds = this._elements.dateContent.querySelectorAll("td");

        const tmpDate = new Date( baseDate.getTime() );
        tmpDate.setDate(tmpDate.getDate() - 1);

        const day = (7 + tmpDate.getDay() + 1 - this._props.weekBegin) % 7;

        const setSingleDateContent = (td, date)=>{
            td.querySelectorAll('slot').forEach(slot=>slot.remove());

            const dataValue = formatDate(date, 'YYYY-MM-DD');

            const titleSlot = dom('slot', {'name': `title-${dataValue}`});
            td.querySelector('div.cellTitle').appendChild(titleSlot);

            const contentSlot = dom('slot', {'name': `content-${dataValue}`});
            td.querySelector('div.cellContent').appendChild(contentSlot);

            td.querySelector('span:first-child').textContent = formatDate(date, 'D');

            td.dataset.dateValue =  dataValue;
            dom.checkClass(td, "today", date.getTime() === todayMillisecond );
            dom.checkClass(td, "other", date.getMonth() !== this.#currentDate.getMonth());
        };

        for (let i=day-1; i>=0; i--){
            setSingleDateContent( tds[i], tmpDate );
            tmpDate.setDate(tmpDate.getDate() - 1);
        }

        for (let i=day; i<tds.length; i++){
            setSingleDateContent( tds[i], baseDate );
            baseDate.setDate(baseDate.getDate() + 1);
        }
    }

}
