export default ".content{\r\n    background-color: var(--option-bg);\r\n    transition: background-color 0.2s, color 0.2s;\r\n    cursor: pointer;\r\n    overflow: hidden;\r\n    box-sizing: border-box;\r\n}\r\n.content *{\r\n    box-sizing: border-box;\r\n}\r\n.body:hover{\r\n    background-color: var(--option-over-bg);\r\n    color: var(--option-over);\r\n}\r\n.body.selected{\r\n    font-weight: bold;\r\n    color: var(--option-selected);\r\n    background-color: var(--option-selected-bg);\r\n}\r\n.body.disabled{\r\n    opacity: 0.4;\r\n    cursor: not-allowed;\r\n}\r\n.body.disabled:hover{\r\n    background-color: var(--option-bg);\r\n}\r\n.body.focus{\r\n    background-color: var(--option-cascade-focus-bg);\r\n}\r\n\r\n.area{\r\n    float: left;\r\n    line-height: 2em;\r\n    overflow: hidden;\r\n    padding: 0 0 0 1em;\r\n    white-space: nowrap;\r\n    display: flex;\r\n    gap: 0.5em;\r\n    align-items: center;\r\n}\r\n\r\n.label, .text{\r\n    line-height: 2em;\r\n    overflow: hidden;\r\n    word-break: keep-all;\r\n    text-overflow: ellipsis;\r\n}\r\n\r\n.body{\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding: 0;\r\n    gap: 1em;\r\n}\r\n.arrow{\r\n    padding: 0 0.5em 0 0;\r\n}\r\n\r\n\r\n\r\n.options-position-node{\r\n    position: absolute;\r\n    height: 2em;\r\n    top: 0;\r\n    transition: background-color 0.2s;\r\n}\r\n.options-position-node.hide{\r\n    display: block !important;\r\n    opacity: 0 !important;\r\n    width: 0 !important;\r\n    height: 0 !important;\r\n}\r\n.options-area{\r\n    top: 0;\r\n    position: absolute;\r\n    border: 1px solid var(--option-border);\r\n    background-color: var(--option-bg);\r\n    border-radius: var(--oo-area-radius);\r\n    box-shadow: var(--option-shadow);\r\n    z-index: 100;\r\n    transform-origin: left top;\r\n    /* transition: height 0.1s, opacity 0.1s, transform 0.1s; */\r\n}\r\n.options-area.visible{\r\n    transform: scale(1);\r\n    opacity: 1;\r\n}\r\n.options-area.invisible{\r\n    transform: scale(0.001, 1);\r\n    opacity: 0;\r\n}\r\n.options-content{\r\n    max-height: 17em;\r\n    padding: 0.5em 0;\r\n    border-radius: var(--oo-area-radius);\r\n    overflow: auto;\r\n}\r\n\r\n.options-content::-webkit-scrollbar {\r\n    width: 8px;\r\n    height: 8px;\r\n    border-radius: 8px;\r\n    background-color: transparent;\r\n}\r\n.options-content::-webkit-scrollbar-thumb{\r\n    width: 8px;\r\n    border-radius: 8px;\r\n    background-color: transparent;\r\n    cursor: pointer;\r\n    opacity: 0;\r\n}\r\n.options-content:hover::-webkit-scrollbar-thumb{\r\n    background-color: #dddddd;\r\n}\r\n.options-content:hover::-webkit-scrollbar-thumb:hover{\r\n    background-color: #cccccc;\r\n}"