import {dom, hyphenate} from '@o2oa/util';
import {I18n} from '../../common/i18n.js';
import {OO$Panel} from '../$panel.js';
import css from './template/dialog.scope.css?raw';

const html = `
<div class="dialog content">
    <div class="header" draggable="true">
        <div class="title"></div>
        <div class="actions">
            <div class="close ooicon-close show"></div>
        </div>
    </div>
    <div class="body">
        <div class="bodyIcon hide"></div>
        <div class="bodyContent">
            <slot name="content"></slot>
        </div>
    </div>
    <div class="buttons">
        <oo-button class="button_ok show">OK</oo-button>
        <oo-button class="button_yes">Yes</oo-button>
        <oo-button class="button_no" type="cancel">No</oo-button>
        <oo-button class="button_cancel show" type="cancel">Cancel</oo-button>
    </div>
<!--    <div class="footer">-->
<!--        <div class="resize" draggable="true"></div>-->
<!--    </div>-->
</div>
`;
class OODialog extends OO$Panel {
    static prop = {
        css: '',
        cssLink: '',
        skin: '',
        isShow: false,
        dlgTitle: '',
        canResize: false,
        canMove: true,
        canClose: true,
        icon: '',
        closeOnEvent: true,
        buttons: 'ok, cancel',
        ok: 'OK',
        yes: 'Yes',
        no: 'No',
        cancel: 'Cancel',
        buttonAlign: 'flex-end', //end center start
        size: 'default', //or small
        okSize: '',
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att) => {
            return hyphenate(att);
        });
    }
    _elements = {
        header: null,
        title: null,
        actions: null,
        close: null,
        body: null,
        buttons: null,
        button_ok: null,
        button_yes: null,
        button_no: null,
        button_cancel: null,
        footer: null,
        resize: null,
        bodyIcon: null,
    };
    constructor() {
        super();
        const lp = I18n.get('index').o('common');
        ['ok', 'yes', 'no', 'cancel'].forEach((k) => (OODialog.prop[k] = lp[k]));
        this.#setPropMap();
        this._init('oo-dialog', OODialog, html, css);
    }
    #setPropMap() {
        this.setPropMap = Object.assign(this._setPropMap, {
            dlgTitle: () => {
                if (this._props.dlgTitle) {
                    this._elements.title.textContent = this._props.dlgTitle;
                    dom.removeClass(this._elements.header, 'hide');
                } else {
                    if (!this._props.canClose) {
                        dom.addClass(this._elements.header, 'hide');
                    } else {
                        dom.removeClass(this._elements.header, 'hide');
                    }
                }
                // this.removeAttribute('title');
                this._setBodySize();
            },
            buttons: () => {
                if (this._props.buttons) {
                    dom.removeClass(this._elements.buttons, 'hide');

                    const bts = this._props.buttons.split(/,\s*/g);
                    ['ok', 'yes', 'no', 'cancel'].forEach((k) => {
                        if (bts.includes(k)) {
                            dom.addClass(this._elements[`button_${k}`], 'show');
                        } else {
                            dom.removeClass(this._elements[`button_${k}`], 'show');
                        }
                    });
                } else {
                    dom.addClass(this._elements.buttons, 'hide');
                }
                this._setBodySize();
            },
            buttonAlign: () => {
                if (this._elements.buttons) {
                    dom.setStyle(this._elements.buttons, 'justify-content', this._props.buttonAlign || 'flex-end');
                }
            },
            canResize: () => {
                const method = this._props.canResize ? 'addClass' : 'removeClass';
                dom[method](this, 'resize');
            },
            canMove: () => {
                this._elements.header.setAttribute('draggable', !!this._props.canMove);
                this._elements.body.setAttribute('draggable', !!this._props.canMove);
            },
            canClose: () => {
                const method = this._props.canClose ? 'addClass' : 'removeClass';
                dom[method](this._elements.close, 'show');
                if (!this._props.canClose && !this._props.dlgTitle) {
                    dom.addClass(this._elements.header, 'hide');
                } else {
                    dom.removeClass(this._elements.header, 'hide');
                }
            },
            icon: (k, old) => {
                if (this._props.icon) {
                    if (this._elements.bodyIcon) {
                        dom.addClass(this._elements.bodyIcon, 'ooicon-' + this._props.icon);
                        dom.removeClass(this._elements.bodyIcon, 'hide');
                    }
                } else {
                    if (this._elements.bodyIcon) {
                        dom.removeClass(this._elements.bodyIcon, 'ooicon-' + old);
                        dom.addClass(this._elements.bodyIcon, 'hide');
                    }
                }
            },
            ok: () => {
                if (this._elements.button_ok) this._elements.button_ok.textContent = this._props.ok;
            },
            yes: () => {
                if (this._elements.button_ok) this._elements.button_yes.textContent = this._props.yes;
            },
            no: () => {
                if (this._elements.button_ok) this._elements.button_no.textContent = this._props.no;
            },
            cancel: () => {
                if (this._elements.button_ok) this._elements.button_cancel.textContent = this._props.cancel;
            },
            okSize: () => {
                if (this._elements.button_ok) {
                    if (this._props.okSize) {
                        dom.setStyle(this._elements.button_ok, 'width', this._props.okSize);
                    } else {
                        dom.setStyle(this._elements.button_ok, 'width', 'unset');
                    }
                }
            },
            size: () => {
                if (this._props.size === 'small') {
                    dom.addClass(this._elements.header, 'small');
                    dom.addClass(this._elements.buttons, 'small');
                } else {
                    dom.removeClass(this._elements.header, 'small');
                    dom.removeClass(this._elements.buttons, 'small');
                }
            },
            closeOnEvent: () => {},
        });
    }
    _addHideEvent() {}
    _removeHideEvent() {}

    #moveData = {
        x: 0,
        y: 0,
        positionX: 0,
        positionY: 0,
        styleX: 'left',
        styleY: 'top',
    };
    #getMoveData() {
        const o = {x: 0, y: 0, positionX: 0, positionY: 0};
        o.styleX = this.style.right ? 'right' : 'left';
        o.styleY = this.style.bottom ? 'bottom' : 'top';

        const pNode = dom.getOffsetParent(this);
        const p = dom.getPosition(this, pNode);
        const pSize = dom.getSize(pNode);
        const size = dom.getSize(this);
        o.positionX = o.styleX === 'right' ? pSize.x - (p.x + size.x) : p.x;
        o.positionY = o.styleY === 'bottom' ? pSize.y - (p.y + size.y) : p.y;

        return o;
    }
    #resizeData = {
        x: 0,
        y: 0,
        width: 0,
        height: 0,
    };

    #createEvent(type, e) {
        const event = new MouseEvent(type, Object.assign({}, e, {bubbles: false}));
        const preventDefault = event.preventDefault;
        event.preventDefault = () => {
            this.preventDefault = true;
            preventDefault.apply(event);
        };
        const stopPropagation = event.stopPropagation;
        event.stopPropagation = () => {
            this.stopPropagation = true;
            stopPropagation.apply(event);
        };
        const stopImmediatePropagation = event.stopImmediatePropagation;
        event.stopImmediatePropagation = () => {
            this.stopPropagation = true;
            stopImmediatePropagation.apply(event);
        };
        return event;
    }
    #runButtonEvent(type, e, isClose = true) {
        const event = this.#createEvent(type, e);
        this.dispatchEvent(event);
        if (!this.preventDefault && isClose) this.close(e);
        this.preventDefault = false;
    }

    #drag(e) {
        if (e.screenX || e.screenY) {
            const dx = e.screenX - this.#moveData.x;
            const dy = e.screenY - this.#moveData.y;
            const x = this.#moveData.styleX === 'right' ? this.#moveData.positionX - dx : this.#moveData.positionX + dx;
            const y = this.#moveData.styleY === 'bottom' ? this.#moveData.positionY - dy : this.#moveData.positionY + dy;

            const style = {};
            style[this.#moveData.styleX] = x + 'px';
            style[this.#moveData.styleY] = y + 'px';

            dom.setStyles(this, style);
            this.dispatchEvent(new DragEvent('move', e));
        }
    }
    #dragstart(e) {
        this.#moveData = this.#getMoveData();
        this.#moveData.x = e.screenX;
        this.#moveData.y = e.screenY;

        this.dispatchEvent(new DragEvent('startMove', e));
    }
    #dragend(e) {
        this.dispatchEvent(new DragEvent('endMove', e));
    }
    _setEvent() {
        super._setEvent();
        this._elements.close.addEventListener('click', (e) => {
            this.close(e);
        });
        this._elements.button_cancel.addEventListener('click', (e) => {
            this.#runButtonEvent('cancel', e);
            if (!this.stopPropagation) this.dispatchEvent(new CustomEvent('resolvecancel'));
            this.stopPropagation = false;
        });
        this._elements.button_ok.addEventListener('click', (e) => {
            this.#runButtonEvent('ok', e, this._props.closeOnEvent);
            if (!this.stopPropagation) this.dispatchEvent(new CustomEvent('resolveok'));
            this.stopPropagation = false;
        });
        this._elements.button_yes.addEventListener('click', (e) => {
            this.#runButtonEvent('yes', e, this._props.closeOnEvent);
            if (!this.stopPropagation) this.dispatchEvent(new CustomEvent('resolveyes'));
            this.stopPropagation = false;
        });
        this._elements.button_no.addEventListener('click', (e) => {
            this.#runButtonEvent('no', e);
            if (!this.stopPropagation) this.dispatchEvent(new CustomEvent('resolveno'));
            this.stopPropagation = false;
        });

        this._elements.header.addEventListener('drag', (e) => this.#drag(e));
        this._elements.header.addEventListener('dragend', (e) => this.#dragend(e));
        this._elements.header.addEventListener('dragstart', (e) => this.#dragstart(e));

        this._elements.body.addEventListener('drag', (e) => this.#drag(e));
        this._elements.body.addEventListener('dragend', (e) => this.#dragend(e));
        this._elements.body.addEventListener('dragstart', (e) => this.#dragstart(e));
    }
    close(e) {
        this.dispatchEvent(new MouseEvent('close', e));
        this.hide();
    }
    _afterHide() {
        this.remove();
    }
    _render() {
        ['ok', 'yes', 'no', 'cancel'].forEach((k) => this._setPropMap[k]());
        this._fillContent('content');
    }
    _setBodySize() {
        let h = '100%';
        if (this._props.dlgTitle || this._props.canClose) {
            h = h + ' - 4em';
        }
        if (this._props.buttons) {
            h = h + ' - 5em';
        }
        this._elements.body.style.height = `calc(${h})`;
    }
}
if (!customElements.get('oo-dialog')) customElements.define('oo-dialog', OODialog);
