import {dom, hyphenate} from '@o2oa/util';
import {$Input} from './$input.js';
import css from './template/select.scope.css?raw';

export default class OOSelect extends $Input {
    static prop = {
        read: false,
        allowInput: false,
        optionWidth: 'fixed'   //fixed 和输入框等宽； auto 根据内容宽度
    };
    static get observedAttributes() {
        OOSelect.prop = Object.assign({}, $Input.prop, OOSelect.prop);
        return Object.keys(OOSelect.prop).map((att) => {
            return hyphenate(att);
        });
    }
    constructor() {
        super();
        this.#setPropMap();
        this._init('oo-select');
        this._useCss(css);
    }
    #setPropMap() {
        this._setPropMap = Object.assign(this._setPropMap, {
            disabled: () => {
                dom.toggleAttr(this._elements.input, 'disabled', this._props.disabled);
                dom.checkClass(this._elements.box, 'disabled', this._props.disabled);
                dom.checkClass(this._elements.label, 'disabled', this._props.disabled);
                dom.checkClass(this._elements.drop, 'disabled', this._props.disabled);
            },
            optionWidth: ()=>{
                if (this._props.optionWidth==='auto'){
                    if (this.#optionsArea){
                        dom.setStyle(this.#optionsArea, 'min-width', 'unset');
                    }
                }
            },
            value: () => {
                //如果是动态选项，已有值不在已加载的选项中，则直接显示value
                if (this._props.hasOwnProperty('value')) {
                    let selectedFlag = false;
                    const value = Array.isArray(this._props.value) ? this._props.value?.[0] : this._props.value;
                    for (const option of this.#options) {
                        if (option.value === value) {
                            if (!option.selected) option.selected = true;
                            selectedFlag = true;
                            break;
                        }
                    }
                    if (!selectedFlag){
                        this._elements.input.value = this._props.value;
                    } 
                    if (this._props.allowInput){
                        this._inputValue();
                    }
                }
            },
            readonly: () => {},
            readmode: () => {
                // dom.toggleAttr(this._elements.input, 'readonly', this._props.readmode);
                dom.checkClass(this._elements.box, 'readmode', this._props.readmode);
                dom.checkClass(this._elements.label, 'readmode', this._props.readmode);
                if (!this._props.readmode) {
                    dom.toggleAttr(this._elements.body, 'style', '');
                } else {
                    dom.toggleAttr(this._elements.body, 'style', this._props.viewStyle);
                }
                this._setPropMap.viewStyle();
                this._setPropMap.required();
            },
            autoSize: () => {
                if (this._props.autoSize) {
                    dom.setStyle(this._elements.view, 'width', `calc(${this.#maxWidth}px + 1rem)`);
                } else {
                    dom.setStyle(this._elements.view, 'width', `auto`);
                }
            },
            read: () => {},
            allowInput: () => {
                if (!this._props.allowInput) {
                    this._elements.input.setAttribute('readonly', true);
                } else {
                    this._elements.input.removeAttribute('readonly');
                    this._elements.input.value = this._props.value;
                    this._inputValue();
                }
            },
            viewStyle: () => {
                if (this._props.readmode) dom.toggleAttr(this._elements.body, 'style', this._props.viewStyle);
            },
        });
    }
    #droped = false;
    #options = [];
    #hideOptionsFun = null;
    #groups = [];
    #optionsArea = null;
    #optionsPositionNode = null;

    _checkSelectedValue(value){
        for (const option of this.#options) {
            if (option.value === value) {
                return option;
            }
        }
        return null;
    }
    _render() {
        this.#droped = false;
        this._elements.drop = dom('div.drop.ooicon-drop_down');
        this._elements.view.insertAdjacentElement('afterend', this._elements.drop);

        this.#optionsPositionNode = this.querySelector('div.options-position-node');
        if (!this.#optionsPositionNode) {
            this.#optionsPositionNode = dom('div.options-position-node.hide');
            this._elements.body.insertAdjacentElement('afterbegin', this.#optionsPositionNode);
            this.#optionsPositionNode.addEventListener('click', () => {
                this.#hideOptions();
            });
        }

        this.#optionsArea = this.querySelector('div.options-area');
        if (!this.#optionsArea) {
            this.#optionsArea = dom('div.options-area.invisible');
            dom.set(this.#optionsArea, 'html', '<div class="arrow"></div><div class="options-content"><slot name="items"></slot></div>');
            this.#optionsPositionNode.append(this.#optionsArea);
        }

        if (!this._props.allowInput) {
            this._elements.input.setAttribute('readonly', true);
        } else {
            this._elements.input.removeAttribute('readonly');
        }
    }
    _removeItem(option) {
        this.#options.splice(this.#options.indexOf(option), 1);
        option.removeAttribute('slot');
    }
    #maxWidth = 0;
    _appendItem(option) {
        this.#options.push(option);
        option.setAttribute('slot', 'items');
        option.setAttribute('skin', this._props.skin);

        const viewSize = dom.getSize(this._elements.view);
        const sizeOption = dom.getSize(option._elements.text);

        const size = Math.max(sizeOption.x, viewSize.x);
        if (size > this.#maxWidth) {
            this.#maxWidth = size;
            this.#setBoxMinWidth();
        }

        if (this._props.size) option.setAttribute('size', this._props.size);

        this._appendItemCheckValue(option);

        this._setPropMap.$default('value');
    }
    checkMaxWidth(width) {
        if (width > this.#maxWidth) {
            this.#maxWidth = width;
            this.#setBoxMinWidth();
        }
    }
    #setBoxMinWidth() {
        if (this._props.autoSize) {
            if (this.setMinWidthTimer) window.clearTimeout(this.setMinWidthTimer);
            this.setMinWidthTimer = setTimeout(() => {
                dom.setStyle(this._elements.view, 'width', `calc(${this.#maxWidth}px + 1rem)`);
                this.setMinWidthTimer = null;
            }, 10);
        }
    }

    _appendItemCheckValue(option) {
        const value = Array.isArray(this.value) ? this.value?.[0] : this.value;
        if (value) {
            if (option.value === value) {
                option.selected = true;
            }
        }
    }
    _removeGroup(group) {
        this.#groups.splice(this.#groups.indexOf(group), 1);
        group.removeAttribute('slot');
    }
    _appendGroup(group) {
        this.#groups.push(group);
        group.setAttribute('slot', 'items');
        group.setAttribute('skin', this._props.skin);
        if (this._props.size) option.setAttribute('size', this._props.size);
    }
    #selectedOption = null;

    _selected(option) {
        if (option.selected) {
            if (this.#selectedOption && this.#selectedOption !== option) {
                this.#selectedOption.selected = false;
            }

            this.#selectedOption = option;
            const text = option.text;
            this.value = option.value || text;
            this.text = text;

            const textShow = Array.isArray(text) ? text.join(',') : text;
            this._elements.input.value = textShow;
            this._elements.viewText.textContent = textShow;

            if (!text) this._elements.viewText.innerHTML = '-';

            this.unInvalidStyle();

            this._elements.view.title = this._elements.view.textContent;
            this.dispatchEvent(new CustomEvent('change'));
        }
        this.#hideOptions();
    }
    _inputValue() {
        if (this.#selectedOption) {
            this.#selectedOption.selected = false;
        }
        this.#selectedOption = null;

        this.value = this._elements.input.value;
        this._elements.viewText.textContent = this.value ?? ' ';
        this.text = this.value;

        if (!this.value) this._elements.viewText.innerHTML = '-';
    }
    #toggleOptions() {
        this.#droped = !this.#droped;
        if (this.#droped) {
            this.#showOptions();
        } else {
            this.#hideOptions();
        }
    }
    _setEvent() {
        super._setEvent();
        this._elements.box.addEventListener('click', () => {
            if (!this._props.disabled && !this._props.readmode && !this._props.read && !this._props.readonly) this.#toggleOptions();
        });
        this._elements.input.addEventListener('click', (e) => {
            if (!this._props.disabled && !this._props.readmode && !this._props.read && !this._props.readonly) {
                if (!this._props.allowInput) this.#toggleOptions();
                e.stopPropagation();
            }
        });
        this._elements.input.addEventListener('input', (e) => {
            this._inputValue(e);
        });

        // this._elements.viewText.addEventListener('click', ()=>{
        //     this.#toggleOptions()
        // });

        this.#optionsArea.addEventListener('click', (e) => {
            e.stopPropagation();
        });
        this.#optionsArea.addEventListener('mousedown', (e) => {
            e.stopPropagation();
        });
        this._content.firstElementChild.addEventListener('mousedown', (e) => {
            e.stopPropagation();
        });

        // if (this._elements.drop){
        //     this._elements.box.addEventListener('mouseover', (e)=>{
        //         dom.removeClass(this._elements.drop, 'ooicon-drop_down');
        //         dom.addClass(this._elements.drop, 'ooicon-close');
        //     })
        // }
    }
    #positionOptions() {
        const size = dom.getSize(this._elements.box);
        const y = size.y + 6 + size.y * 0.2;
        const w = this._props.optionWidth==='auto' ? dom.getSize(this.#optionsArea).x : size.x;

        const p = dom.getPosition(this._elements.box, dom.getOffsetParent(this._elements.box));
        dom.setStyles(this.#optionsPositionNode, {
            left: p.x + 'px',
        });

        dom.setStyles(this.#optionsArea, {
            top: y + 'px',
            'min-width': this._props.optionWidth==='auto' ? 'unset' : w + 'px',
        });

        const arrowLeft = w / 2 - 6;
        dom.setStyles(this.#optionsArea.querySelector('.arrow'), {
            left: arrowLeft + 'px',
        });
    }
    #toggleStyles() {
        dom.checkClass(this._elements.drop, 'down', this.#droped);
        dom.checkClass(this._elements.box, 'focus', this.#droped);
        dom.checkClass(this._elements.label, 'focus', this.#droped);
    }

    #showOptions() {
        if (!this._props.readmode) {
            this.#droped = true;
            this.#toggleStyles();
            this.#positionOptions();
            dom.removeClass(this.#optionsPositionNode, 'hide');

            dom.removeClass(this.#optionsArea, 'invisible');
            dom.addClass(document.body, 'ooui-select-visible');

            dom.addClass(this.#optionsArea, 'visible');

            this.ownerDocument.dispatchEvent(new MouseEvent('mousedown'));
            this.dispatchEvent(new CustomEvent('show'));

            this.#hideOptionsFun = this.#hideOptions.bind(this);
            this.ownerDocument.addEventListener('mousedown', this.#hideOptionsFun);

            this.#options.forEach((op) => {
                op._hideOptions();
            });
            window.setTimeout(() => {
                if (this.#selectedOption) {
                    this.#selectedOption._checkSelected();
                }
            }, 20);

            this.unInvalidStyle();
        }
    }
    #hideOptions() {
        this.#droped = false;
        this.#toggleStyles();
        dom.removeClass(this.#optionsArea, 'visible');
        dom.addClass(this.#optionsArea, 'invisible');
        dom.removeClass(document.body, 'ooui-select-visible');
        this.ownerDocument.removeEventListener('mousedown', this.#hideOptionsFun);

        this.#options.forEach((op) => {
            op._hideOptions();
        });

        dom.addClass(this.#optionsPositionNode, 'hide');

        this.dispatchEvent(new CustomEvent('hide'));
    }
    get value() {
        return this._props.value;
    }
    set value(v) {
        this.setAttribute('value', v);
    }
    get text() {
        return this._props.text;
    }
    set text(v) {
        this._props.text = v;
    }
}
