import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import {I18n} from '../../common/i18n.js';
import html from './template/$input.html?raw';
import css from './template/$input.scope.css?raw';

class $Input extends OOComponent {
    static prop = {
        leftIcon: '',
        rightIcon: '',
        width: '',
        height: '',
        inputStyle: '',
        labelStyle: '',
        labelAlign: 'right',
        viewStyle: '',
        disabled: false,
        readmode: false,
        label: '',
        bgcolor: '#ffffff',
        skin: '',
        appearance: '',
        selectFile: 'Select File',
        notSelectedFile: 'Not Selected Files',
        validity: '', //表单验证提示信息
        validityBlur: false, //失去焦点时是否进行有效性验证
        autoSize: false,
        resize: false,
        skinMode: '',   //样式模式，在content元素上添加 class。默认模式（pc），可选（mobile）
        col: 1, 
        del: true, //是否显示删除按钮
        fileButtonStyle: 'simple', //default or cancel or light or simple or icon
        fileButtonIcon: 'create',
        

        placeholder: '',
        readonly: false,
        type: 'text',
        autofocus: false,
        form: '',
        max: '',
        maxlength: '',
        min: '',
        minlength: '',
        name: '',
        pattern: '',
        size: '',
        spellcheck: '',
        src: '',
        step: '',
        tabindex: '',
        value: '',
        title: '',
        list: '',
        autocomplete: '',
        accept: '',
        alt: '',
        capture: '',
        checked: '',
        dirname: '',
        formaction: '',
        formenctype: '',
        formmethod: '',
        formnovalidate: '',
        formtarget: '',
        multiple: '',
        required: false,
        cols: '',
        rows: '',
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att) => {
            return hyphenate(att);
        });
    }
    _elements = {
        label: null,
        input: null,
        file: null,
        box: null,
        prefix: null,
        suffix: null,
        labelText: null,
        fileList: null,
        emptyInfo: null,
        invalidHint: null,
        viewText: null,
        view: null,
        requiredFlag: null,
        body: null,
        fileLabel: null,
        fileButton: null,
        fileEmptyFlag: null
    };
    _setPropMap = {
        leftIcon: (old) => {
            // console.log(old)
            // if (!old || this._props.leftIcon !== old){
            dom.removeClass(this._elements.prefix, `ooicon-${old} left-icon`);
            if (!!this._props.leftIcon) {
                dom.addClass(this._elements.prefix, `ooicon-${this._props.leftIcon} left-icon`);
            }
            // }
        },
        rightIcon: (old) => {
            // if ( !old || this._props.rightIcon !== old){
            dom.removeClass(this._elements.suffix, `ooicon-${old} right-icon`);
            if (!!this._props.rightIcon) {
                dom.addClass(this._elements.suffix, `ooicon-${this._props.rightIcon} right-icon`);
            }
            // }
        },
        width: () => {
            if (this._props.width) {
                this._elements.box.style.width = this._props.width;
                this._elements.box.parentElement.style.minWidth = this._props.width;
            }
        },
        height: () => {
            if (this._props.height) {
                this._elements.box.style.height = this._props.height;
                this._elements.box.parentElement.style.minHeight = this._props.height;
            }
        },
        skinMode: (old)=>{
            if (old) dom.removeClass(this._content, old);
            if (this._props.skinMode) dom.addClass(this._content, this._props.skinMode);
        },
        style: () => {
            // dom.toggleAttr(this._elements.box, 'style', this._props.style);
            this._setPropMap.width();
            this._setPropMap.height();
        },
        inputStyle: () => {
            dom.toggleAttr(this._elements.input, 'style', this._props.inputStyle);
        },
        labelStyle: () => {
            dom.toggleAttr(this._elements.labelText, 'style', this._props.labelStyle);
        },
        labelAlign: () => {
            switch (this._props.labelAlign) {
                case 'right':
                    dom.setStyle(this._elements.labelText, 'justify-content', 'flex-end');
                    break;
                case 'center':
                    dom.setStyle(this._elements.labelText, 'justify-content', 'center');
                    break;
                default:
                    dom.setStyle(this._elements.labelText, 'justify-content', 'flex-start');
            }
        },
        viewStyle: () => {
            if (this._props.readmode){
                dom.set(this._elements.body, 'style', this._props.viewStyle);
            }else{
                dom.set(this._elements.body, 'style', '');
            }
        },
        bgcolor: () => {
            if (this._props.bgcolor) {
                dom.setStyle(this._elements.box, 'background-color', this._props.bgcolor);
            } else {
                dom.setStyle(this._elements.box, 'background-color', 'transparent');
            }
        },
        del: () => {
            this._checkFileRead();
        },
        disabled: () => {
            dom.toggleAttr(this._elements.input, 'disabled', this._props.disabled);
            dom.checkClass(this._elements.box, 'disabled', this._props.disabled);
            dom.checkClass(this._elements.label, 'disabled', this._props.disabled);
            if (this._elements.fileLabel) dom.checkClass(this._elements.fileLabel, 'hide', this._props.disabled);
            this._checkFileRead();
        },
        readmode: () => {
            dom.toggleAttr(this._elements.input, 'readonly', this._props.readmode);
            dom.checkClass(this._elements.box, 'readmode', this._props.readmode);
            dom.checkClass(this._elements.label, 'readmode', this._props.readmode);

            if (this._elements.fileLabel) dom.checkClass(this._elements.fileLabel, 'hide', this._props.readmode);

            this._setPropMap.viewStyle();
            this._setPropMap.required();
            this._checkFileRead();
        },
        readonly: () => {
            dom.toggleAttr(this._elements.input, 'readonly', this._props.readonly);
            if (this._elements.fileInput){
                if (this._props.readonly){
                    this._elements.fileInput.setAttribute('disabled', 'true');
                }else{
                    this._elements.fileInput.removeAttribute('disabled');
                }
            }
            // if (this._elements.fileLabel) dom.checkClass(this._elements.fileLabel, 'hide', this._props.readonly);
            this._checkFileRead();
        },
        label: () => {
            if (this._props.label) {
                this._elements.labelText.textContent = this._props.label;
                dom.removeClass(this._elements.labelText, 'hide');
                dom.removeClass(this._elements.label, 'hide');
            } else {
                this._elements.labelText.textContent = '';
                dom.addClass(this._elements.labelText, 'hide');
                dom.addClass(this._elements.label, 'hide');
            }
        },
        skin: () => {
            this._useSkin(this._props.skin);
        },
        value: () => {
            try {
                this._elements.input.value = this._props.value;
            } catch (e) {}
            if (this._props.value) {
                this._elements.viewText.textContent = this._props.value;
                if (this._props.type === 'color') {
                    this._elements.view.style.color = this._props.value;
                }
            } else {
                this._elements.viewText.innerHTML = '-';
            }
        },
        appearance: () => {
            dom.checkClass(this._elements.input, 'appearance', this._props.appearance === 'none');
        },
        validity: () => {},
        type: () => {
            this._checkTypeFile();
        },
        autoSize: () => {},
        resize: () => {
            dom.toggleClass(this._elements.input, 'resize', this._props.resize);
        },
        required: () => {
            dom.toggleAttr(this._elements.input, 'required', this._props.required);
            dom.checkClass(
                this._elements.requiredFlag,
                'hide',
                !this._props.required || this._props.readmode || this._props.disabled,
            );
        },
        selectFile: ()=>{
            const button = this._elements.file.querySelector('oo-button');
            if (this._props.type === 'file') {
                button.setAttribute('text', this._props.selectFile || lp['selectFile']);
            }
        },
        fileButtonStyle: ()=>{
            this._elements.fileButton.setAttribute('type', this._props.fileButtonStyle);
        },
        fileButtonIcon: ()=>{
            this._elements.fileButton.setAttribute('left-icon', this._props.fileButtonIcon);
        },
        placeholder: () => {
            dom.toggleAttr(this._elements.input, 'placeholder', this._props.placeholder);
            if (this._props.type==='file'){
                this._checkFileRead();
                // this._elements.emptyInfo.textContent = this._props.placeholder || '未选择文件';
            }
        },
        $default: (key) => {
            if (key === 'value') {
                this.value = this._props[key];
                // this._elements.input.value = this._props[key];
            } else {
                dom.toggleAttr(this._elements.input, key, this._props[key]);
                if (this._elements.fileInput) dom.toggleAttr(this._elements.fileInput, key, this._props[key]);
            }
        }
    };
    _checkTypeFile(){
        if (this._props.type === 'file'){
            if (!this._elements.fileInput){
                this._elements.fileInput = this._elements.input.cloneNode(true);
                this._elements.fileInput.setAttribute('class', 'fileInput');
                this._elements.fileInput.setAttribute('type', 'file');
                this._elements.fileInput.setAttribute('id', 'fileinput');
                this._elements.view.append(this._elements.fileInput);

                if (this._props.readonly)  this._elements.fileInput.setAttribute('disabled', 'true');
            }
            
            if (this._elements.file){
                dom.removeClass(this._elements.file, 'hide');
            }
        }else{
            if (this._elements.file) dom.addClass(this._elements.file, 'hide');
            dom.toggleAttr(this._elements.input, 'type', this._props.type);
        }
        dom.toggleAttr(this._elements.view, 'type', this._props.type);
    }

    _checkFileRead() {
        const isRead = this._props.readonly || this._props.readmode || this._props.disabled;
        const isReadMode = this._props.readmode || this._props.disabled;
        if (this._elements.fileList){
            const isDel = !(isRead) && this._props.del;
            const files = this._elements.fileList.querySelectorAll('oo-file');
            for (const file of files) {
                file.setAttribute('del', isDel);
            }
        }

        if (isReadMode){
            if (this._elements.emptyInfo){
                this._elements.emptyInfo.textContent = '';
            }
            if (isReadMode && this._elements.fileEmptyFlag) dom.show(this._elements.fileEmptyFlag);
        }else{
            if (this._elements.fileEmptyFlag) dom.hide(this._elements.fileEmptyFlag);
            if (this._elements.emptyInfo){
                this._elements.emptyInfo.textContent = this._props.placeholder || '未选择文件';
            }
        }
    }
    constructor() {
        super();
        const lp = I18n.get('index').o('component');
        ['selectFile', 'notSelectedFile'].forEach((k) => ($Input.prop[k] = lp[k]));
    }
    _init(name, clazz, ohtml, ocss) {
        this._initialize(name, clazz || $Input, ohtml || html, ocss || css);
    }

    _inputFocus(){
        this._elements.input.addEventListener('focus', () => {
            if (!this._props.readmode) {
                dom.addClass(this._elements.box, 'focus');
                dom.addClass(this._elements.label, 'focus');
            }
        });
    }
    _fileInputFocus(){
        if (this._elements.fileInput) this._elements.fileInput.addEventListener('focus', (e) => {
            if (!this._props.readmode) {
                dom.addClass(this._elements.box, 'focus');
                dom.addClass(this._elements.label, 'focus');
            }
        });
    }
    _canValidityBlur = false;
    _inputBlur(){
        this._elements.input.addEventListener('blur', () => {
            this.unInvalidStyle();
            if (this._props.validityBlur && this._canValidityBlur) {
                this.checkValidity();
            }
            dom.removeClass(this._elements.box, 'focus');
            dom.removeClass(this._elements.label, 'focus');
        });
        
    }
    _fileInputBlur(){
        if (this._elements.fileInput) this._elements.fileInput.addEventListener('blur', (e) => {
            this.unInvalidStyle();
            if (this._props.validityBlur && this._canValidityBlur) {

                this.checkValidity();
            }
            dom.removeClass(this._elements.box, 'focus');
            dom.removeClass(this._elements.label, 'focus');
        });
    }

    _inputInput(){
        this._elements.input.addEventListener('input', () => {
            if( !this.constructor._name || this.constructor._name !== 'OOCurrency' ){
                this.value = this._elements.input.value;
                this._elements.viewText.textContent = this._elements.input.value;
                this.unInvalidStyle();
            }
        });
    }
    _inputChange(){
        this._elements.input.addEventListener('change', (e) => {
            this._canValidityBlur = true;
            this.unInvalidStyle();
            this.dispatchEvent(new Event('change', e));
        });
    }

    _fileInputChange(){
        if (this._elements.fileInput) this._elements.fileInput.addEventListener('change', (e) => {
            //type=file时，选择文件后触发
            this._canValidityBlur = true;
            this.unInvalidStyle();
            this._changeFileValue(e);

            this.dispatchEvent(new Event('change', e));
        });
    }

    _inputInvalid(){
        this._elements.input.addEventListener('invalid', (e) => {
            this.#checkInvalid(e.target.validity);
        });
    }
    _setEvent() {
        super._setEvent();
        this._inputFocus();
        this._inputBlur();
        this._inputInput();
        this._inputChange();
        this._inputInvalid();
        this._fileInputFocus();
        this._fileInputBlur();
        this._fileInputChange();
    }

    _changeFileValue(e){
        if (this._props.type === 'file') {
            if (e.target.files.length) {
                dom.empty(this._elements.fileList);
                for (const file of e.target.files) {
                    const fileNode = dom('oo-file', {filename: file.name, size: file.size});
                    this._elements.fileList.append(fileNode);
                }
            } else {
                dom.empty(this._elements.fileList);
                this._elements.fileList.innerHTML = `<div class="emptyInfo">${this._props.notSelectedFile}</div>`;
            }
        }
    }

    /** 此处处理表单数据验证。做了一下几件事：
     * 1、监听 input 对象的 invalid 原生事件，当前数据无效时触发。
     * 2、实现 oo-input 的 setCustomValidity、checkValidity 和 reportValidity三个方法，以兼容原生的表单验证方法
     * 3、模板中增加了 class为"invalidHint"的元素，用于显示错误信息。
     * 4、在输入框的 input 事件中取消 无效 视觉状态。
     *
     * 如何使用输入框验证功能：
     * 1、与 原生表单数据验证功能兼容，可以使用如 required、pattern等属性来检测数据有效性。
     *    @see https://developer.mozilla.org/zh-CN/docs/Web/HTML/Element/input#%E5%AE%A2%E6%88%B7%E7%AB%AF%E9%AA%8C%E8%AF%81
     * 2、要自定义验证错误提示，可以增加属性 validity="提示信息" 来设置。
     * 3、要自定义更复杂的错误提示，可以监听 oo-input 的 invalid 事件， 如下示例：
     * ooinput.addEventListener('invalid', (e)=>{
     *     const o = {
     *         valueMissing: '当required为true，但没有填写内容时的提示',
     *         typeMismatch: '值的类型不正确，如type=email，但没有 @ 时提示',
     *         patternMismatch: '当值不匹配匹配 pattern 属性的正则表达式时提示',
     *         tooLong: '当设置了属性maxlength，但值的长度过长时提示',
     *         tooShort: '当设置了属性minlength，但值的长度过短时提示',
     *         rangeUnderflow: '当值大于 max 属性所定义的最大值时提示',
     *         rangeOverflow: '当值小于 min 属性所定义的最小值时提示',
     *         stepMismatch: '值不满足步进增量时提示。（step默认值为 1，所以对于 type="number" 来说，只有整数才有效。设置step="any" 永远不会抛出这个错误。）',
     *         badInput: '当输入值无法转换时提示，比如当 type="number" 时，输入了无法转换为数字的字符串内容',
     *         customError: '自定义的错误内容，通过 setCustomValidity 方法设置，一般不在此事件中处理' && ''
     *     }
     *     //通过 e.detail 获取 验证有效性状态对象：ValidityState
     *     for (const k in o){
     *         if (e.detail[k]){
     *             if (o[k]){
     *                 e.target.setCustomValidity(o[k]);
     *                 break;
     *             }
     *         }
     *     }
     * });
     *
     * 4、如果原生属性验证不能满足要求，可以使用自定义验证，方法是监听 validity 事件，如下示例：
     * ooinput.addEventListener('validity', (e)=>{
     *     //执行数据验证
     *     //如果由于 原因A 验证不通过：
     *     if ('<原因A>'){
     *         e.target.setCustomValidity('由于原因A验证不通过的自定义提示消息！');
     *     }
     *
     *     //如果由于 原因B 验证不通过：
     *     if ('<原因B>'){
     *         e.target.setCustomValidity('由于原因B验证不通过的自定义提示消息！');
     *     }
     * })
     *
     * 5、要执行输入框验证，可使用 checkValidity（推荐） 或 reportValidity方法。
     *    它们都返回一个boolen数据，表示验证有没有通过。区别是 reportValidity方法会出现系统的错误提示，而 checkValidity方法不会。
     *    要在 blur 事件中进行有效性校验，可以设置属性: validity-blur="true"
     *
     * 6、@todo 后续考虑加入 oo-form 元素，oo-form的 checkValidity 和 reportValidity方法，可以校验form中的所有表单元素。todo
     *
     * */
    #setValidationMessage() {
        if (!this._elements.input.validity.customError) {
            if (this._props.validity) this._elements.input.setCustomValidity(this._props.validity);
        }
        if (this._elements.invalidHint)
            this._elements.invalidHint.textContent = this._elements.input.validationMessage;
        return this._elements.input.validationMessage;
    }
    #checkInvalid(detail) {
        if (detail['valueMissing']) {
            const lp = I18n.get('index').o('component');
            this._elements.input.setCustomValidity(lp.t('valueMissing', {label: this._props.label || lp['this']}));
        }
        this.dispatchEvent(new CustomEvent('invalid', {detail}));
        dom.addClass(this._elements.input, 'invalid');
        if (this.#setValidationMessage()) dom.addClass(this._elements.invalidHint, 'show');
    }
    unInvalidStyle(detail) {
        this._elements.input.setCustomValidity('');
        dom.removeClass(this._elements.input, 'invalid');

        this._elements.invalidHint.textContent = '';
        dom.removeClass(this._elements.invalidHint, 'show');
    }

    setCustomValidity() {
        return this._elements.input.setCustomValidity(...arguments);
    }
    checkValidity() {
        this.unInvalidStyle();
        const readonlyFlag = this._elements.input.hasAttribute('readonly');
        if (readonlyFlag) this._elements.input.removeAttribute('readonly');

        this.dispatchEvent(new CustomEvent('validity'));
        const flag = this._elements.input.checkValidity(...arguments);

        if (readonlyFlag) this._elements.input.setAttribute('readonly', true);

        dom.toggleAttr(this, 'valid', flag);

        return flag;
    }
    reportValidity() {
        const readonlyFlag = this._elements.input.hasAttribute('readonly');
        if (readonlyFlag) this._elements.input.removeAttribute('readonly');

        this.dispatchEvent(new CustomEvent('validity'));
        const flag = this._elements.input.reportValidity(...arguments);

        if (readonlyFlag) this._elements.input.setAttribute('readonly', true);
        return flag;
    }

    _render() {
        this._setPropMap.viewStyle();
        this._setPropMap.value();
        this._setPropMap.type();
    }
    _connected(){
        this._setPropMap.labelAlign();
    }
    focus() {
        this._elements.input.focus();
    }
    blur() {
        this._elements.input.blur();
    }
    get files() {
        return this._elements.input.files;
    }
    get value() {
        switch (this._props.type) {
            case 'number':
                return parseFloat(this._elements.input.value);
            default:
                return this._elements.input.value;
        }
    }
    set value(v) {
        this.setAttribute('value', v);
    }
}

export {$Input};
