import {dom} from '@o2oa/util';
import {$Check} from './$check.js';

export default class OOCheckbox extends $Check{
    constructor() {
        super();
        this._init('oo-checkbox');
    }
    _render(){
        this._elements.input.type = 'checkbox';
        dom.addClass(this._elements.checkIcon, 'ooicon-checkmark');
        super._render();
    }
}
