import {$Check} from "./$check.js";
import css from './template/radio.scope.css?raw';

export default class OORadio extends $Check{
    constructor() {
        super();
        this._init('oo-radio');
        this._useCss(css);
    }
    _unCheckedOther(){
        const radios = this.ownerDocument.querySelectorAll(`oo-radio[name='${this._props.name}']`);
        radios.forEach((r)=>{
            if (r!==this){
                if (r.checked) r._unchecked();
            }
        })
    }
    _render(){
        this._elements.input.type = 'radio';
        const checked = this._elements.input.checked;
        if (checked) this._unCheckedOther();

        super._render();
    }
}
