import {dom, hyphenate, typeOf} from '@o2oa/util';
import {OOComponent} from '../base.js';
import html from './template/$selector.html?raw';
import css from './template/$selector.scope.css?raw';
import {selectOrg} from '../../common/org/org.js';
import {exec} from '@o2oa/util';
import {I18n} from '../../common/i18n.js';

class $Selector extends OOComponent{
    static prop = {
        leftIcon: '',
        rightIcon: 'create',
        itemType: 'text',
        isTooltip: true,
        label: '',
        inputStyle: '',
        labelStyle: '',
        labelAlign: 'right',
        viewStyle: '',
        bgcolor: '#ffffff',
        skin: '',
        placeholder: '',
        disabled: false,
        readonly: false,
        readmode: false,
        value: [],
        selConfig: '',
        selOptions: '',
        selTitle: '',
        count: 0,
        validity: '',   //表单验证提示信息
        validityBlur: false,    //失去焦点时是否进行有效性验证
        skinMode: '',   //样式模式，在content元素上添加 class。默认模式（pc），可选（mobile）

        required: false
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _elements={
        label: null,
        input: null,
        box: null,
        body: null,
        prefix : null,
        suffix: null,
        labelText: null,
        placeholder: null,
        inputContent: null,
        inputValidity: null,
        invalidHint: null,
        requiredFlag: null
    };
    _setPropMap={
        leftIcon: (old)=>{
            // if (this._props.leftIcon !== old){
                dom.removeClass(this._elements.prefix, `ooicon-${old} left-icon`);
                if (!!this._props.leftIcon){
                    dom.addClass(this._elements.prefix, `ooicon-${this._props.leftIcon} left-icon`)
                }
            // }
        },
        rightIcon: (old)=>{
            // if ( !old || this._props.rightIcon !== old){
                dom.removeClass(this._elements.suffix, `ooicon-${old} right-icon`);
                if (!!this._props.rightIcon){
                    dom.addClass(this._elements.suffix, `ooicon-${this._props.rightIcon} right-icon`)
                }
            // }
        },
        skinMode: (old)=>{
            if (old) dom.removeClass(this._content, old);
            if (this._props.skinMode) dom.addClass(this._content, this._props.skinMode);
        },
        itemType: (old)=>{
            if (!this._props.itemType) this._props.itemType = 'simple';
            if (old !== this._props.itemType){
                const items = this._elements.inputContent.querySelectorAll('oo-org');
                items.forEach((item)=>{
                    item.setAttribute('type', this._props.itemType);
                });
            }
        },
        isTooltip: ()=>{
            const flag = (this._props.isTooltip!==false);
            const items = this._elements.inputContent.querySelectorAll('oo-org');
            items.forEach((item)=>{
                item.setAttribute('is-tooltip', flag);
            });
        },
        disabled: ()=>{
            dom.toggleAttr(this._elements.input, 'disabled', this._props.disabled);
            dom.checkClass(this._elements.box, 'disabled', this._props.disabled);
            dom.checkClass(this._elements.label, 'disabled', this._props.disabled);
        },
        readmode: ()=>{
            if (!this.value || !this.value.length) dom.checkClass(this._elements.placeholder, 'hide', this._props.readmode);
            dom.toggleAttr(this._elements.input, 'readmode', this._props.readmode);
            dom.checkClass(this._elements.box, 'readmode', this._props.readmode);
            dom.checkClass(this._elements.label, 'readmode', this._props.readmode);

            if (!this._props.readmode){
                dom.toggleAttr(this._elements.body, 'style', '');
            }else{
                dom.toggleAttr(this._elements.body, 'style', this._props.viewStyle);
            }
            this._setPropMap.required();
        },
        label: ()=>{
            if (this._props.label){
                this._elements.labelText.textContent = this._props.label;
                dom.removeClass(this._elements.label, 'hide');
            }else{
                this._elements.labelText.textContent = '';
                dom.addClass(this._elements.label, 'hide');
            }
        },
        skin: ()=>{
            this._useSkin(this._props.skin);
        },
        value: ()=>{
            this.#setValue(this._props.value);
        },
        placeholder: ()=>{
            if (this.value && this.value.length){
                dom.addClass(this._elements.placeholder, 'hide');
            }else{
                this._elements.inputContent.innerHTML = this._props.readmode ? '<div style="padding: 0.1em;">-</div>' : '<div style="padding: 0.1em;">&nbsp;</div>';
                if (!this._props.readmode && !this._props.readonly && !this._props.disabled) dom.removeClass(this._elements.placeholder, 'hide');
                if (this._props.placeholder){
                    this._elements.placeholder.textContent = this._props.placeholder;
                }else{
                    this._elements.placeholder.innerHTML = '&nbsp';
                }
            }
        },
        readonly: ()=>{
            this._setPropMap.placeholder();
        },
        inputStyle: ()=>{
            dom.toggleAttr(this._elements.input, 'style', this._props.inputStyle);
        },
        labelStyle: ()=>{
            dom.toggleAttr(this._elements.labelText, 'style', this._props.labelStyle);
        },
        // labelAlign: ()=>{
        //     dom.checkClass(this._elements.labelText, 'flex-end', this._props.labelAlign==='right');
        // },
        labelAlign: ()=>{
            switch (this._props.labelAlign){
                case 'right':
                    dom.setStyle(this._elements.labelText, 'justify-content', 'flex-end');
                    break;
                case 'center':
                    dom.setStyle(this._elements.labelText, 'justify-content', 'center');
                    break;
                default:
                    dom.setStyle(this._elements.labelText, 'justify-content', 'flex-start');
            }
        },
        viewStyle: ()=>{
            if (this._props.readmode) dom.toggleAttr(this._elements.body, 'style', this._props.viewStyle);
        },
        bgcolor: ()=>{
            if (this._props.bgcolor){
                dom.setStyle(this._elements.box, 'background-color', this._props.bgcolor);
            }else{
                dom.setStyle(this._elements.box, 'background-color', 'transparent');
            }

        },
        required: () => {
            (!this._props.required) ? this._elements.inputValidity.removeAttribute('required') : this._elements.inputValidity.setAttribute('required', this._props.required);
            dom.checkClass(this._elements.requiredFlag, 'hide', (!this._props.required || this._props.readmode || this._props.disabled));
        },

        $default: ()=>{}
    };
    #setValue(value){
        let v = value;
        v = (Array.isArray(v)) ? v : [v];
        v = this._props.count ? v.slice(0, this._props.count) : v;
        this._props.value = v;

        // dom.addClass(this._elements.placeholder, 'hide');
        if (v && v.length){
            // dom.removeClass(this._elements.input, 'one-line');
            dom.addClass(this._elements.placeholder, 'hide');
            dom.empty(this._elements.inputContent);
            this.#createValueItem(v);
            this._elements.inputValidity.value = v;
        }else{
            this._elements.inputContent.innerHTML = this._props.readmode ? '<div style="padding: 0.1em;">-</div>' : '<div style="padding: 0.1em;">&nbsp;</div>';

            if (!this._props.readmode && !this._props.readonly && !this._props.disabled) dom.removeClass(this._elements.placeholder, 'hide');
            if (this._props.placeholder){
                this._elements.placeholder.textContent = this._props.placeholder;
            }else{
                this._elements.placeholder.innerHTML = '&nbsp';
            }

        }
        // dom[(v && v.length) ? 'addClass' : 'removeClass'](this._elements.placeholder, 'hide');

    }
    addValue(v){
        const value = v.dn || v;
        switch (this._props.value){
            case 'string':
                if (!this._props.count || this._props.value.split(/\s*,\s*/g).length<this._props.count){
                    this._props.value += ', '+value;
                }
                break;
            case 'array':
                if (!this._props.count || this._props.value.length<this._props.count) {
                    this._props.value.push(value);
                }
                break;
        }
        this.#createValueItem(value);
    }
    setTooltip(e, value){
        const target = e.detail.target;
        target.tooltip = value;
    }
    #createValueItem(v){
        switch (typeOf(v)){
            case 'string':
                const list = v.split(/\s*,\s*/g);
                if (list.length===1){
                    if (list[0]){
                        const node = dom('oo-org', {value: list[0], type: this._props.itemType});
                        if (this._props.itemType==='text'){
                            node.setAttribute('type', 'text');
                        }
                        if (this._props.isTooltip===false){
                            node.setAttribute('is-tooltip', false);
                        }

                        node.addEventListener('tooltip', (e)=>{
                            this.dispatchEvent(new CustomEvent('tooltip', {
                                detail: {
                                    target: e.target
                                }
                            }));
                        });
                        this._elements.inputContent.append(node);
                    }
                }else{
                    this.#createValueItem(list)
                }
                break;
            case 'array':
                v.forEach((i)=>{
                    this.#createValueItem(i)
                });
                break;
        }
    }
    constructor() {
        super();
    }
    _init(name, clazz, ohtml, ocss){
        this._initialize(name,clazz||$Selector, ohtml||html, ocss||css);
    }
    // _render(name) {
    //     this.#setValue(this._props.value);
    // }

    _setEvent(){
        super._setEvent();

        this._elements.box.addEventListener('mouseover', ()=>{
            if (!this._props.disabled && !this._props.readonly){
                dom.addClass(this._elements.box, 'focus');
                dom.addClass(this._elements.label, 'focus');
            }
        });
        this._elements.box.addEventListener('mouseout', ()=>{
            if (!this._props.disabled && !this._props.readonly) {
                dom.removeClass(this._elements.box, 'focus');
                dom.removeClass(this._elements.label, 'focus');
            }
        });
        this._elements.box.addEventListener('click', ()=>{
            if (!this._props.disabled && !this._props.readonly) {
                this.dispatchEvent(new MouseEvent('select'));
                if (this._props.selConfig || this._props.selOptions){
                    this.select();
                }
            }
        });

        this._elements.inputValidity.addEventListener('invalid', (e)=>{
            this.#checkInvalid(e.target.validity);
        });
    }
    select(){
        // const options = this._props.selOptions ? JSON.parse(this._props.selOptions) : {};
        const options = this._props.selOptions ? exec('return ' + this._props.selOptions) : {};
        if (this._props.selConfig) options.config = this._props.selConfig;
        options.selected = this.value;
        options.count = this._props.count;

        const target = dom.getPositionParent(this);
        selectOrg(this._props.selTitle, options, target).then((selected)=>{
            if (selected.type==='ok'){
                const values = selected.data.map((item)=>{
                    return item.dn || item.id || item;
                })
                this.#setValue(values);

                this.unInvalidStyle();
                if (this._props.validityBlur){
                    this.checkValidity();
                }

                this.dispatchEvent(new MouseEvent('change'));
            }else{
                this.dispatchEvent(new MouseEvent('cancel'));
            }
        });
    }
    focus(){
        this._elements.input.focus();
    }
    blur(){
        this._elements.input.blur();
    }

    setCustomValidity(){
        return this._elements.inputValidity.setCustomValidity(...arguments);
    }
    checkValidity(){
        this.dispatchEvent(new CustomEvent("validity"));
        return this._elements.inputValidity.checkValidity(...arguments);
    }
    reportValidity() {
        this.dispatchEvent(new CustomEvent("validity"));
        return this._elements.inputValidity.reportValidity(...arguments);
    }

    #setValidationMessage(){
        if (!this._elements.inputValidity.validity.customError){
            if (this._props.validity) this._elements.inputValidity.setCustomValidity(this._props.validity);
        }
        if (this._elements.invalidHint) this._elements.invalidHint.textContent = this._elements.inputValidity.validationMessage;
        return this._elements.inputValidity.validationMessage;
    }
    #checkInvalid(detail){
        if (detail['valueMissing']) {
            lp = I18n.get('index').o('component');
            this._elements.input.setCustomValidity(lp.t('valueMissing', {label: this._props.label || lp['this']}));
        }
        this.dispatchEvent(new CustomEvent("invalid", {detail}));
        dom.addClass(this._elements.inputValidity, 'invalid');
        if (this.#setValidationMessage())
            dom.addClass(this._elements.invalidHint, 'show');
    }
    unInvalidStyle(){
        this._elements.inputValidity.setCustomValidity('');
        dom.removeClass(this._elements.inputValidity, 'invalid');

        this._elements.invalidHint.textContent = '';
        dom.removeClass(this._elements.invalidHint, 'show');
    }
    _connected(){
        this._setPropMap.labelAlign();
    }
}

export {$Selector}
