export default "body{\r\n    display: inline-block;\r\n}\r\n* {\r\n    box-sizing: border-box;\r\n    user-select: none;\r\n}\r\n.content{\r\n    --color: var(--oo-color-text3);\r\n    --bg: var(--oo-color-gray2);\r\n    --radius: var(--oo-default-radius);\r\n    --current-color: var(--oo-color-text-white);\r\n    --current-bg: var(--oo-color-main);\r\n}\r\n\r\n.button {\r\n    border-radius: var(--radius);\r\n    border: 0;\r\n    padding: 0.5em 0.5em;\r\n    cursor: pointer;\r\n    color: var(--color);\r\n    background-color: var(--bg);\r\n    transition: background 0.2s, color 0.2s;\r\n    text-align: center;\r\n    line-height: 1em;\r\n    height: 100%;\r\n    width: 100%;\r\n    display: flex;\r\n    align-items: center;\r\n}\r\n.button.current{\r\n    background-color: var(--current-bg)!important;\r\n    color: var(--current-color)!important;\r\n}\r\n.box>div{\r\n    /*margin: 0 0.2em;*/\r\n}\r\ndiv.prefix{\r\n    display: none;\r\n    font-size: 0.9em;\r\n}\r\ndiv.suffix{\r\n    display: none;\r\n    font-size: 0.9em;\r\n}\r\ndiv.text{\r\n    height: 100%;\r\n    display: inline-flex;\r\n    align-items: center;\r\n    justify-content: baseline;\r\n}\r\n.box{\r\n    display: grid;\r\n    gap: 0.2em;\r\n    grid-auto-flow: column;\r\n    align-items: baseline;\r\n}\r\n.box>.icon{\r\n    display: inline-block;\r\n}\r\n\r\n\r\n.box>.close{\r\n    display: inline-block;\r\n    border-radius: var(--oo-default-radius);\r\n    font-size: 0.8em;\r\n    padding: 0.1em;\r\n    margin: 0;\r\n}\r\n.box>.menu{\r\n    display: none;\r\n    border-radius: var(--oo-default-radius);\r\n    font-size: 0.8em;\r\n    padding: 0.1em;\r\n    margin: 0;\r\n}\r\n.button .close:hover{\r\n    background-color: var(--color);\r\n    color: var(--bg);\r\n}\r\n.button.current .close:hover, .menu:hover{\r\n    background-color: var(--current-color);\r\n    color: var(--current-bg);\r\n}\r\n\r\n.button.current .menu{\r\n    display: inline-block;\r\n}\r\n\r\n.hide{\r\n    display: none !important;\r\n}\r\n"