import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';

const html = `
<div class="content">
</div>
`
const svgs = import.meta.glob('../../assets/icons/*.svg', { as: 'raw'});

export default class OOIcon extends OOComponent{
    static prop = {
        type: ''
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _setPropMap={
        type: ()=>{
            this._render();
        }
    }
    constructor() {
        super();
        this._initialize('oo-icon',OOIcon, html, `svg{width:100%; height: 100%}`);
    }
    async _render() {
        dom.empty(this._content);
        if (this._props.type){
            const svg = (await svgs[`../../assets/icons/${this._props.type}.svg`]());
            dom.empty(this._content);
            this._content.insertAdjacentHTML('afterbegin', svg);
        }
    }
}
