import {dom, hyphenate, typeOf} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from "./template/org.scope.css?raw";
import {tooltip} from '../../api/tooltip.js';
import {getPersonIcon, cn, typeOfOrg, getGroup, getIdentity, getPerson, getRole, getUnit, processDesigner} from '../../common/org/org.js';

const html = `
<div class="content">
    <div class="icon"></div>
    <div class="text"></div>
</div>
`
export default class OOOrg extends OOComponent{
    static prop = {
        type: 'simple',    //icon, simple, text
        value: '',
        text: '',
        data: {},
        entity: '',
        cssLink: '',
        skin: '',
        isTooltip: true
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _elements = {
        icon: null,
        text: null
    }
    _canRender() {
        return super._canRender(null, ['value', 'data']);
    }

    _setPropMap={
        type: ()=>{
            if (this._canRender()) this.#renderIcon();
        },
        value: ()=>{
            if (this._canRender()) {
                this.#renderIcon();
                this.#renderText();
            }
        },
        data: ()=>{
            if (this._canRender()){
                this.#renderIcon();
                this.#renderText();
            }
        },
        text: ()=>{
            if (this._props.text){
                this._elements.text.textContent = this._props.text;
            }else{
                this.#renderText();
            }
        },
        entity: ()=>{
            if (this._canRender()){
                this.#renderIcon();
                this.#renderText();
            }
        },
        isTooltip: ()=>{
            this.tooltip = (this._props.isTooltip!==false) ? (this.tooltip || this.defaultTooltip) : null;
        },
        cssLink: ()=>{
            this.useCssLink(this._props.cssLink);
        },
        skin: ()=>{
            this._useSkin(this._props.skin);
        }
    }
    constructor() {
        super();
        this._initialize('oo-org', OOOrg, html, css);
    }
    #renderText(){
        if (this._props.text){
            this._elements.text.textContent = this._props.text;
        }else if (this._props.value){
            this._elements.text.textContent = cn(this._props.value);
        }else{
            this._elements.text.textContent = this.#getText(this._props.data);
        }
    }
    #getText(data){
        // @todo 根据不同类型的数据获取不同的显示值
        return (data.dn) ? cn(data.dn) : data.name;
    }
    #renderIcon(){
        switch (this._props.type){
            case 'text':
                dom.addClass(this._elements.icon, 'hide');
                break;
            // case 'icon':
            //     dom.addClass(this._elements.icon, 'avatar');
            //     dom.removeClass(this._elements.icon, 'hide');
            //     break;
            default:
                const icon = this.#getIcon(this._props.entity || typeOfOrg(this._props.value || this._props.data));

                if (typeOf(icon)==='function'){
                    dom.setStyle(this._elements.icon, 'background-image', `url('${icon(this._props.value || this._props.data)}')`);
                }else{
                    dom.addClass(this._elements.icon, icon);
                }
                dom.removeClass(this._elements.icon, 'hide');
        }
    }
    #getIcon(t){
        return {
            'I': (v)=>{return getPersonIcon(v.person?.dn || v.dn || v);},
            'P': (v)=>{return getPersonIcon(v.dn || v);},
            'U': 'ooicon-unit',
            'G': 'ooicon-group',
            'R': 'ooicon-role',
            'UD': 'ooicon-duty',
            'UA': 'ooicon-attribute',
            'PA': 'ooicon-attribute',
            'PROCESS': 'ooicon-process',
            'APP': 'ooicon-computer'
        }[t];
    }
    _render() {
        if (this._canRender()) this.#renderIcon();
    }
    _connected() {
        if (this._canRender()){
            this.#renderIcon();
            this.#renderText();
        }
        this.tooltip = (this._props.isTooltip!==false) ? this.defaultTooltip : null;
        if (!this.tip){
            this.tip = tooltip(async () => {
                if (typeOf(this.tooltip) === 'function') {
                    return await this.tooltip();
                }
                return this.tooltip;
            }, this);
        }
    }
    defaultTooltip(){
        const card = dom('oo-card', {
            value: this.value,
            data: JSON.stringify(this.data),
            skin: 'border: 0; shadow: 0'
        });
        return new Promise((resolve)=>{
            card.addEventListener('loaded', ()=>{
                resolve(card);
            })
        })
    }
}
