import {dom, hyphenate} from '@o2oa/util';
import {OOComponent} from '../base.js';
import css from "./template/tabs.scope.css?raw";

const html = `
<div class="content tabs">
    <div class="labels">
        <div class="slider"></div>
        <slot name="label"></slot>
    </div>
    <div class="panes">
        <slot name="pane"></slot>
    </div>
</div>
`
export default class OOTabs extends OOComponent{
    static prop = {
        css: '',
        cssLink: '',
        skin: '',
        position: 'top',
        current: 0
    };
    static get observedAttributes() {
        return Object.keys(this.prop).map((att)=>{
            return hyphenate(att);
        });
    }
    _elements={
        labels: null,
        panes : null,
        slider: null
    };
    _setPropMap={
        css: ()=>{
            this.useCss(this._props.css);
        },
        cssLink: ()=>{
            this.useCssLink(this._props.cssLink);
        },
        skin: ()=>{
            this._useSkin(this._props.skin);
        },
        position: (old)=>{
            dom.removeClass(this._content, old);
            dom.addClass(this._content, this._props.position);

            dom.removeClass(this._elements.labels, old);
            dom.addClass(this._elements.labels, this._props.position);

            dom.removeClass(this._elements.slider, old);
            dom.addClass(this._elements.slider, this._props.position);
        },
        current: ()=>{
            this._checkCurrentTab();
        }
    }
    constructor() {
        super();
        this._initialize('oo-tabs',OOTabs, html, css);
    }

    _checkCurrentTab(){
        const tabs = this.querySelectorAll('oo-tab');
        const tab = tabs.item(this._props.current);
        if (tab && tab!==this.currentTab) this.setCurrent(tab);
    }
    currentTab = null;
    setCurrent(tab){
        if (this.currentTab){
            this.currentTab.unselected();
            this.currentTab = null;
        }
        if (tab){
            const tabs = this.querySelectorAll('oo-tab');
            let idx = 0;
            for (const t of tabs){
                if (t===tab) break;
                idx++;
            }
            this._props.current = idx;

            this.currentTab = tab;
            tab.selected();
            this.showSlider();
        }else{
            this.#hideSlider();
        }
    }
    showSlider(){
        const p = dom.getPosition(this.currentTab.label, this._elements.labels);
        const s = dom.getSize(this.currentTab.label);

        if (this._props.position==='top' || this._props.position==='bottom'){
            const scroll = this._elements.labels.scrollLeft;
            const left = p.x+scroll;
            dom.setStyles(this._elements.slider, {
                left: left+'px',
                width: s.x+'px'
            });
        }else{
            const scroll = this._elements.labels.scrollTop;
            const top = p.y+scroll;
            dom.setStyles(this._elements.slider, {
                top: top+'px',
                height: s.y+'px'
            });
        }
    }
    #hideSlider(){
        dom.setStyles(this._elements.slider, {
            left: 0,
            width: 0
        });
    }
}
