export default "* {\r\n    box-sizing: border-box;\r\n}\r\n.content{\r\n    --label: var(--oo-color-text2);\r\n    --icon-left: var(--oo-color-text2);\r\n    --icon-right: var(--oo-color-text2);\r\n    --focus: var(--oo-color-main);\r\n    --border: var(--oo-color-gray1);\r\n    --radius: var(--oo-default-radius);\r\n    --input: var(--oo-color-text);\r\n    --placeholder: var(--oo-color-text4);\r\n    --disabled: var(--oo-color-gray2);\r\n    --bg:  var(--oo-color-bg-white);\r\n    --shadow: var(--oo-shadow-border);\r\n    --shadow-focus: var(--oo-shadow-border-focus)\r\n}\r\n.content {\r\n    position: relative;\r\n    width: 100%;\r\n    height: 100%;\r\n    display: flex;\r\n    align-items: center;\r\n}\r\n.label {\r\n    white-space: nowrap;\r\n    transition: color 0.2s;\r\n    display: inline-flex;\r\n    align-items: center;\r\n    color: var(--label);\r\n    height: 100%;\r\n}\r\n.labelText{\r\n    padding: 0.5em 0.35em;\r\n    margin-right: 0.5em;\r\n    display: flex;\r\n    align-items: center;\r\n    height: 100%;\r\n}\r\n.flex-end{\r\n    justify-content: flex-end;\r\n}\r\n.hide{\r\n    display: none !important;\r\n}\r\n.box {\r\n    border-radius: var(--radius);\r\n    box-shadow: var(--shadow);\r\n    display: inline-flex;\r\n    align-items: center;\r\n    height: 100%;\r\n    width: 100%;\r\n    /* background: var(--bg); */\r\n    transition: border-color 0.2s, box-shadow 0.2s;\r\n    cursor: pointer;\r\n}\r\n.box:has(input.invalid){\r\n    background: var(--oo-color-error-bg);\r\n    box-shadow: var(--oo-shadow-border-error);\r\n}\r\n.body{\r\n    flex: 1;\r\n}\r\n.input {\r\n    border: 0;\r\n    outline: none;\r\n    color: var(--input);\r\n    border-radius: var(--radius);\r\n    padding: 0.35em 0.6em;\r\n    height: 100%;\r\n    min-height: 1.89em;\r\n    /*min-width: 12.28em;*/\r\n    box-shadow: none;\r\n    width: inherit;\r\n    /*min-width: 13.365875em;*/\r\n    font-size: 1em;\r\n    background: transparent;\r\n    position: relative;\r\n}\r\n\r\ndiv.left-icon{\r\n    width: 1em;\r\n    text-align: right;\r\n    transition: color 0.2s;\r\n    font-size: 0.9em;\r\n    margin-left: 0.6em;\r\n    color: var(--icon-left);\r\n}\r\ndiv.right-icon{\r\n    width: 1em;\r\n    text-align: left;\r\n    transition: color 0.2s;\r\n    font-size: 0.9em;\r\n    margin-right: 0.6em;\r\n    color: var(--icon-right);\r\n}\r\n\r\n\r\n.box.focus {\r\n    box-shadow: var(--shadow-focus);\r\n    outline: none;\r\n    color: var(--focus);\r\n}\r\n.box.focus  div.left-icon{\r\n    color: var(--focus);\r\n}\r\n.box.focus  div.right-icon{\r\n    color: var(--focus);\r\n}\r\n\r\n.label.focus {\r\n    color: var(--focus);\r\n    outline: none;\r\n}\r\n\r\n.box.disabled {\r\n    background-color: var(--disabled)!important;\r\n    outline: none;\r\n    cursor: not-allowed;\r\n}\r\n.box.disabled div.left-icon{\r\n    /*color: var(--oo-color-text4);*/\r\n    opacity: 0.6;\r\n}\r\n.box.disabled div.right-icon{\r\n    /*color: var(--oo-color-text4);*/\r\n    opacity: 0.6;\r\n}\r\n.label.disabled {\r\n    /*color: var(--oo-color-text4);*/\r\n    outline: none;\r\n    cursor: not-allowed;\r\n}\r\ninput:disabled, textarea:disabled{\r\n    opacity: 0.45;\r\n    cursor: not-allowed;\r\n}\r\n.box.readmode {\r\n    outline: none;\r\n    cursor: default;\r\n    box-shadow: none;\r\n    background-color: transparent;\r\n}\r\n.box.readmode div.left-icon{\r\n    color: inherit;\r\n    display: none;\r\n}\r\n.box.readmode div.right-icon{\r\n    color: inherit;\r\n    display: none;\r\n}\r\n.label.readmode {\r\n    outline: none;\r\n    cursor: default;\r\n}\r\n.label.readmode.focus {\r\n    color: inherit;\r\n    outline: none;\r\n}\r\n\r\ninput:focus, textarea:focus {\r\n    border: 0;\r\n    outline: none;\r\n    box-shadow: none;\r\n}\r\n\r\n.list{\r\n    min-width: 13.365875em;\r\n    width: 100%;\r\n    padding: 0.3em 0.5em;\r\n    height: 100%;\r\n    min-height: 1.3em;\r\n}\r\n\r\n.placeholder{\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    color: var(--placeholder);\r\n    padding: 0.5em 0.6em;\r\n    white-space: nowrap;\r\n    text-overflow: ellipsis;\r\n    width: 100%;\r\n    overflow: hidden;\r\n}\r\n\r\n.inputContent{\r\n    color: var(--oo-color-text2);\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    align-items: center;\r\n    padding: 0.051em;\r\n}\r\n.item{\r\n    padding: 0.15em 0.3em;\r\n    border-radius: var(--oo-default-radius);\r\n    display: inline-flex;\r\n    align-items: baseline;\r\n    font-size: 1em;\r\n}\r\n.item:hover{\r\n    background-color: var(--oo-color-gray-e);\r\n}\r\n\r\n.itemIcon{\r\n    width: 1.25em;\r\n    height: 1.25em;\r\n    background-position: center;\r\n    background-size: cover;\r\n    background-repeat: no-repeat;\r\n    border-radius: var(--oo-default-radius);\r\n    opacity: 0.6;\r\n}\r\n\r\n.inputValidity{\r\n    position: absolute;\r\n    width: 1px;\r\n    height: 1px;\r\n    bottom: 0;\r\n    left: 50%;\r\n    border: 0;\r\n    outline: none;\r\n    overflow: hidden;\r\n    display: block;\r\n    font-size: 1px;\r\n    opacity: 0;\r\n}\r\ndiv.invalidHint{\r\n    /*position: absolute;*/\r\n    color: var(--oo-color-error);\r\n    height: 0;\r\n    overflow: hidden;\r\n    word-break: break-all;\r\n    transition: margin-top 0.2s, height 0.2s;\r\n}\r\ndiv.invalidHint.show{\r\n    margin: 0.3em 0 0 0em;\r\n    height: unset;\r\n    line-height: 1.2em;\r\n}\r\ndiv.invalidHint.show::before{\r\n    line-height: 1.4em;\r\n    margin-right: 0.2em;\r\n}\r\n.requiredFlag{\r\n    color: var(--oo-color-highlight);\r\n    padding-left: 0.3em;\r\n    font-size: 0.75em;\r\n}\r\n\r\n/*.one-line{*/\r\n/*    height: calc(26.27px + 0.35em + 0.35em);*/\r\n/*    overflow: hidden;*/\r\n/*}*/\r\n\r\n\r\n/* 移动端，标签在上，输入框下划线 skin-mode='mobile' */\r\n.content.mobile{\r\n    --oo-shadow-border: inset 0 -1px 0 0px var(--oo-color-gray-d);\r\n    --oo-shadow-border-focus: inset 0 -1px 0 0px var(--oo-color-main);\r\n    --oo-shadow-border-error: inset 0 -1px 0 0px var(--oo-color-error);\r\n    --oo-default-radius: 0;\r\n    --oo-area-radius: 0;\r\n}\r\n\r\n.content.mobile {\r\n    display: flex !important;\r\n    align-items: unset !important;\r\n    position: relative !important;\r\n    height: unset !important;\r\n    flex-direction: column;\r\n}\r\n.content.mobile .label {\r\n    color: var(--oo-color-text3);\r\n}\r\n\r\n.content.mobile .labelText {\r\n    width: unset !important;\r\n    min-width: unset !important;\r\n    max-width: unset !important;\r\n    display: block !important;\r\n}\r\n.content.mobile .input {\r\n    padding: 0.35em;\r\n    font-size: 1.125em;\r\n}\r\n.content.mobile .placeholder{\r\n    padding: 0.35em;\r\n}\r\n\r\n.content.mobile .box.readmode .input{\r\n    padding: 0.5em 0.35em;\r\n    font-size: 1.125em;\r\n    background-color: transparent !important;\r\n    border-bottom: 1px solid #dddddd !important;\r\n}\r\n.content.mobile .hide {\r\n    display: none !important;\r\n}\r\n\r\n/* ---------------------------------------------- */"