import {dom, loadCssText} from '@o2oa/util';
import {Tooltip} from './tooltip.js';

const cssModules = import.meta.glob('./styles/mask/*.css', {as: 'inline'});

class Mask {
    static option = {
        position: 'top', //top, bottom, left, right,
        style: 'default',
        autoClose: 0,
        emerge: -8,
        offset: 6,
        defer: 600,
        events: {
            show: 'mouseover',
            hide: 'mouseout, mousedown',
        },
        showOnEnterCotent: false,
        resetContentOnShow: false,
    };

    static loadedStyles = {};

    constructor(node, options) {
        this.node = node;
        this.options = Object.assign({}, Tooltip.option, options);
    }

    show() {
        this.#loadCss();
        if (!this.maskNode) this.#createMaskNode();
        this.positionMaskNode();
        dom.addClass(this.maskNode, 'show');
    }
    hide() {
        dom.removeClass(this.maskNode, 'show');
        setTimeout(() => {
            this.maskNode.remove();
        }, 200);
    }

    positionMaskNode() {
        dom.overlap(this.maskNode, this.node, dom.getOffsetParent(this.node));
    }
    #createMaskNode() {
        this.maskNode = dom('div.maskNode');
        this.node.insertAdjacentElement('afterend', this.maskNode);
        // (this.maskNode);
    }
    async #getStyle(styleName) {
        const cssText = await cssModules[`./styles/mask/${styleName}.css`]();
        return loadCssText(cssText.default, this.container);
    }
    async #loadCss() {
        const style = Mask.loadedStyles[this.options.style || 'default'];
        if (!style) {
            const styleName = this.options.style || 'default';
            Mask.loadedStyles[styleName] = this.#getStyle(styleName);
            Mask.loadedStyles[styleName] = await Mask.loadedStyles[styleName];
        } else {
            const styleNode = await Promise.resolve(style);
            dom.addClass(this.maskNode, styleNode.id);
        }
    }
}

export function mask(node, options) {
    if (!node.mask) node.mask = new Mask(node, options);
    node.mask.show();
    return node.mask;
}
export function unmask(node) {
    const mask = node.mask;
    if (mask) mask.hide();
}
